import ErrorReport from '@components/ui/ErrorReport';
import { Checkbox, InputRow, TextInput, TextLabel } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Form, useLoaderData, useOutletContext } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React, { useState } from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import type { ZodTypeAny } from 'zod';
import z, { ZodError } from 'zod';
import {
  createOrgRole,
  createOrgRoleWithRole,
  findRolesNotAssigned,
} from './.server/role.queries';
import { getOrgBeingViewed } from './.server/org.queries';
import useIsAdmin from '@lib/hooks/useIsAdmin';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  const { currentOrg: org, roles: userRoles } = await requireUserAsObject(request, {
    permissions: Attribute.ORGROLES_READWRITE,
  });

  let orgId;

  const orgIdParam = new URL(request.url).searchParams.get('org');

  if (!orgIdParam) {
    ({ uuid: orgId } = org);
  } else {
    orgId = orgIdParam;
  }

  const {orgBeingViewed: orgs} = await getOrgBeingViewed({defaultOrgId: org.uuid, searchParams: new URL(request.url).searchParams});
  const roles = await findRolesNotAssigned(orgId, orgs.isWingLevel);

  return json({
    orgId,
    roles,
    userRoles,
  });
};

export async function action({ request }: ActionFunctionArgs) {
  const zodRadioButton: [ZodTypeAny, ZodTypeAny, ...ZodTypeAny[]] = [
    z.literal('on').transform(() => true),
    z.literal('off').transform(() => false),
  ];

  const createOrgRoleSchema = z.object({
    contactPhone: z.string().min(3).max(80),
    contactEmail: z.string().min(3),
    organizationId: z.string().min(3),
    roleId: z.string().optional(),
    _action: z.string(),
    roleName: z.string().optional(),
    baseAgency: z.union(zodRadioButton).optional(),
    roleDescription: z.string().optional(),
    roleAbbreviation: z.string().refine((val: string) => !(val === 'ADMIN' || val === 'SUPERADMIN' || val === 'USER'), { message: 'Abbreviation cannot be ADMIN or SUPERADMIN.'}).optional(),
  });

  try {
    await requireUser(request, {
      options: {
        redirectOnFailure: false,
      },
      permissions: Attribute.ORGROLES_READWRITE,
    });

    const {
      contactPhone,
      contactEmail,
      organizationId,
      roleId,
      _action,
      roleName,
      baseAgency,
      roleAbbreviation,
      roleDescription,
    } = createOrgRoleSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    if (_action === 'link' && roleId)
      await createOrgRole({
        contactPhone,
        contactEmail,
        organizationId,
        roleId,
      });
    if (_action === 'add' && roleName && roleDescription && roleAbbreviation) {
      // add the role and link
      await createOrgRoleWithRole({
        roleName,
        roleAbbreviation,
        roleDescription,
        contactPhone,
        contactEmail,
        organizationId,
        baseAgency,
      });
    }

    return redirectWithSuccess(
      `/role?org=${organizationId}`,
      'Role Added!'
    );
  } catch (e) {
    console.error(e);

    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          { status: 'error', message: 'validation error', errors: e.errors },
          'There was an error adding the role.'
        );
      }
      return jsonWithError(
        { status: 'error', message: e.message },
        'There was an error adding the role.'
      );
    }
  }
}

const CreateRoleDefaultPage = () => {
  const { roles, orgId, userRoles } = useLoaderData<typeof loader>();
  const {orgBeingViewed} = useOutletContext();
  const [isAddRole, setIsAddRole] = useState(roles.length === 0);
  const {isAdmin} = useIsAdmin(userRoles);
  const [submitBtnAction, setSubmitBtnAction] = useState(
    roles.length === 0 ? 'add' : 'link'
  );

  const selectChangeHandler = (val: number | string) => {
    if (val === 'add') {
      setIsAddRole(true);
      setSubmitBtnAction('add');
    } else {
      setIsAddRole(false);
      setSubmitBtnAction('link');
    }
  };

  return (
    <Modal>
      <Form method="post">
        <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
          <Modal.Title>Create Organization Role</Modal.Title>
          <input type="hidden" name="organizationId" value={orgId} />
          <InputRow>
            <TextLabel htmlFor="roleId">Role to Link:</TextLabel>
            <select
              name="roleId"
              id="roleId"
              className="block w-full rounded-md border-gray-300 shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50"
              onChange={(e) => {
                selectChangeHandler(e.currentTarget.selectedOptions[0].value);
              }}>
              {roles.map((role) => (
                <option key={role.uuid} value={role.uuid}>
                  {role.name}
                </option>
              ))}
              <option value="add">Add New Role</option>
            </select>
          </InputRow>
          {isAddRole && (
            <>
              <InputRow>
                <TextLabel htmlFor="roleName">Role Name:</TextLabel>
                <TextInput name="roleName" required />
              </InputRow>
              <InputRow>
                <TextLabel htmlFor="roleAbbreviation">
                  Role Abbreviation:
                </TextLabel>
                <TextInput name="roleAbbreviation" required />
              </InputRow>
              <InputRow>
                <TextLabel htmlFor="roleDescription">
                  Role Description:
                </TextLabel>
                <TextInput name="roleDescription" required />
              </InputRow>
              {!orgBeingViewed.isWingLevel && isAdmin && <InputRow>
                <Checkbox label="Base Agency?" name="baseAgency" defaultChecked={false} reverse />
              </InputRow>}
              <hr className="my-8 h-px border-0 bg-gray-300" />
            </>
          )}
          <InputRow>
            <TextLabel htmlFor="contactPhone">Role's Phone Number:</TextLabel>
            <TextInput
              name="contactPhone"
              pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
              required/>
          </InputRow>
          <InputRow>
            <TextLabel htmlFor="contactEmail">Role's Email:</TextLabel>
            <TextInput name="contactEmail" required />
          </InputRow>
        </div>
        <Modal.Buttons link=".." value={submitBtnAction}>
          Save
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default CreateRoleDefaultPage;
