import ErrorReport from '@components/ui/ErrorReport';
import { Form, Input, Label } from '@components/ui';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData, useParams } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { getBase, updateBase } from './.server/base.queries';
import { Modal } from '@components/ui/modals';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, {permissions: Attribute.BASES_READWRITE });
  if (!params.baseId) throw new Error('Base ID is required.');
  return json({ base: await getBase(params.baseId) });
};

export const action = async ({
  request,
  params,
}: ActionFunctionArgs) => {
  const createSchema = z.object({
    id: z.string(),
    name: z.string(),
  });

  try {
    const { id: userId } = await requireUserAsObject(request, { options: {redirectOnFailure: false}, permissions: Attribute.BASES_READWRITE });

    const formData = await request.formData();
    const { id, name } = createSchema.parse(
      Object.fromEntries(formData.entries())
    );

    if (id !== params.baseId) throw new Error('ids do not match.');

    const updatedAt = new Date(Date.now());

    await updateBase(id, { name, updatedBy: userId, updatedAt });
    return redirectWithSuccess('/base', 'Base saved.');
  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof ZodError) {
        return jsonWithError({
          status: 'error',
          message: 'validation error',
          errors: e.errors,
        }, 'There were validation errors.');
      }
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      return jsonWithError({ status: 'error', message: e.message }, 'There was an error.');
    }
  }
};

const EditBase = () => {
  const { baseId } = useParams();
  const { base } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="post">
        <Input.Hidden name="id" value={baseId} />
        <Modal.Body>
          <Modal.Title>Edit Base</Modal.Title>
          <Form.Row>
            <Label htmlFor="name">Name</Label>
            <Input.Text
              name="name"
              defaultValue={base.name} />
          </Form.Row>
        </Modal.Body>
        <Modal.Buttons link="/base">Save</Modal.Buttons>
      </Form>
    </Modal>
  );
};

export const ErrorBoundary = ErrorReport;
export default EditBase;
