import ErrorReport from '@components/ui/ErrorReport';
import { Form, Input, Label } from '@components/ui';
import { TextInput } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs } from '@remix-run/node';
import { requireUser, requireUserAsObject } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { createBase } from './.server/base.queries';

export const action = async ({ request }: ActionFunctionArgs) => {
  await requireUser(request);
  const updateSchema = z.object({
    name: z.string(),
    abbreviation: z.string(),
  });

  try {
    const {id: userId } = await requireUserAsObject(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.BASES_READWRITE,
    });

    const formData = await request.formData();
    const { name, abbreviation } = updateSchema.parse(Object.fromEntries(formData.entries()));

    const updatedAt = new Date(Date.now());
    const createdAt = updatedAt;
    await createBase({
      userId,
      name,
      abbreviation,
      isWing: false,
      isBase: true,
      updatedBy: userId,
      updatedAt,
      createdAt,
      createdBy: userId,
    });
    return redirectWithSuccess('/base', 'Base saved.');
  } catch (e) {
    console.error(e);

    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          { status: 'error', message: 'validation error', errors: e.errors },
          'There was an error adding the base.'
        );
      }
      return jsonWithError(
        { status: 'error', message: e.message },
        'There was an error adding the base.'
      );
    }
  }
};

const EditBase = () => (
  <Modal>
    <Form method="post">
      <Modal.Body>
        <Modal.Title>Add a new Base</Modal.Title>
        <Form.Row>
          <Label htmlFor="name">Name:</Label>
          <Input.Text
            name="name" />
          <span className="text-gray-500 text-sm">ex: Travis AFB</span>
        </Form.Row>
        <Form.Row>
          <Label htmlFor="abbreviation">Abbreviation:</Label>
          <Input.Text
            name="abbreviation" />
          <span className="text-gray-500 text-sm">ex: TAFB</span>
        </Form.Row>
      </Modal.Body>
      <Modal.Buttons link="/base">Add</Modal.Buttons>
    </Form>
  </Modal>
);
export const ErrorBoundary = ErrorReport;
export default EditBase;
