import ErrorReport from '@components/ui/ErrorReport';
import { SolidCheckmark, DatabaseUpdatedIcon, XMarkIcon } from '@components/ui/Icons';
import { Dialog, DialogTitle } from '@headlessui/react';
import { CalendarDaysIcon } from '@heroicons/react/24/solid';
import { ChatBubbleBottomCenterTextIcon } from '@heroicons/react/24/outline';
import { useChecklistLayoutData } from '@lib/hooks/useMatchesData';
import { Link, Outlet, useNavigate } from '@remix-run/react';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Models } from '@lib/models.server';
import { requireUserAsObject } from '@services/user.server';
import { Status } from './status';
import {
  DetailsCommentsButton, ToggleCompleteButton,
  ToggleReadyButton,
} from '@components/Checklist/Slideover/Buttons';
import { ChecklistItemStatus } from '@prisma/client';

export async function loader({ request, params }: LoaderFunctionArgs) {
  const { currentOrg, id: loggedInUser } = await requireUserAsObject(request);
  const { itemId } = params;

  if (!itemId) throw new Error('item id is required.');

  const [item, comments] = await Models.checklistItem.getWithComments(itemId, {
    internalOnly: false,
    orgId: currentOrg.uuid,
  });

  if (!item) throw new Error('item not found');

  return json({ item, comments, loggedInUser }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
}

export default function ChecklistSlideOutLayout() {
  const navigate = useNavigate();
  const { item, comments, loggedInUser } = useChecklistLayoutData();

  const itemCompletedByMember = (itemRequiredRole: string) =>
    itemRequiredRole === 'USER';
  const notCompletedByMember = (itemRequiredRole: string) =>
    itemRequiredRole !== 'USER';

  const dueDate = new Date(item?.createdAt);
  dueDate.setDate(dueDate.getDate() + 30);

  const closeModal = () => {
    navigate('..');
  };

  const unread = (comments: { readBy: string[] }[]) => {
    let total = 0;
    for (const item of comments) {
      if (!item.readBy.includes(loggedInUser)) total++;
    }
    return total;
  };

  return (
    <>
      <Dialog
        as="div"
        id="slideover"
        className="fixed inset-0"
        open={true}
        onClose={closeModal}>
        <div className="flex justify-end min-h-screen">
          <div
            className="fixed inset-0 bg-black opacity-30"
            onClick={closeModal}/>
          <div className="z-50 bg-white shadow-lg min-h-screen h-screen w-full md:w-5/12 grid grid-cols-1 grid-rows-[auto_minmax(14rem,1fr)] overflow-y-scroll">
            <div
              className={`grid gap-y-2 grid-cols-2 grid-rows-[2rem_2rem_0.25rem_${
                item.isComplete ? '4rem' : '2rem'
              }_0.25rem]`}>
              <div className="flex col-span-2 row-span-1 py-5 pl-3 pr-5 justify-between items-center bg-gradient-to-r from-blue-200 to-blue-100 border-b border-b-blue-500">
                <DialogTitle className="font-bold mb-1 text-2xl pl-2">
                  {item.templateItem.reference?.name ?? item.templateItem.name}
                </DialogTitle>
                <XMarkIcon
                  className="w-10 h-10 hover:cursor-pointer"
                  onClick={ closeModal }/>
              </div>
              <div className="col-span-2 mx-5">
                <div className="col-span-2 flex my-3 flex-wrap gap-x-5">
                  <DetailsCommentsButton />
                  {notCompletedByMember(
                    item.templateItem.requiredRole.abbreviation
                  ) && (
                    <ToggleReadyButton
                      status={item.status}
                      itemId={item.id}
                      loggedInUser={loggedInUser}/>
                  )}
                  {itemCompletedByMember(
                    item.templateItem.requiredRole.abbreviation
                  ) && (
                    <ToggleCompleteButton
                      status={item.status}
                      itemId={item.id}
                      loggedInUser={loggedInUser}/>
                  )}
                </div>
              </div>
              <div className="col-span-2">
                <hr className="h-px bg-zinc-100" />
              </div>
              <div className="col-span-2 text-lg mx-5 my-2">
                <div className="flex gap-4 items-center justify-between">
                  <h4 className="font-bold text-xl">Status</h4>
                </div>
                <div className="mt-2">
                  <Status status={item.status} isSlideOver></Status>
                </div>
                {item.isComplete &&
                  notCompletedByMember(
                    item.templateItem.requiredRole.abbreviation
                  ) && (
                  <div className="flex mt-2 mx-3">
                      Approved By:
                    <p className="pl-4 font-semibold text-md place-content-center">
                      {item.completedBy?.name}
                    </p>
                  </div>
                )}
                <div className="text-sm text-zinc-600 mx-3 mt-4">
                  <div className="flex gap-5 justify-between">
                    <div className="inline-flex items-center">
                      {item.status === ChecklistItemStatus.Completed && (
                        <SolidCheckmark className="size-6 text-green-500 mr-1" />
                      )}
                      {!item.isComplete && !item.dateCompleted && (
                        <CalendarDaysIcon className="size-6 mr-2" />
                      )}
                      {new Date(item.dateCompleted ?? dueDate).toDateString()}
                    </div>
                    <Link
                      className="inline-flex items-center gap-1.5 hover:bg-zinc-300 hover:rounded-lg p-1"
                      to={`/checklist/${item.id}/comments`}>
                      <ChatBubbleBottomCenterTextIcon className="size-6" />
                      {comments && (
                        <>
                          {comments.length}
                          {unread(comments) > 0 && (
                            <span className="relative flex size-2">
                              <span className="animate-[ping_1s_ease-in-out_5] absolute inline-flex h-full w-full rounded-full bg-red-400 opacity-75"></span>
                              <span className="relative inline-flex rounded-full size-2 bg-red-500"></span>
                            </span>
                          )}
                        </>
                      )}
                      {!comments && '0'}
                    </Link>
                    <div className="inline-flex gap-1 ml-3">
                      <DatabaseUpdatedIcon className="size-6" />
                      {new Date(item.updatedAt).toDateString()}
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-span-2">
                <hr className="h-px bg-zinc-100" />
              </div>
              {notCompletedByMember(
                item.templateItem.requiredRole.abbreviation
              ) && (
                <>
                  <div className="col-span-2 mx-5">
                    <h4 className="text-xl font-bold">
                      {item.templateItem.requiredRole.name}
                    </h4>
                    <dl className="grid grid-cols-1 my-4 gap-y-3">
                      <div className="flex justify-between">
                        <dt>Email</dt>
                        <dd>
                          <a
                            href={`mailto:${item.templateItem.requiredRole.orgRoles[0].contactEmail}`}
                            className="underline underline-offset-2">
                            {
                              item.templateItem.requiredRole.orgRoles[0]
                                .contactEmail
                            }
                          </a>
                        </dd>
                      </div>
                      <hr className="h-px bg-zinc-100" />
                      <div className="flex justify-between">
                        <dt>Phone</dt>
                        <dd>
                          {
                            item.templateItem.requiredRole.orgRoles[0]
                              .contactPhone
                          }
                        </dd>
                      </div>
                    </dl>
                  </div>
                  <div className="col-span-2">
                    <hr className="h-px bg-zinc-100" />
                  </div>
                </>
              )}
            </div>
            <Outlet context={{ item, comments, loggedInUser }} />
            {/** This Outlet will show either the description from the index.tsx file or the Comments and TextBox from comments.tsx */}
          </div>
        </div>
      </Dialog>
    </>
  );
}

export const ErrorBoundary = ErrorReport;
