import { useState, useEffect } from "react";

const useInView = (elementRef: React.MutableRefObject<HTMLElement | null>, containerRef: React.MutableRefObject<HTMLElement | null>) => {
    const [isInView, setIsInView] = useState(false);
  
    useEffect(() => {
      const handleScroll = () => {
        const container = containerRef.current;
        const element = elementRef.current;
  
        if (container && element) {
          const containerRect = container.getBoundingClientRect();
          const elementRect = element.getBoundingClientRect();
  
          // Container's scroll position
          const scrollTop = container.scrollTop;
          const scrollLeft = container.scrollLeft;
  
          // Calculate the visible area of the container
          const containerVisibleTop =
            containerRect.top + scrollTop;
          const containerVisibleLeft = containerRect.left + scrollLeft;
          const containerVisibleBottom =
            containerVisibleTop + containerRect.height;
          const containerVisibleRight =
            containerVisibleLeft + containerRect.width;
  
          // Check if the item is in the visible area of the container
          const elementTop = elementRect.top + scrollTop;
          const elementLeft = elementRect.left + scrollLeft;
          const elementBottom = elementTop + elementRect.height;
          const elementRight = elementLeft + elementRect.width;
  
          setIsInView(
            elementBottom > containerVisibleTop &&
              elementTop < containerVisibleBottom &&
              elementRight > containerVisibleLeft &&
              elementLeft < containerVisibleRight,
          );
  
  
        }
      };
  
      // Attach the scroll event listener to the container
      const container = containerRef.current;
      if (container)
        container.addEventListener('scroll', handleScroll);
  
      // Initial check
      handleScroll();
  
      // Cleanup
      return () => {
        container?.removeEventListener('scroll', handleScroll);
      };
    }, [elementRef, containerRef]);
  
    return isInView;
  };

  export default useInView;