import ActionMenu, { ActionMenuLink } from '@components/ui/ActionMenu';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { Table } from '@components/ui/table/Table';
import { useRootLayoutData } from '@lib/hooks/useMatchesData';
import { Attribute } from '@lib/utils';
import { json, type LoaderFunctionArgs } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import useIsAdmin from '@lib/hooks/useIsAdmin';
import { getOnboardingUsers } from './.server/user.queries';
import { UserRank } from '@lib/constants';
import React from 'react';

export async function loader({ request }: LoaderFunctionArgs) {
  await requireUser(request, {
    permissions: Attribute.MEMBERS_READWRITEDELETE,
  });

  const users = await getOnboardingUsers();

  return json({
    users,
  });
}

export default function OnboardingUserIndex() {
  const { users } = useLoaderData<typeof loader>();
  const roles = useRootLayoutData('roles');

  const { isAdmin } = useIsAdmin(roles);

  const tableContents = [
    {
      header: 'Rank',
      key: 'rank',
      render: (rank: string) => {
        return UserRank[rank].abb;
      },
    },
    {
      header: 'Name',
      key: 'name',
    },
    {
      header: 'Email',
      key: 'workEmail',
    },
    {
      header: 'Requested Organization',
      key: 'requestedOrg.name',
    },
  ];

  return (
    <>
      <div className="mb-5">
        <PageHeader pageTitle="Onboarding Users" />
        <div>
          {users.length > 0 && (
            <Table cells={tableContents}>
              <Table.HeaderRow></Table.HeaderRow>
              <Table.Body>
                {users
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((user) => (
                    <>
                      <Table.Row item={user} key={user.id}>
                        <Table.ButtonCell>
                          <ActionMenu key={`action-menu-${user.id}`}>
                            <ActionMenuLink
                              text="Onboard User"
                              url={`${user.id}`}/>
                            {isAdmin && (
                              <ActionMenuLink
                                text="Delete User Account"
                                type="danger"
                                url={{
                                  pathname: 'delete',
                                  searchParams: { userId: user.id },
                                }}/>
                            )}
                          </ActionMenu>
                        </Table.ButtonCell>
                      </Table.Row>
                    </>
                  ))}
              </Table.Body>
            </Table>
          )}
          {users.length === 0 && <p>No Users found.</p>}
        </div>
      </div>
      <Outlet />
    </>
  );
}
export const ErrorBoundary = ErrorReport;
