import { Checkbox } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import { AttributeCategory, Operation } from '@lib/utils';
import { json, type LoaderFunctionArgs } from '@remix-run/node';
import { Form, useLoaderData } from '@remix-run/react';
import { getRole } from './.server/role.queries';

export async function loader({ params }: LoaderFunctionArgs) {
  if (!params.roleId) throw new Error('role id is required');
  const { permissions, ...role } = await getRole(params.roleId);
  return json({
    role,
    permissions: JSON.parse(atob(permissions)),
  });
}

export default function EditPermissions() {
  const { role, permissions } = useLoaderData<typeof loader>();
  const permissionsMap = new Map<string, Operation[]>(permissions);

  return (
    <Modal>
      <Form>
        <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
          <Modal.Title>Edit Permissions for {role.name}</Modal.Title>
          <div className="grid md:grid-cols-3 gap-5">
            {Object.entries(AttributeCategory)
              .sort()
              .map((val) => {
                const hasRead =
                  permissionsMap.get(val[1])?.includes(Operation.READ) ?? false;
                const hasWrite =
                  permissionsMap.get(val[1])?.includes(Operation.WRITE) ??
                  false;
                const hasDelete =
                  permissionsMap.get(val[1])?.includes(Operation.DELETE) ??
                  false;
                return (
                  val[0] !== 'ADMIN' && // TODO: SUPER ADMINS Will be the only ones able to change admin
                  val[0] !== 'SUPERADMIN' && ( // no one should be able to edit super admin
                    <div
                      key={`${role.id}-${val[0]}`}
                      className="border-b border-1 pb-5">
                      <label className="mb-2 block text-sm font-medium text-gray-700">
                        {val[0]}
                      </label>
                      <Checkbox
                        label="Read"
                        defaultChecked={hasRead}
                        name={`${val[0].toLowerCase()}-read`}/>
                      <Checkbox
                        label="Write"
                        defaultChecked={hasWrite}
                        name={`${val[0].toLowerCase()}-write`}/>
                      <Checkbox
                        label="Delete"
                        defaultChecked={hasDelete}
                        name={`${val[0].toLowerCase()}-del`}/>
                    </div>
                  )
                );
              })}
          </div>
        </div>
        <Modal.Buttons link="/role/permissions" value="edit">
          Save
        </Modal.Buttons>
      </Form>
    </Modal>
  );
}
