import { DeleteButton, LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { Dialog, Transition } from '@headlessui/react';
import { useManageLayoutData } from '@lib/hooks/useMatchesData';
import { Attribute } from '@lib/utils';
import { handleActionError } from '@lib/utils/utils.server';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import type { FetcherWithComponents } from '@remix-run/react';
import { Outlet, useFetcher, useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import { Fragment, useRef, useState } from 'react';
import { jsonWithSuccess } from 'remix-toast';
import z from 'zod';
import { cancelMemberMove, getOutboundMembers } from './.server/org.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  const { currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.MOVES_READ,
  });

  const { orgId } = params;
  if (!orgId) throw new Error('org id is required.');
  const outbound = await getOutboundMembers(orgId);

  return json({ outbound });
};

export const action = async ({ request }: ActionFunctionArgs) => {
  const deleteSchema = z.object({
    id: z.string().min(1),
    _action: z.string().regex(/delete/),
  });

  try {
    await requireUser(request, {
      permissions: Attribute.MOVES_READWRITEDELETE,
      options: {
        redirectOnFailure: false,
      },
    });

    const { id } = deleteSchema.parse(
      Object.fromEntries(await request.formData())
    );

    await cancelMemberMove(id);

    return jsonWithSuccess({ status: 'success' }, 'Move cancelled.');
  } catch (e) {
    return handleActionError(e, 'There was an error cancelling the move.');
  }
};

function ConfirmNavigation({
  fetcher,
  showModal,
  setShowModal,
  userName,
  idRef,
}: {
  fetcher: FetcherWithComponents<any>;
  showModal: boolean;
  setShowModal: React.Dispatch<React.SetStateAction<boolean>>;
  idRef: React.MutableRefObject<HTMLInputElement | null>;
  userName: string;
}) {
  return (
    <Transition.Root show={true} as={Fragment}>
      <Dialog
        as="div"
        className="relative z-10"
        open={showModal}
        onClose={() => setShowModal(false)}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </Transition.Child>
        <div className="fixed inset-0 z-10 w-screen overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95">
              <Dialog.Panel className="relative transform rounded-lg bg-white text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">
                <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
                  <Dialog.Title
                    as="h3"
                    className="text-base font-semibold leading-6 text-gray-900 mb-3">
                    Are you sure you want to cancel {userName}'s move?
                  </Dialog.Title>
                </div>
                <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
                  <button
                    type="button"
                    onClick={() => {
                      fetcher.submit(
                        {
                          id: idRef.current?.value ?? '',
                          _action: 'delete',
                        },
                        { method: 'POST' }
                      );
                      setShowModal(false);
                    }}
                    className="inline-flex w-full justify-center rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 sm:ml-3 sm:w-auto">
                    Yes
                  </button>
                  <button
                    type="button"
                    onClick={() => setShowModal(false)}
                    className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:mt-0 sm:w-auto">
                    No
                  </button>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
}

const OutboundMembers = () => {
  const { outbound } = useLoaderData<typeof loader>();
  const { orgBeingViewed } = useManageLayoutData();
  const deleteFetcher = useFetcher();
  const userIdRef = useRef<HTMLInputElement>(null);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [modalUserName, setModalUserName] = useState<string>('');

  const handleClick = (userName: string) => {
    setModalUserName(userName);
    setShowConfirmModal(true);
  };

  return (
    <>
      <PageHeader
        org={orgBeingViewed}
        pageTitle="Outbound Members"
        navigateTo="outbound"/>
      {outbound.length > 0 ? (
        <table className="text-left table-auto w-full border-neutral-300 border-1">
          <thead>
            <tr className="border-b border-black">
              <th className="pl-3">Name</th>
              <th>Move Type</th>
              <th>Next Organization</th>
              <th>Last Updated</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {outbound
              .sort((a, b) => a.name.localeCompare(b.name))
              .map((user) => {
                const updatedAt = new Date(user.updatedAt).toDateString();
                return (
                  <tr
                    key={user.id}
                    className="table-row border-b even:bg-zinc-50">
                    <td className="py-4 pl-3">{user.name}</td>
                    <td>{user.moveType}</td>
                    <td>{user.nextOrg?.name}</td>
                    <td>{updatedAt}</td>
                    <td className="text-center">
                      <LinkButton
                        className="my-2"
                        to={`/org/${orgBeingViewed.uuid}/outbound/${user.id}/edit`}>
                        Edit
                      </LinkButton>
                      <deleteFetcher.Form className="inline" method="post">
                        <input
                          type="hidden"
                          ref={userIdRef}
                          id="id"
                          name="id"
                          value={user.id}/>
                        <DeleteButton
                          className="ml-3"
                          name="_action"
                          value="delete"
                          type="button"
                          onClick={() => handleClick(user.name)}>
                          Cancel
                        </DeleteButton>
                      </deleteFetcher.Form>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      ) : (
        `No members to show.`
      )}
      {showConfirmModal ? (
        <ConfirmNavigation
          fetcher={deleteFetcher}
          showModal={showConfirmModal}
          setShowModal={setShowConfirmModal}
          userName={modalUserName}
          idRef={userIdRef}/>
      ) : null}
      <Outlet />
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default OutboundMembers;
