import { OrgCombobox } from '@components/OrgCombobox';
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/solid';
import type { AccountWithProfile, DefaultUserProfile } from '@lib/constants';
import { UserRank } from '@lib/constants';
import type { StringOrDate } from '@lib/types';
import { isInputType, isSelectType } from '@lib/utils';
import { MoveType, type Organization, Rank } from '@prisma/client';
import { useFetcher } from '@remix-run/react';
import type { OrganizationWithChildren } from '@repositories/organization.server';
import type { ErrorType } from '@routes/_auth+/register.queries.server';
import {
  useCallback,
  useEffect,
  useMemo,
  useReducer,
  useRef,
  useState,
} from 'react';
import { AcceptButton, Button } from '../Buttons';
import ErrorReport from '../ErrorReport';
import { Input } from '../Input';
import { Transition } from '@headlessui/react';

type UserFormProps = {
  orgs: StringOrDate<Pick<Organization, 'name' | 'uuid'>>[];
  users?: Array<Pick<DefaultUserProfile, 'id' | 'name'>>;
  account?: Omit<StringOrDate<AccountWithProfile>, 'password'>;
  allRequired?: boolean;
  btnAction: 'create' | 'update';
  registerOrCreate: 'create' | 'register';
  currentOrg?: Organization;
  orgParentNode: OrganizationWithChildren;
  orgFlatArray: Organization[];
};

export type FetcherResponseType =
  | { status: 'success' }
  | {
      status: 'error';
      error: {
        type: ErrorType;
        message: string;
      };
    };

const RegisterForm = ({
  orgs,
  allRequired = true,
  btnAction = 'create',
  registerOrCreate = 'register',
  currentOrg,
  orgFlatArray,
  orgParentNode,
}: UserFormProps) => {
  const tabs = ['userAccount', 'details', 'questionnaire', 'confirmation'];
  const [activeTab, setActiveTab] = useState(tabs[0]);
  const [confPassHasChanged, setConfPassHasChanged] = useState(false);

  const initialValues = {
    name: '',
    rank: Rank.CIV,
    dodId: '0000000000',
    org: currentOrg?.uuid ?? orgFlatArray[0].uuid,
    password: '',
    confirmPassword: '',
    personalPhone: '',
    workPhone: '',
    workEmail: '',
    hasDependents: false,
    isDormResident: false,
    sponsorReach: false,
    hasEFMP: false,
    personalEmail: '',
    emailDomain: 'us.af.mil',
  };

  const reducerFn = (
    inputs: typeof initialValues,
    action: {
      type: 'changeText' | 'changeRadio';
      value: string | boolean;
      field: string;
    }
  ) => {
    switch (action.type) {
      case 'changeText':
        return {
          ...inputs,
          [action.field]: action.value,
        };
      case 'changeRadio':
        return {
          ...inputs,
          [action.field]: action.value,
        };
      default:
        return {
          ...inputs,
          etc: action.value,
        };
    }
  };

  const [state, dispatch] = useReducer(reducerFn, initialValues);
  const [buttonClicked, setButtonClicked] = useState(false);

  const [showMove, setShowMove] = useState(false);

  const isDodIdValid = useCallback(() => {
    const dodId = state.dodId;
    return /^[0-9]{10}$/.test(dodId);
  }, [state.dodId]);

  const isPasswordValid = useCallback(() => {
    console.log('checking password');
    const password = state.password;

    const hasNoSpaces = !/\s/.test(password);
    const isLongEnough = password.length >= 10;
    return hasNoSpaces && isLongEnough;
  }, [state.password]);

  const isConfirmPasswordValid = useCallback(() => {
    const confirmPassword = state.confirmPassword;

    return (
      confirmPassword !== '' &&
      confirmPassword === state.password &&
      isPasswordValid()
    );
  }, [isPasswordValid, state.confirmPassword, state.password]);

  const isNameValid = useCallback(() => {
    const trimmedName = state.name.trim();
    const words = trimmedName.split(' ');
    state.name = words
      .map(
        (word) =>
          word.charAt(0).toUpperCase() + word.slice(1).toLocaleLowerCase()
      )
      .join(' ');

    return words.length >= 2 && words.every((word) => word !== '');
  }, [state]);

  const isWorkEmailValid = useCallback(() => {
    state.workEmail = state.workEmail.trim().toLocaleLowerCase();
    const email = state.workEmail;

    const maxEmailLength = 254;
    if (email === '' || email.length > maxEmailLength) {
      return false;
    }

    return !email.includes('@');
  }, [state]);

  const isFormValid = useCallback(() => {
    console.log('checking form');
    return (
      state.org !== '' &&
      state.workEmail !== '' &&
      state.password !== '' &&
      state.confirmPassword !== '' &&
      state.name !== '' &&
      isNameValid() &&
      isWorkEmailValid() &&
      isDodIdValid() &&
      isPasswordValid() &&
      isConfirmPasswordValid()
    );
  }, [isConfirmPasswordValid, isDodIdValid, isNameValid, isPasswordValid, isWorkEmailValid, state.confirmPassword, state.name, state.org, state.password, state.workEmail]);


  const userAccountTabFirstInput = useRef<HTMLInputElement | null>(null);
  const detailsTabFirstInput = useRef<HTMLInputElement | null>(null);
  const questionTabFirstInput = useRef<HTMLInputElement | null>(null);
  const summaryTabFirstInput = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    switch (activeTab) {
      case 'userAccount':
        if (userAccountTabFirstInput.current) {
          userAccountTabFirstInput.current.focus();
        }
        break;
      case 'details':
        if (detailsTabFirstInput.current) {
          detailsTabFirstInput.current.focus();
        }
        break;
      case 'questionnaire':
        if (questionTabFirstInput.current) {
          questionTabFirstInput.current.focus();
        }
        break;
      case 'confirmation':
        break;
    }
  }, [activeTab]);

  const handleTabClick = (tabIndex: string | number) => {
    if (typeof tabIndex === 'number') {
      if (tabIndex >= 0 && tabIndex < tabs.length) {
        setActiveTab(() => {
          return tabs[tabIndex];
        });
      }
    } else if (typeof tabIndex === 'string') {
      setActiveTab(tabIndex);
    }
  };

  const handleNextClick = () => {
    if (isFormValid()) {
      const currentIndex = tabs.indexOf(activeTab);
      handleTabClick(currentIndex + 1);
    }

    if (buttonClicked) {
      setButtonClicked(true);
    }
  };

  const handlePreviousClick = () => {
    const currentIndex = tabs.indexOf(activeTab);
    handleTabClick(currentIndex - 1);
  };

  const handleChange: React.ChangeEventHandler<HTMLInputElement> = (event) => {
    if (event.currentTarget.name && event.currentTarget.value !== undefined) {
      const value =
        event.currentTarget.name === 'workEmail'
          ? event.currentTarget.value.toLocaleLowerCase()
          : event.currentTarget.value;
      dispatch({
        type: 'changeText',
        field: event.currentTarget.name,
        value,
      });
      if (event.currentTarget.name === 'confirmPassword') {
        setConfPassHasChanged(true);
      }
    }
  };

  const handleRadioChange = (field: string, value: boolean | string) => {
    dispatch({ type: 'changeRadio', field, value });
  };

  function handleKeyUp(e: React.KeyboardEvent<HTMLFormElement>) {
    if (e.key === 'Enter') {
      if (activeTab === 'confirmation') {
        handleSubmit();
      } else {
        handleNextClick();
      }
    }
  }

  const handleSubmit = () => {
    // const { confirmPassword, ...rest } = state;
    if (!isFormValid()) {
      return;
    }
    fetcher.submit(
      {
        ...state,
      },
      { method: 'POST' }
    );
  };

  const fetcher = useFetcher<FetcherResponseType>();

  return (
    <>
      {fetcher.data?.status === 'error' && (
        <div className="bg-red-700/80 rounded-lg my-5 py-2 text-slate-100 font-bold text-lg text-center">
          {Array.isArray(fetcher.data.error)
            ? fetcher.data.error.map((err) => err.message)
            : fetcher.data.error.message}
        </div>
      )}
      <ol className="mt-5 flex items-center w-full text-sm font-medium text-center text-black mb-5">
        <li className="flex md:w-full items-center after:content-[''] after:w-full after:h-1 after:border-b after:border-gray-200 after:border-1 after:hidden sm:after:inline-block after:mx-6 xl:after:mx-10 dark:after:border-gray-700">
          <span className="flex items-center after:content-['/'] sm:after:hidden after:mx-2 after:text-gray-200 dark:after:text-gray-500">
            <Button
              onClick={() => handleTabClick('userAccount')}
              name="userAccount"
              className={`${activeTab === 'userAccount' ? ' bg-zinc-700 text-white' : ''} whitespace-nowrap`}>
              Account Information
            </Button>
          </span>
        </li>
        <li className="flex md:w-full items-center after:content-[''] after:w-full after:h-1 after:border-b after:border-gray-200 after:border-1 after:hidden sm:after:inline-block after:mx-6 xl:after:mx-10 dark:after:border-gray-700">
         
        </li>
        <li className="flex md:w-full items-center after:content-[''] after:w-full after:h-1 after:border-b after:border-gray-200 after:border-1 after:hidden sm:after:inline-block after:mx-6 xl:after:mx-10 dark:after:border-gray-700">
          <span className="flex items-center after:content-['/'] sm:after:hidden after:mx-2 after:text-gray-200 dark:after:text-gray-500">
            <Button
              disabled={!isFormValid()}
              onClick={() => handleTabClick('questionnaire')}
              name="questionnaire"
              className={`whitespace-nowrap ${activeTab === 'questionnaire' ? ' bg-zinc-700 text-white' : ''} ${isFormValid() ? '' : 'cursor-not-allowed disabled'}`}>
              Questionnaire
            </Button>
          </span>
        </li>
        <li className="flex items-center">
          <Button
            disabled={!isFormValid()}
            onClick={() => handleTabClick('confirmation')}
            className={`whitespace-nowrap ${activeTab === 'confirmation' ? ' bg-zinc-700 text-white' : ''} ${isFormValid() ? '' : 'cursor-not-allowed disabled'}`}>
            Confirmation
          </Button>
        </li>
      </ol>
      <fetcher.Form
        method="post"
        className="flex flex-col lg:h-[600px] pt-5"
        id="registerForm"
        onKeyUp={handleKeyUp}>
        {activeTab === 'userAccount' && (
          <div className="grid mx-auto w-3/5 px-10 py-5 rounded-lg border border-1 shadow-lg">
            <h2 className="text-xl text-center font-bold mt-5 mb-5">
              Account Infomation
            </h2>
            <div className="mb-2">
              <label className="block mb-1 text-sm font-medium text-black">
                Select Rank
              </label>
              <select
                name="rank"
                ref={userAccountTabFirstInput}
                defaultValue={state.rank}
                onChange={handleChange}
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50">
                {Object.entries(UserRank).map((item, index) => (
                  <option key={index} value={item[0]}>
                    {item[1].name}
                  </option>
                ))}
              </select>
            </div>
            <div className="mb-2">
              <Input.TextWithValidation
                label="Full Name"
                type="text"
                validationFn={isNameValid}
                validationMsg="Please enter a valid First and Last name"
                name="name"
                value={state.name}
                onChange={handleChange}
                required={true}/>
            </div>
            {/* <div className="mb-2 hidden">
             <label
             htmlFor="text"
             className="block mb-1 text-sm font-medium text-gray-900 dark:text-black">
             DOD ID Number
             </label>
             <input
             type="text"
             id="dod"
             name="dodId"
             value={state.dodId}
             onChange={handleChange}
             className={`bg-gray-50 border border-gray-300 text-gray-500 text-sm rounded-lg block w-full p-2.5 placeholder:text-black ${buttonClicked && !isDodIdValid() ? 'border border-red-500' : ''}`}
             pattern="[0-9]{10}"
             required={allRequired} />
             </div>
             {!isDodIdValid() && buttonClicked ? <p className="text-red-500">Please enter your valid DOD ID number</p> : null} */}
            {/* <div className="mb-2">
             <label
             htmlFor="email"
             className="block mb-1 text-sm font-medium text-gray-900 dark:text-black">
             Official Email address
             </label>
             <input
             type="email"
             id="workEmail"
             name="workEmail"
             className={`placeholder:text-black bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg block w-full p-2.5 ${state.workEmail !== '' && (isWorkEmailValid() ? 'border border-green-500 shadow-[0_0_2px_1px_rgba(34,197,94,0.8)]' : 'border border-red-500 shadow-[0_0_2px_1px_rgba(235,10,10,0.5)]')}`}
             value={state.workEmail}
             onChange={handleChange}
             required={allRequired} />
             {state.workEmail !== '' && (!isWorkEmailValid() ? <p className="text-red-500">Please enter a valid .mil email address.</p> : null)}
             </div> */}
            <div className="mb-2">
              <Input.TextInlineSelectWithValidation
                label="Official Email Address"
                validationFn={isWorkEmailValid}
                validationMsg="Please enter the beginning of your email address (first_name.last_name)."
                onSelectChange={handleChange}
                name="workEmail"
                value={state.workEmail}
                onChange={handleChange}
                required={true}></Input.TextInlineSelectWithValidation>
              <p className="text-sm mt-2">
                If your domain is missing please let us know{' '}
                <a
                  href="https://travisspark.atlassian.net/servicedesk/customer/portal/4/group/8/create/10032"
                  target="_blank"
                  className="underline"
                  rel="noreferrer">
                  here
                </a>
                .
              </p>
            </div>
            {!currentOrg && (
              <>
                <hr className="my-4 h-px border-0 bg-gray-300" />
                <div className="mb-2">
                  <OrgCombobox
                    name="org"
                    parentNode={orgParentNode}
                    selected={orgFlatArray.find(org => org.uuid === state.org) ?? orgFlatArray[0]}
                    orgArray={orgFlatArray}
                    onChange={handleChange}
                    isCreateAccountPage={true}
                    label="Select Organization:"/>
                </div>
              </>
            )}
            <hr className="my-4 h-px border-0 bg-gray-300" />
            <div className="mb-2">
              <Input.TextWithValidation
                label="Password"
                type="password"
                validationFn={isPasswordValid}
                validationMsg="The password needs a minimum of 10 characters."
                name="password"
                value={state.password}
                onChange={handleChange}
                required={true} />
            </div>
            <div className="mb-2">
              <Input.TextWithValidation
                label="Confirm Password"
                type="password"
                validationFn={isConfirmPasswordValid}
                validationMsg="The passwords must match."
                name="confirmPassword"
                value={state.confirmPassword}
                onChange={handleChange}
                required={true} />
            </div>
          </div>
        )}
        {activeTab === 'details' && (
          <div className="grid mx-auto w-3/5 px-10 py-5 rounded-lg border border-1 shadow-lg">
           
            <div className="mb-2">
              <label
                htmlFor="phone"
                className="block mb-1 text-sm font-medium text-gray-900 dark:text-black">
                Work Phone number (Optional)
                <span className="text-xs" />
              </label>
              <input
                type="tel"
                id="workPhone"
                name="workPhone"
                className="placeholder:text-black bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg block w-full p-2.5"
                onChange={handleChange}
                value={state.workPhone}
                pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
                required={allRequired}/>
            </div>
      
            <div className="mb-2">
              <label
                htmlFor="bio"
                className="block mb-1 text-sm font-medium text-gray-900 dark:text-black">
                Biography (Optional)
              </label>
              <textarea
                id="bio"
                name="bio"
                className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg block w-full p-2.5"/>
            </div>
          </div>
        )}
        {activeTab === 'questionnaire' && (
          <div className="grid mx-auto w-3/5 px-10 py-5 rounded-lg border border-1 shadow-lg">
            <h2 className="text-xl text-center font-bold mt-5 mb-5">
              Questionnaire
            </h2>

            <div className="mb-2">
              <h1>Are you currently a dorm resident?</h1>
              <input
                type="radio"
                ref={questionTabFirstInput}
                id="dormResidentYes"
                name="isDormResident"
                defaultChecked={state.isDormResident}
                onChange={(e) => handleRadioChange(e.currentTarget.name, true)}
                required={allRequired}/>
              <label className="ml-1" htmlFor="dormResidentYes">
                Yes
              </label>
              <input
                className="ml-3"
                type="radio"
                id="dormResidentNo"
                name="isDormResident"
                defaultChecked={!state.isDormResident}
                onChange={(e) => handleRadioChange(e.currentTarget.name, false)}
                required={allRequired}/>
              <label className="ml-1" htmlFor="dormResidentNo">
                No
              </label>
            </div>

            <div className="mb-2">
              <h1>Do you currently have any dependents?</h1>
              <input
                type="radio"
                id="hasDependentsYes"
                name="hasDependents"
                defaultChecked={state.hasDependents}
                onChange={(e) => handleRadioChange(e.currentTarget.name, true)}
                required={allRequired}/>
              <label className="ml-1" htmlFor="hasDependentsYes">
                Yes
              </label>
              <input
                className="ml-3"
                type="radio"
                id="hasDependentsNo"
                name="hasDependents"
                defaultChecked={!state.hasDependents}
                onChange={(e) => handleRadioChange(e.currentTarget.name, false)}
                required={allRequired}/>
              <label className="ml-1" htmlFor="hasDependentsNo">
                No
              </label>
            </div>

            {/* <div className="mb-2">
              <h1>Have you been contacted by your sponsor?</h1>
              <input
                type="radio"
                id="sponsorContactYes"
                name="sponsorReach"
                defaultChecked={state.sponsorReach}
                onChange={(e) => handleRadioChange(e.currentTarget.name, true)} />
              <label
                className="ml-1"
                htmlFor="sponsorContactYes">Yes</label>
              <input
                className="ml-3"
                type="radio"
                id="sponsorContactNo"
                name="sponsorReach"
                defaultChecked={!state.sponsorReach}
                onChange={(e) => handleRadioChange(e.currentTarget.name, false)} />
              <label
                className="ml-1"
                htmlFor="sponsorContactNo">No</label>
            </div> */}

            <div className="mb-2">
              <h1>
                Is any member of your family enrolled in the Exceptional Family
                Member Program (EFMP)?*
              </h1>
              <div className="text-xs mb-1">
                <p>
                  *Service members on active duty enroll in the program if they
                  have a Family member with a physical, emotional,
                  developmental, or intellectual disorder requiring specialized
                  services so their needs can be considered in the military
                  personnel assignment process.
                </p>
              </div>
              <input
                type="radio"
                id="hasEFMPYes"
                name="hasEFMP"
                defaultChecked={state.hasEFMP}
                onChange={(e) => handleRadioChange(e.currentTarget.name, true)}/>
              <label className="ml-1" htmlFor="hasEFMPYes">
                Yes
              </label>

              <input
                className="ml-3"
                type="radio"
                id="hasEFMPNo"
                name="hasEFMP"
                defaultChecked={!state.hasEFMP}
                onChange={(e) => handleRadioChange(e.currentTarget.name, false)}/>
              <label className="ml-1" htmlFor="hasEFMPNo">
                No
              </label>
            </div>
            <div></div>
          </div>
        )}
        {activeTab === 'confirmation' && (
          <div className="container grid grid-cols-3">
            <div className="col-start-2 text-xl text-center font-bold mt-5 mb-5">
              <h2>
                {registerOrCreate === 'create'
                  ? 'Confirm Information'
                  : 'Confirm Your Information'}
              </h2>
            </div>
            <div className="col-start-2 rounded-lg border border-neutral mx-auto w-[510px]">
              <table className="table w-full ">
                <tbody>
                  <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">Rank:</td>
                    <td className="font-bold py-2 pr-2">
                      {UserRank[state.rank].name}
                    </td>
                  </tr>
                  <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">Name:</td>
                    <td className={`font-bold py-2 pr-2`}>{state.name}</td>
                  </tr>
                  {!currentOrg && (
                    <tr className="border-b even:bg-zinc-100">
                      <td className="pl-2 py-2">Organization:</td>
                      <td className="font-bold py-2 pr-2">
                        {orgs.filter((org) => org.uuid === state.org)[0]?.name}
                      </td>
                    </tr>
                  )}
                  {/* <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">DoD ID:</td>
                    <td className={`font-bold py-2 pr-2`}>{state.dodId ? '':'n/a'}</td>
                  </tr> */}
                  <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">Official Email:</td>
                    <td className={`font-bold py-2 pr-2`}>
                      {state.workEmail}@{state.emailDomain}
                    </td>
                  </tr>
                
                  {state.workPhone && (
                    <tr className="border-b even:bg-zinc-100">
                      <td className="pl-2 py-2">Work Phone:</td>
                      <td className="font-bold py-2 pr-2">{state.workPhone}</td>
                    </tr>
                  )}
              
                  {/* <tr className="border-b even:bg-zinc-100">
                     <td className="pl-2 py-2">Biography:</td>
                     <td className="font-bold py-2 pr-2">Add your biography content here</td>
                  </tr> */}
                  <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">
                      Are {registerOrCreate === 'create' ? 'they' : 'you'}{' '}
                      currently a dorm resident?
                    </td>
                    <td className="font-bold py-2 pr-2">
                      {state.isDormResident ? 'Yes' : 'No'}
                    </td>
                  </tr>
                  <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">
                      Do {registerOrCreate === 'create' ? 'they' : 'you'}{' '}
                      currently have any dependents?
                    </td>
                    <td className="font-bold py-2 pr-2">
                      {state.hasDependents ? 'Yes' : 'No'}
                    </td>
                  </tr>
                  {/* <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">Have you been contacted by your sponsor?</td>
                    <td className="font-bold py-2 pr-2">{state.sponsorReach ? 'Yes' : 'No'}</td>
                  </tr> */}
                  <tr className="border-b even:bg-zinc-100">
                    <td className="pl-2 py-2">
                      Do {registerOrCreate === 'create' ? 'they' : 'you'}{' '}
                      qualify for EFMP?
                    </td>
                    <td className="font-bold py-2 pr-2">
                      {state.hasEFMP ? 'Yes' : 'No'}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <Transition show={registerOrCreate === 'create'}>
              <div className="col-start-2 my-5 justify-self-center">
                <Input.Checkbox
                  name="assignChecklist"
                  onChange={(e) => {
                    setShowMove((prev) => {
                      handleRadioChange(
                        'moveType',
                        !prev ? MoveType.PCS : undefined
                      );
                      return !prev;
                    });
                    console.log(e.target.checked);
                    handleRadioChange(e.target.name, e.target.checked);
                  }}
                  defaultChecked={showMove}
                  variant="textLg"
                  label={`Would you like to assign a checklist to ${state.name}?`}/>
                <Transition
                  show={showMove}
                  as="div"
                  enter="transition duration-200"
                  enterFrom="opacity-0 -translate-y-10"
                  enterTo="opacity-100 translate-y-0"
                  leaveTo="opacity-0 -translate-y-10"
                  leave="transition duration-200">
                  <p className="mb-2 text-base text-center text-zinc-600">
                    What type of checklist would you like to assign?
                  </p>
                  <div className="flex gap-10 justify-center">
                    <label>
                      <input
                        type="radio"
                        name="moveType"
                        value="PCS"
                        required={showMove}
                        onChange={(e) =>
                          handleRadioChange('moveType', MoveType.PCS)
                        }
                        defaultChecked={true}
                        id="pcsMoveType"
                        className="border sr-only peer border-zinc-400 rounded px-6"/>
                      <span className="border hover:bg-blue-200 hover:cursor-pointer peer-checked:text-white border-zinc-400 rounded-lg px-6 py-1 text-sm peer select-none peer-checked:bg-blue-500 peer-checked:font-bold font-medium text-gray-900 dark:text-gray-300">
                        PCS
                      </span>
                    </label>
                    <label>
                      <input
                        type="radio"
                        name="moveType"
                        value="PCA"
                        required={showMove}
                        onChange={(e) =>
                          handleRadioChange('moveType', MoveType.PCA)
                        }
                        id="pcaMoveType"
                        className="border sr-only peer border-zinc-400 rounded px-6"/>
                      <span className="border hover:bg-blue-200 hover:cursor-pointer peer-checked:text-white border-zinc-400 rounded-lg px-6 py-1 text-sm peer select-none peer-checked:bg-blue-500 peer-checked:font-bold font-medium text-gray-900 dark:text-gray-300">
                        PCA
                      </span>
                    </label>
                  </div>
                </Transition>
              </div>
            </Transition>
            <div className="col-start-2 mt-20">
              <AcceptButton
                className="bg-green-400 w-full"
                ref={summaryTabFirstInput}
                name="_action"
                value={btnAction}
                type="button"
                onClick={handleSubmit}>
                Submit
              </AcceptButton>
            </div>
          </div>
        )}
      </fetcher.Form>
      <div className="flex justify-between w-full mt-20">
        <div>
          <Button
            onClick={handlePreviousClick}
            disabled={activeTab === 'userAccount'}
            className="inline-flex items-center"
            name="previous"
            type="button">
            <ChevronLeftIcon className="h-5 w-5"></ChevronLeftIcon> Previous
          </Button>
        </div>
        <div>
          <Button
            onClick={() => {
              setButtonClicked(true);
              handleNextClick();
            }}
            disabled={!isFormValid() || activeTab === 'confirmation'}
            className={`inline-flex items-center ${isFormValid() && !buttonClicked ? '' : 'bg-blue-500'}`}
            name="next"
            type="button">
            Next <ChevronRightIcon className="h-5 w-5"></ChevronRightIcon>
          </Button>
        </div>
      </div>
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default RegisterForm;
