type MetricTableProps = {
  header: string;
  targetLength?: number;
  metricItems?: {
    header: string;
    metric: string | number;
  }[];
};
export function MetricTable({ header, metricItems, targetLength = 6 }: MetricTableProps) {
  let items = metricItems ?? [];
  const itemsNeeded = Math.max(0, targetLength - items.length);
  const fillerItems: any[] = Array.from({ length: itemsNeeded });

  items = items.concat(fillerItems);

  return (
    <div className="bg-gray-100 border-2 border-gray-200 row-span-1 flex flex-col items-center justify-between text-center col-span-3">
      <h3 className="text-center my-1.5">{header}</h3>
      <table className="table-auto w-full text-center">
        <tbody>
          <tr>
            <td className="text-sm">
              <ol>
                {items.map((item, index) => {
                  return (
                    <li
                      key={index}
                      className={`flex items-center justify-between overflow-hidden odd:bg-neutral-200 bg-neutral-100 px-2 py-0.5`}>
                      {item ? (
                        <>
                          <span className="px-1 flex-grow text-left overflow-hidden text-ellipsis whitespace-nowrap pr-4">
                            {item.header}
                          </span>
                          <span className="text-right px-1">{item.metric}</span>
                        </>
                      ) : (
                        <div className="flex justify-center items-center text-center w-full">
                          N/A
                        </div>
                      )}
                    </li>
                  );
                })}
              </ol>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export function MetricsTableRowSkeleton() {
  return (
    <div className="h-5 w-full bg-gradient-to-r from-zinc-50 to-zinc-200 rounded-full"></div>
  );
}

export function MetricsTableSkeleton() {
  return (
    <div className="animate-pulse h-[186px] bg-gray-100 px-2 border-2 border-gray-200 row-span-1 flex flex-col items-center justify-center gap-1 text-center col-span-3">
      <div className="h-6 w-3/5 bg-gradient-to-r from-zinc-50 to-zinc-200 rounded-full"></div>
      {Array.from({length: 6}).map((_, index) => (
        <MetricsTableRowSkeleton key={`${index}-metrics-table-row-skelly`} />
      ))}
    </div>
  );
}

