import ErrorReport from '@components/ui/ErrorReport';
import RegisterForm from '@components/ui/forms/RegisterForm';
import PageHeader from '@components/ui/PageHeader';
import type { ManageLayoutOutletContext } from '@lib/types';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import {
  useLoaderData,
  useOutletContext,
} from '@remix-run/react';
import { getOrgs, registerUserActionFn } from '@routes/_auth+/register.queries.server';
import { getOrgBeingViewed } from '@routes/_withNav+/_withSideBar+/_manage+/.server/org.queries';
import { requireUserAsObject } from '@services/user.server';

export const loader = async () => {
  const orgs = await getOrgs();
  return json({ orgs }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
};

export async function action({ request, params }: ActionFunctionArgs) {
  const { id: loggedInUser, currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.MEMBERS_READWRITE,
    options: { redirectOnFailure: false },
  });
  const { orgBeingViewed } = await getOrgBeingViewed({defaultOrgId: currentOrg.uuid, searchParams: new URL(request.url).searchParams, params });
  const response = await registerUserActionFn(request, '/user', loggedInUser, orgBeingViewed.uuid);
  return json(response, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
}

const AccountRegister = () => {
  const { orgs } = useLoaderData<typeof loader>();
  const { orgBeingViewed } = useOutletContext<ManageLayoutOutletContext>();

  return (
    <>
      <PageHeader org={orgBeingViewed} pageTitle="Create User Account" />
      <section className="container mx-auto h-full px-5">
        <RegisterForm orgs={orgs} currentOrg={orgBeingViewed} btnAction="create" registerOrCreate="create" />
      </section>
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default AccountRegister;
