import ErrorReport from '@components/ui/ErrorReport';
import { Form, Input, Label } from '@components/ui';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React, { useState } from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { denyTask, getTask } from './.server/tasks.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, { permissions: Attribute.ORGS_READWRITE });
  const { taskId } = params;
  if (!taskId) throw json({ status: 'error', error: 'Task Id is required.' });
  const task = await getTask(taskId);
  return json({ taskId: taskId, task });
};

enum DenialReason {
  inPerson,
  docsNeeded,
  incompleteDocs,
  pastDue,
  missingInfo,
  other,
}

// TODO: Add try catch
export async function action({ request, params }: ActionFunctionArgs) {
  const schema = z.object({
    message: z.string().optional(),
    reason: z.nativeEnum(DenialReason),
  });

  try {
    const ReasonMessages = new Map();
    ReasonMessages.set(DenialReason.inPerson, 'In-person visit required!');
    ReasonMessages.set(DenialReason.docsNeeded, 'Documents needed to complete task!');
    ReasonMessages.set(DenialReason.incompleteDocs, 'Documents are incomplete!');
    ReasonMessages.set(DenialReason.pastDue, 'Task is past required completion date!');
    ReasonMessages.set(DenialReason.missingInfo, 'Missing information!');
    ReasonMessages.set(DenialReason.other, '');

    if (!params.taskId) throw new Error('missing task id');
    const { id: userId } = await requireUserAsObject(request, {
      permissions: Attribute.ORGS_READWRITE,
    });

    const { reason: reasonString, ...formData } = Object.fromEntries(
      (await request.formData()).entries()
    );

    let { message, reason } = schema.parse({
      reason: +reasonString,
      ...formData,
    });

    if(message){
      message = ReasonMessages.get(reason) + ' ' + message;
    }

    if (!message) {
      message = ReasonMessages.get(reason);
      if (!message)
        message = 'Item Denied.';
    }

    denyTask(params.taskId, userId, message);

    // await updateOrg(orgId, {
    //   userId,
    //   orgName,
    //   abbreviation,
    // });

    return redirectWithSuccess('..', 'Task successfully denied.');
  } catch (e) {
    console.error(e);

    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          { status: 'error', message: 'validation error', errors: e.errors },
          'There was an error denying the task.'
        );
      }
      return jsonWithError(
        { status: 'error', message: e.message },
        'There was an error adding the base.'
      );
    }
  }
}
const EditOrgDefaultPage = () => {
  const { taskId, task } = useLoaderData<typeof loader>();

  const [optionSelect, setOptionSelect] = useState<undefined | DenialReason | string>();

  const [addMessageCheck, setAddMessageCheck] = useState(false);

  const handleOptionSelect = (event: React.FormEvent) => {
    setOptionSelect((event.target as HTMLSelectElement).value);
    if(+(event.target as HTMLSelectElement).value === DenialReason.other){
      setAddMessageCheck(true);

    }
    else{
      setAddMessageCheck(false);
    }
  };

  const handleMessageCheck = () => {
    setAddMessageCheck(!addMessageCheck);
  };

  return (
    <Modal>
      <Form method="POST">
        <Modal.Body>
          <Modal.Title>Deny Task: {task?.templateItem.name}</Modal.Title>
          <Form.Row>
            <Label htmlFor="reason">Reason for denial:</Label>
            <Input.ValidationGroup
              message="Please select a reason for denial."
              isValid={optionSelect !== 'default'}>
              <Input.Select
                name="reason"
                onChange={handleOptionSelect}
                options={[
                  { id: DenialReason.inPerson, name: 'In-Person Visit' },
                  { id: DenialReason.docsNeeded, name: 'Missing Documents' },
                  { id: DenialReason.incompleteDocs, name: 'Incomplete Documents'},
                  { id: DenialReason.missingInfo, name: 'Missing Information'},
                  { id: DenialReason.pastDue, name: 'Past Due Date'},
                  { id: DenialReason.other, name: 'Other' },
                ]}></Input.Select>
            </Input.ValidationGroup>
          </Form.Row>
          <Form.Row>
            <Input.Checkbox
              label="Add Message"
              id="MessageCheckbox"
              checked={addMessageCheck}
              onChange={handleMessageCheck}
              disabled={+optionSelect === DenialReason.other}></Input.Checkbox>
            {addMessageCheck && <Input.Textarea name="message" required></Input.Textarea>}
          </Form.Row>
        </Modal.Body>
        <Modal.Buttons link="/" value="edit" onClick={(e) => {if (optionSelect === 'default') e.preventDefault();}}
          disabled={optionSelect === 'default' || optionSelect === undefined}>
          Send
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditOrgDefaultPage;
