import ErrorReport from '@components/ui/ErrorReport';
import { MessageContainer } from '@components/ui/Message';
import useMatchesData, {
  type ChecklistLayoutData,
  useChecklistLayoutData,
  useRootLayoutData,
} from '@lib/hooks/useMatchesData';
import {
  Outlet,
  useFetcher,
  useLoaderData,
  useMatches,
  useOutletContext,
  useParams,
} from '@remix-run/react';
import { getChecklistItem } from '@routes/_withNav+/_withSideBar+/.server/tasks.queries';
import { requireUser } from '@services/user.server';
import { useState } from 'react';
import type { ActionFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Attribute } from '@lib/utils';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError } from 'remix-toast';
import { z } from 'zod';
import { sendMessage, markRead } from '../.server/checklist.queries';

export async function action({ request }: ActionFunctionArgs) {
  const cookieUserId = await requireUser(request);

  const updateTaskSchema = z.object({
    itemId: z.string().optional(),
    postedById: z.string().optional(),
    userId: z.literal(cookieUserId).optional(),
    internal: z
      .enum(['true', 'false'])
      .transform((value) => value === 'true')
      .optional(),
    content: z.string().optional(),
    commentId: z.string().optional(),
    _action: z.union([z.literal('send'), z.literal('markRead')]),
  });

  try {
    const {
      itemId,
      userId,
      _action,
      content,
      postedById,
      internal,
      commentId,
    } = updateTaskSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    if (_action === 'send') {
      await sendMessage(itemId, postedById ?? '', content ?? '', internal);
      return json({ status: 'success' }, {
        headers: {
          'X-Frame-Options': 'SAMEORIGIN',
        },
      });
    }

    if (_action === 'markRead' && commentId) {
      const items: number[] = Array.from(
        JSON.parse(decodeURIComponent(commentId))
      );
      await markRead(items, userId as string);
      return json({ status: 'success' }, {
        headers: {
          'X-Frame-Options': 'SAMEORIGIN',
        },
      });
    }
  } catch (error) {
    console.error('Error during approval: ', error);
    if (error instanceof Error) {
      if (error instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.',
          {
            headers: {
              'X-Frame-Options': 'SAMEORIGIN',
            },
          }
        );
      }
      return jsonWithError(
        { status: 'error', error: 'Failed to approve task' },
        'There was an error.',
        {
          headers: {
            'X-Frame-Options': 'SAMEORIGIN',
          },
        }
      );
    }
  }
}

export default function ChecklistSlideOutComments() {
  const { item, comments, loggedInUser } = useOutletContext<ChecklistLayoutData>();

  const sendMessageFetcher = useFetcher();
  const [messageContent, setMessageContent] = useState('');

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setMessageContent(event.target.value);
  };

  const handleFormSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    sendMessageFetcher.submit(event.target as HTMLFormElement);
    setMessageContent('');
  };

  return (
    <div className="col-span-2 my-3 mx-5 grid grid-rows-[1fr_auto]">
      <div className="grid grid-rows-[auto_1fr_3rem] gap-3 overflow-y-scroll min-h-40">
        <h4 className="font-bold text-xl">Comments</h4>
        <MessageContainer
          loggedInUser={loggedInUser}
          comments={comments}
          status={item.dateCompleted} />
      <sendMessageFetcher.Form onSubmit={handleFormSubmit} method="POST">
        <input type="hidden" value={item?.id} name="itemId" />
        <input type="hidden" value={loggedInUser} name="userId" />
        <input type="hidden" value={loggedInUser} name="postedById" />
        <input type="hidden" name="_action" value="send" />
        <div className="flex gap-2">
          <input
            type="text"
            name="content"
            className="block w-full rounded p-2 text-sm border"
            placeholder="Type a message"
            value={messageContent}
            onChange={handleInputChange}
            required/>
          <button
            type="submit"
            className="bg-blue-500 text-white py-2 px-4 rounded w-40">
            Send
          </button>
        </div>
      </sendMessageFetcher.Form>
      </div>
    </div>
  );
}

export const ErrorBoundary = ErrorReport;
