// Since the dev server re-requires the bundle, do some shenanigans to make
// certain things persist across that ðŸ˜†
// Borrowed/modified from https://github.com/jenseng/abuse-the-platform/blob/2993a7e846c95ace693ce61626fa072174c8d9c7/app/utils/singleton.ts and
// https://github.com/remix-run/examples/blob/552edc2cb10fbad48944d9c87faef5d792025cd0/_official-jokes/app/utils/singleton.server.ts#L5

export const singleton = <Value>(
  name: string,
  valueFactory: () => Value
): Value => {
  const g = global as any;
  g.__singletons ??= {};
  g.__singletons[name] ??= valueFactory();
  return g.__singletons[name];
};
