import { CompleteChecklists } from '@components/Checklist/completeChecklists';
import { InProgressChecklists } from '@components/Checklist/inProgressChecklists';
import ErrorReport from '@components/ui/ErrorReport';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json, redirect } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import z from 'zod';
import { uncompleteItem } from '../_withSideBar+/.server/tasks.queries';
import {
  completeItem,
  getUserChecklists,
  markRead,
  sendUserMessage,
  userApprovalRequest,
  userApprovalRequestUnMark,
} from './.server/checklist.queries';

export async function loader({ request }: LoaderFunctionArgs) {
  const userId = await requireUser(request);
  const checklists = await getUserChecklists(userId);

  return json({ checklists }, {
    headers: {
      'Content-Security-Policy': 'frame-ancestors: none',
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
}

export async function action({ request }: ActionFunctionArgs) {
  const cookieUserId = await requireUser(request);
  // const schema = z.object({
  //   itemId: z.string(),
  //   userId: z.literal(cookieUserId),
  // });
  const updateTaskSchema = z.object({
    itemId: z.string(),
    postedById: z.string().optional(),
    userId: z.literal(cookieUserId),
    content: z.string().optional(),
    commentId: z.string().optional(),
    _action: z.union([
      z.literal('update'),
      z.literal('incomplete'),
      z.literal('send'),
      z.literal('markready'),
      z.literal('notready'),
      z.literal('markRead'),
      z.literal('notready'),
    ]), // with more actions this needs to be z.union([z.literal('update'), z.literal('otherAction')])
  });

  try {
    const { userId, itemId, _action, content, postedById, commentId } =
      updateTaskSchema.parse(
        Object.fromEntries((await request.formData()).entries())
      );

    if (_action === 'update') {
      await completeItem(itemId, userId);
      return jsonWithSuccess({ status: 'success' }, 'Task Signed!');
    }
    if (_action === 'incomplete') {
      await uncompleteItem(itemId, userId);
      return jsonWithSuccess({ status: 'success' }, 'Task Unsigned');
    }
    if (_action === 'send') {
      await sendUserMessage(itemId, postedById ?? '', content ?? '');
      return jsonWithSuccess({ status: 'success' }, 'Message sent!');
    }
    if (_action === 'markready') {
      await userApprovalRequest(itemId);
      return jsonWithSuccess({ status: 'success' }, 'Item awaiting approval.');
    }
    // TODO: implement user not ready
    if (_action === 'notready') {
      await userApprovalRequestUnMark(itemId);
      return jsonWithSuccess({ status: 'success'}, 'Task Unmarked');
    }
    if (_action === 'markRead') {
      const items: number[] = Array.from(
        JSON.parse(decodeURIComponent(commentId ?? ''))
      );
      await markRead(items, userId);
      return json({ status: 'success' });
    }
  } catch (error) {
    console.error('Error during approval: ', error);
    if (error instanceof TypeError) {
      if (error instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      return jsonWithError(
        { status: 'error', error: error.message || 'TypeError.' },
        'The URL provided for the request is invalid.'
      );
    } else if (error instanceof Error) {
      return jsonWithError(
        { status: 'error', error: error.message || 'Unknown Error.' },
        'An error occurred.'
      );
    }
  }
}

const ChecklistLayout = () => {
  const { checklists } = useLoaderData<typeof loader>();

  return (
    <>
      <div className="rounded-lg bg-zinc-50 drop-shadow-xl container mx-auto px-5 pt-2 mt-5">
        <InProgressChecklists checklists={checklists} />
      </div>
      <div className="rounded-lg bg-zinc-50 drop-shadow-xl container mx-auto px-5 pt-2">
        <CompleteChecklists checklists={checklists} />
      </div>
      <Outlet />
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default ChecklistLayout;
