import useGenerateSearchParams from '@lib/hooks/useGenerateSearchParams';
import usePermissions from '@lib/hooks/usePermissions';
import type { CategoryPermission, NavBarLink } from '@lib/types';
import { useEffect, useState } from 'react';
import { Link, useLocation } from '@remix-run/react';

type LinkWithPermissions = {
  permissionRequired: CategoryPermission | CategoryPermission[];
} & NavBarLink;

type RestrictedLinkProps = {
  userPermissions: string;
  link: LinkWithPermissions;
} & MainNavLinkProps;

type MainNavLinkProps = {
  link: NavBarLink;
  isMenuShowing: boolean;
  index: number;
};

function RestrictedLink({
  userPermissions,
  link,
  isMenuShowing,
  index,
}: RestrictedLinkProps) {
  const checkPermissions = usePermissions();

  if (checkPermissions(userPermissions, link.permissionRequired))
    return (
      <MainNavLink link={link} isMenuShowing={isMenuShowing} index={index} />
    );
}

function MainNavLink({ link, index, isMenuShowing }: MainNavLinkProps) {
  function checkActive(pathname: string, link: NavBarLink) {
    let isActive = false;
    if (link.text === 'Home') {
      isActive = pathname === '/' || pathname === '/profile';
    } else {
      if (pathname.includes(link.href)) isActive = true;
      else if (link.activeText && link.activeText.length > 0) {
        for (const str of link.activeText) {
          if (pathname.includes(str)) isActive = true;
        }
      }
    }

    return isActive;
  }

  const { pathname } = useLocation();
  const isActive = checkActive(pathname, link);
  const searchParams = useGenerateSearchParams({keysToRemove: 'includeChildren'});

  let mainNavLinkClass =
    'flex items-center ';
  if (link.text === 'Logout')
    mainNavLinkClass += 'hover:bg-red-400/80';
  else
    mainNavLinkClass += 'hover:bg-slate-400 hover:text-black';
  const mainNavLinkActive = 'bg-gray-200 text-black';
  const mainMenuIconClasses = `h-[30px] w-[30px] ml-3 md:mr-6 my-3`;
  return (
    <li key={`${link.text}-${index}`} className={link.text === 'Logout' ? 'mt-28' : ''}>
      <>
        {link.href.startsWith('https://') ? (
          <a
            href={link.href}
            target={link.href.startsWith('https://') ? '_blank' : '_self'}
            className={`${mainNavLinkClass}${
              isActive ? ` ${mainNavLinkActive}` : ''
            }`}
            rel="noreferrer">
            <link.icon className={mainMenuIconClasses} />
            <span className={`${isMenuShowing ? 'block' : 'hidden'}`}>
              {link.text}
            </span>
          </a>
        ) : (
          <Link
            to={{
              pathname: link.href,
              search: searchParams,
            }}
            className={`${mainNavLinkClass}${
              isActive ? ` ${mainNavLinkActive}` : ''
            }`}>
            <link.icon className={mainMenuIconClasses} />
            <span className={`${isMenuShowing ? 'block' : 'hidden'}`}>
              {link.text}
            </span>
          </Link>
        )}
      </>
    </li>
  );
}

function hasPermissionRequired(link: unknown): link is LinkWithPermissions {
  return !!(link as LinkWithPermissions).permissionRequired;
}

type MainNavBarProps = {
  links: NavBarLink[];
  userPermissions: string;
};

export function useWindowSize() {
  const [size, setSize] = useState<{
    width: number | null;
    height: number | null;
  }>({
    width: null,
    height: null,
  });

  useEffect(() => {
    const handleResize = () => {
      setSize({
        width: global.innerWidth,
        height: global.innerHeight,
      });
    };

    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return size;
}

const MainNavBar = ({ links, userPermissions }: MainNavBarProps) => {
  if (!userPermissions) return;

  return (
    // for fun :) style = {{ background: "url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRpKqcxTIr8omvzr8-IkPHKlfNmQxp1SNTpfR2-f2781A&s)" }}
    <div
      id="mainNav"
      className={`h-screen sticky top-0 flex-shrink-0 bg-slate-900 flex flex-col justify-between text-white py-4 w-[90px] focus:w-[280px] focus-within:w-[280px] hover:w-[280px] overflow-hidden transition-all`}>
      <div className="h-[245px] px-2">
        <Link to="/" className="flex flex-col items-center">
          <img
            className={`w-40`}
            alt="Virtual In-processing logo"
            src="/logo-light.png"
            width={60}
            height={60}/>
        </Link>
      </div>
      <ul className="ml-6 flex-grow w-[280px]">
        {links.map((link, index) => {
          if (link.shouldShow !== undefined && !link.shouldShow) return null;
          if (link.permissionRequired && hasPermissionRequired(link)) {
            return (
              <RestrictedLink
                link={link}
                userPermissions={userPermissions}
                key={`link-${link.href}-${index}`}
                isMenuShowing={true}
                index={index}/>
            );
          }
          return (
            <MainNavLink
              key={`link-${link.href}-${index}`}
              link={link}
              isMenuShowing={true}
              index={index}/>
          );
        })}
      </ul>
      <div className={`m-3 text-sm`}>
        <a href="https://travisspark.com">
          <img
            className={`mx-auto pb-2`}
            alt="Travis Air Force Base Phoenix Spark Innovation Lab Logo"
            src="/phoenix.png"
            width={60}
            height={60}/>
        </a>
      </div>
    </div>
  );
};

export default MainNavBar;
