import ErrorReport from '@components/ui/ErrorReport';
import ProfileForm from '@components/ui/forms/ProfileForm';
import { isValidPassword } from '@lib/utils';
import { Rank } from '@prisma/client';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import {
  getAccount,
  updateProfile,
} from './.server/profile.queries';
import { requireUser } from '@services/user.server';
import React from 'react';
import {
  jsonWithError,
  jsonWithSuccess,
} from 'remix-toast';
import type { ZodTypeAny } from 'zod';
import z, { ZodError } from 'zod';
import { getSponsors, getSubordinates } from '../_withSideBar+/_manage+/.server/org.queries';

export const loader = async ({ request }: LoaderFunctionArgs) => {

  const userId = await requireUser(request);
  //const { workEmail } = await getUser(userId);
  const account = await getAccount(userId);
  const subordinates = await getSubordinates(account.currentOrg.uuid);
  const sponsor = await getSponsors(userId);
  return json({ account, subordinates, sponsor }, {
    headers: {
      'Content-Security-Policy': 'frame-ancestors: none',
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
};

export const action = async ({ request }: ActionFunctionArgs) => {
  const zodRadioButton: [ZodTypeAny, ZodTypeAny, ...ZodTypeAny[]] = [
    z.literal('true').transform(() => true),
    z.literal('false').transform(() => false),
  ];
  const updateSchema = z.object({
    userId: z.string(),
    name: z.string().optional(),
    personalPhone: z.string().nullable().optional(),
    workPhone: z.string().nullable().optional(),
    workEmail: z.string().email().optional(),
    personalEmail: z.string().optional(),
    hasDependents: z.union(zodRadioButton).optional(),
    isDormResident: z.union(zodRadioButton).optional(),
    hasEFMP: z.union(zodRadioButton).optional(),
    // sponsorReach: z.union(zodRadioButton),
    password: z.string().optional().refine((val) => {if (val) return isValidPassword(val); else return true;}, {message: 'Not a valid password'}),
    confirmPassword: z.string().optional().refine((val) => {if (val) return isValidPassword(val); else return true;} ),
    rank: z.nativeEnum(Rank).optional(),
    // dodId: z.string().min(10).max(10),
    // sponsor: z.string().optional(),
    // supervisor: z.string().optional(),
  });

  try {
    const data = updateSchema.parse(Object.fromEntries((await request.formData()).entries()));
    if (data.password){
      if (!isValidPassword(data.password, data.confirmPassword))
        return jsonWithError( {status: 'error'}, 'There was a validation error.');
    }
    await updateProfile(data.userId, data);
    return jsonWithSuccess( { status: 'ok' }, 'Profile Saved!');


  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof ZodError) return jsonWithError({ status: 'error', message: 'There was a validation error.', issues: e.issues }, 'There was a validation error.');
      return jsonWithError({ status: 'error', error: e }, 'There was an error.');
    }
  }
};

const ChecklistIndex = () => {
  const { account, subordinates, sponsor } = useLoaderData<typeof loader>();

  return (
    <>
      <ProfileForm
        account={account}
        sub={subordinates}
        airman={sponsor}
        btnAction="update" />
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default ChecklistIndex;
