  public async create(data: UserRegisterDto) {
    const { password: plainText, profile, assignChecklist, moveType } = data;

    const {
      name,
      rank,
      personalEmail,
      personalPhone,
      workEmail,
      workPhone,
      hasDependents,
      isDormResident,
      hasEFMP,
      cssVerified,
      currentOrgId: nextOrg,
    } = profile;

    const account = await this.prisma.account.create({
      data: {
        email: profile.workEmail,
        password: await hashPassword(plainText),
        profile: {
          create: {
            name,
            workEmail,
            personalEmail,
            personalPhone,
            workPhone,
            hasDependents,
            isDormResident,
            hasEFMP,
            rank,
            cssVerified: new Date(Date.now()),
            nextOrg: {
              connect: {
                uuid: nextOrg,
              },
            },
            currentOrg: {
              connect: {
                uuid: nextOrg,
              },
            },
            moveType,
            // dodId,
            // sponsorId: sponsor.id,
            // supervisorId: supervisor.id,
          },
        },
      },
      select: {
        profile: true,
      },
    });

    if (!account.profile)
      throw new Error('There was an error creating the profile.');

    return account.profile;
  }

  public async register(data: UserRegisterDto) {
    const { password: plainText, profile } = data;

    const {
      name,
      rank,
      personalEmail,
      personalPhone,
      workEmail,
      workPhone,
      hasDependents,
      isDormResident,
      hasEFMP,
      cssVerified,
      currentOrgId: requestedOrg,
    } = profile;

    const account = await this.prisma.account.create({
      data: {
        email: profile.workEmail,
        password: await hashPassword(plainText),
        profile: {
          create: {
            name,
            rank,
            personalEmail,
            personalPhone,
            workEmail,
            workPhone,
            hasDependents,
            isDormResident,
            hasEFMP,
            cssVerified,
            requestedOrg: {
              connect: {
                uuid: requestedOrg,
              },
            },
            currentOrg: {
              connect: {
                uuid: 'GLOBAL',
              },
            },
          },
        },
      },
      select: {
        id: true,
        profile: {
          select: {
            workEmail: true,
            name: true,
            currentOrgId: true,
          },
        },
      },
    });

    return account.id;
  }

