import OrgComboboxSearchResults from './OrgComboboxSearchResults';
import {
  Combobox,
  ComboboxButton,
  ComboboxInput,
  ComboboxOption,
  ComboboxOptions,
  Field,
  Label,
} from '@headlessui/react';
import {
  CheckIcon,
  ChevronDownIcon,
  ChevronRightIcon,
  ChevronUpIcon,
} from '@heroicons/react/20/solid';
import type { Organization } from '@prisma/client';
import { useNavigate, useSearchParams } from '@remix-run/react';
import type { OrganizationWithChildren } from '@repositories/organization.server';
import { useState } from 'react';
import OrgComboboxOrgTree from './OrgComboboxOrgTree';

type OrgComboboxProps = {
  parentNode: OrganizationWithChildren;
  selected: { id: number; uuid: string; name: string };
  orgArray: Array<Organization>;
  navigateTo?: 'inbound' | 'inprocess' | 'completed' | 'outbound' | 'supervisors';
  isCreateAccountPage?: boolean;
  name?: string;
  label?: string;
};

export default function OrgCombobox({
  parentNode,
  selected,
  orgArray,
  navigateTo,
  isCreateAccountPage = false,
  name,
  label = 'Change organization to manage:',
  onChange,
}: OrgComboboxProps) {
  const [selectedOrg, setSelectedOrg] = useState(selected);
  const [query, setQuery] = useState('');

  const navigate = useNavigate();
  const [_, setSearchParams] = useSearchParams();

  const filteredOrgs =
    query !== ''
      ? orgArray.filter((org) =>
        org.name.toLowerCase().includes(query.toLowerCase())
      )
      : [];

  function generateUrl(id: string, path: 'inbound' | 'outbound' | 'inprocess' | 'completed' | 'supervisors' ) {
    return `/org/${id}/${path}`;
  }

  function handleChange(val: { id: number; uuid: string; name: string }) {
    if (val) {
      if (onChange) {
        onChange({currentTarget: {name: 'org', value: val.uuid }});
      }
      setSelectedOrg( val );

      if (!isCreateAccountPage) {
        if (navigateTo) {
          navigate(generateUrl(val.uuid, navigateTo));
        } else {
          const query = new URLSearchParams();
          query.set('org', val.uuid);
          setSearchParams(query);
        }
      }
    }
  }

  let inputStyles =
    'relative bg-white py-1.5 pl-3 pr-10 text-left text-slate-900 focus:outline-none focus:ring-indigo-500/20 sm:text-sm sm:leading-6 rounded-md border-slate-300 text-xs shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50';
  let optionListStyles =
    '[--anchor-padding:0px] rounded-xl bg-gray-200 shadow-lg p-0 py-0 [--anchor-gap:4px] focus:outline-none transition-all duration-200 ease-out empty:invisible data-[closed]:-translate-y-10 data-[closed]:opacity-0';
  let labelStyles = 'mx-auto text-sm font-bold mb-1 text-slate-800';

  if (isCreateAccountPage) {
    inputStyles += ' w-full';
    optionListStyles += ' w-[var(--input-width)] [--anchor-max-height:20rem]';
    labelStyles = 'mb-1 block text-sm font-medium text-gray-700';
  } else {
    inputStyles += ' w-80';
    optionListStyles +=
      ' w-[calc(var(--input-width)+10rem)] [--anchor-max-height:50rem]';
  }

  return (
    <Field className="flex flex-col">
      <Label className={labelStyles}>{label}</Label>
      <Combobox
        immediate
        value={selectedOrg}
        onChange={handleChange}
        by="id"
        onClose={() => setQuery('')}>
        <div className="relative">
          <ComboboxInput
            displayValue={(org: { id: string; name: string }) => org?.name}
            onChange={(event) => setQuery(event.target.value)}
            className={inputStyles}/>
          <ComboboxButton className="group absolute inset-y-0 right-0 px-2.5 border-l border-l-slate-300">
            {({ open }) => (
              <>
                {open && (
                  <ChevronUpIcon
                    className="size-4 fill-slate-900"
                    aria-hidden="true"/>
                )}
                {!open && (
                  <ChevronDownIcon
                    className="size-4 fill-slate-900"
                    aria-hidden="true"/>
                )}
              </>
            )}
          </ComboboxButton>
        </div>
        <ComboboxOptions
          anchor={{ to: isCreateAccountPage ? 'bottom start' : 'bottom end' }}
          transition
          className={optionListStyles}>
          {query === '' && (
            <>
              {!isCreateAccountPage && (
                <>
                  <div className="sticky top-0 px-4 py-3 flex items-center font-semibold text-sm text-slate-900 dark:text-slate-200 bg-slate-50/90 dark:bg-slate-700/90 border-0 border-slate-900/10 border-x-0 backdrop-blur-sm ring-0 ring-slate-900/10 dark:ring-black/10">
                    Your Organization
                  </div>
                  <ComboboxOption
                    value={parentNode}
                    className="rounded-lg divide-y divide-slate-500 group flex items-center select-none cursor-default data-[focus]:cursor-pointer data-[focus]:bg-slate-900/10 data-[selected]:bg-blue-500/30">
                    {({ selected }) => (
                      <div className="flex items-center gap-2 p-4">
                        {selected ? (
                          <CheckIcon className="h-5 w-5" />
                        ) : (
                          <ChevronRightIcon className="h-5 w-5" />
                        )}
                        <strong className="text-slate-900 text-sm font-medium dark:text-slate-200">
                          {parentNode.name}
                        </strong>
                      </div>
                    )}
                  </ComboboxOption>
                </>
              )}
              <OrgComboboxOrgTree tree={parentNode} isCreateAccountPage={isCreateAccountPage} />
            </>
          )}
          {query !== '' && (
            <>
              <div className="sticky top-0 px-4 py-3 flex items-center font-semibold text-sm text-slate-900 dark:text-slate-200 bg-slate-50/90 dark:bg-slate-700/90 border-0 border-slate-900/10 border-x-0 backdrop-blur-sm ring-0 ring-slate-900/10 dark:ring-black/10">
                Results:
              </div>
              <OrgComboboxSearchResults filteredOrgs={filteredOrgs} />
            </>
          )}
        </ComboboxOptions>
      </Combobox>
    </Field>
  );
}
