import type { UserWithChecklistsAndCount } from '@lib/constants';
import { Organization } from '@prisma/client';
import { useContext, useEffect, useState } from 'react';

enum FilterType {
  NONE = 'NONE',
  VERIFIED = 'VERIFIED',
  UNVERIFIED = 'UNVERIFIED',
}

const useFilteredUsers = ({
  searchContext,
  initialUserList,
}: {
  searchContext: React.Context<{
    searchQuery: string;
    setSearchQuery?: Function;
  }>;
  initialUserList: Array<{requestedOrg?:Organization}&Pick<UserWithChecklistsAndCount, 'checklists' | 'currentOrg' | 'workEmail' | 'name' | 'cssVerified' | 'supervisor' | 'id' | 'moveType' | 'previousOrg' | '_count' | 'rank'>>;
}) => {
  const { searchQuery } = useContext(searchContext);
  const [users, setUsers] = useState([...initialUserList]);
  const [filter, setFilter] = useState<FilterType>(FilterType.NONE);
  const [filteredUsers, setFilteredUsers] = useState(users);

  const search = (user: { workEmail: string; name: string | null }) =>
    user.workEmail.toLowerCase().includes(searchQuery.toLowerCase()) ||
    user.name?.toLowerCase().includes(searchQuery.toLowerCase());

  useEffect(() => {
    if (initialUserList) setUsers([...initialUserList]);
  }, [initialUserList]);

  useEffect(() => {
    switch (filter) {
      case FilterType.NONE:
        if (searchQuery === '') setFilteredUsers(() => [...users]);
        else setFilteredUsers(() => [...users.filter(search)]);
        break;
      case FilterType.VERIFIED:
        if (searchQuery === '')
          setFilteredUsers(() => [
            ...users.filter((user) => user.cssVerified !== null),
          ]);
        else
          setFilteredUsers(() => [
            ...users.filter((user) => user.cssVerified !== null).filter(search),
          ]);
        break;
      case FilterType.UNVERIFIED:
        if (searchQuery === '')
          setFilteredUsers(() => [
            ...users.filter((user) => user.cssVerified === null),
          ]);
        else
          setFilteredUsers(() => [
            ...users.filter((user) => user.cssVerified === null).filter(search),
          ]);
        break;
      default:
        break;
    }
  }, [searchQuery, filter, users]);

  return { filteredUsers, filter, setFilter };
};

export default useFilteredUsers;
