import { DeleteButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { Modal } from '@components/ui/modals';
import {
  ExclamationCircleIcon,
  ExclamationTriangleIcon,
} from '@heroicons/react/24/outline';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { Form, useNavigate, useParams } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import { z } from 'zod';
import { deleteAccount } from './.server/user.queries';

export async function loader({ request }: LoaderFunctionArgs) {
  await requireUser(request, {
    permissions: Attribute.MEMBERS_READWRITEDELETE,
  });
  return null;
}

export async function action({ request }: ActionFunctionArgs) {
  const orgParam = new URL(request.url).searchParams.get('org');
  const updateAccountStatus = z.object({
    userId: z.string(),
    _action: z.literal('delete'),
  });
  try {
    await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.MEMBERS_READWRITEDELETE,
    });

    const { userId } = updateAccountStatus.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    await deleteAccount(userId);

    return redirectWithSuccess(
      `/user${orgParam ? `?org=${orgParam}` : ''}`,
      'Account deleted!'
    );
  } catch (e) {
    console.error(e);
    return jsonWithError({ status: e }, 'Account could not be deleted', {
      status: 500,
    });
  }
  // return redirectWithSuccess(
  //   `/user${orgParam ? `?org=${orgParam}` : ''}`,
  //   'Member Saved!'
  // );
}

export default function DeleteAccountPage() {
  const { userId } = useParams();
  const navigate = useNavigate();

  return (
    <Modal>
      <Form method="post">
        <input type="hidden" name="userId" value={userId} />
        <div className="bg-white p-2">
          <div className="flex flex-col items-center justify-center">
            <ExclamationTriangleIcon className="w-10 h-10 stroke-red-600" />
            <h1 className="text-red-600 text-2xl">Delete User Account</h1>
          </div>
        </div>
        <div className="bg-gray-200">
          <div className="flex flex-col justify-center items-center p-5">
            <h2 className="mb-2 text-lg font-semibold text-gray-900 dark:text-white">
              Warning: this action cannot be undone.
            </h2>
            <ul className="max-w-md space-y-1 text-gray-500 list-inside dark:text-gray-400">
              <li className="flex items-center">
                <ExclamationCircleIcon className="w-5 h-5 mr-2 stroke-red-600" />
                The user will all lose access to thier account.
              </li>
              <li className="flex items-center">
                <ExclamationCircleIcon className="w-5 h-5 mr-2 stroke-red-600" />
                All historical data will be lost (metrics).
              </li>
              <li className="flex items-center">
                <ExclamationCircleIcon className="w-5 h-5 mr-2 stroke-red-600" />
                The account cannot be recovered.
              </li>
            </ul>
          </div>
          <div className="flex flex-row-reverse p-2">
            <DeleteButton
              type="submit"
              name="_action"
              value="delete"
              className="inline-flex w-full justify-center rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 sm:ml-3 sm:w-auto">
              Delete Account
            </DeleteButton>
            <button
              type="button"
              onClick={() => navigate(-1)}
              className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:mt-0 sm:w-auto">
              Cancel
            </button>
          </div>
        </div>
      </Form>
    </Modal>
  );
}

export const ErrorBoundary = ErrorReport;
