import type { CategoryPermission } from '@lib/types';
import type { AttributeCategory, Operation } from '@lib/utils';
import { compareMaps, objectToMap, verifyAttributes } from '@lib/utils';
import { useCallback } from 'react';

/**
 * Checks the user permissions for the required values
 * @param {string} userPermissionsString Provided from user object as a JSON parseable string that represents an array of arrays.
 * For example, '[['tasks', ['READ', 'WRITE']]]'
 * @param {CategoryPermission | CategoryPermission[]} requiredPermissionsObject
 * Example: 'chk:READ,WRITE'
 * @returns {boolean}
 */
export const permissionCallback = (
  userPermissionsString: string,
  requiredPermissionsObject: CategoryPermission | CategoryPermission[]
): boolean => {
  if (!userPermissionsString || !requiredPermissionsObject) return false;
  // Convert strings to Map()s
  const userPermissions = new Map<AttributeCategory, Operation[]>(
    JSON.parse(userPermissionsString)
  );

  // object to map
  const requiredPermissions = objectToMap(requiredPermissionsObject);

  verifyAttributes(userPermissions);

  return compareMaps(userPermissions, requiredPermissions);
};

const UsePermissions = () => useCallback(permissionCallback, []);

export default UsePermissions;
