import { useRootLayoutData } from '@lib/hooks/useMatchesData';
import usePermissions from '@lib/hooks/usePermissions';
import type { SideBarNavLink } from '@lib/types';
import SidebarLink from './SidebarLink';

export type SideBarProps = {
  navLinks: SideBarNavLink[];
};

export default function SideBarNav({ navLinks }: SideBarProps) {
  const permissions = useRootLayoutData('permissions');
  const checkPermissions = usePermissions();

  const actualLinks = new Map<string, SideBarNavLink['links']>();

  for (const navLink of navLinks) {
    let links: any[] = [];
    navLink.links.forEach((link) => {
      if (
        link.permission &&
        checkPermissions(permissions, link.permission)
      )
        links.push({ ...link });
      else if (!link.permission) links.push({ ...link });
    });

    let key = navLink.header;
    if (navLink.header === '') key = '';

    if (links.length > 0) actualLinks.set(key, links);
  }

  return (
    <div
      id="extraMenu"
      className="w-[300px] h-screen top-0 sticky overflow-scroll no-scrollbar flex-shrink-0 bg-gray-200 py-8 pt-2 px-4">
      <ul>
        {Array.from(actualLinks).map(
          ([header, links], index) =>
            links.length > 0 && (
              <SidebarLink
                key={`${header}-${index}`}
                links={links}
                header={header}/>
            )
        )}
      </ul>
    </div>
  );
}
