import { CheckIcon } from '@heroicons/react/24/outline';
import { isValidPassword, PasswordError } from '@lib/utils';
import type { Dispatch, SetStateAction} from 'react';
import { useEffect, useState } from 'react';

function handleChange(e: React.ChangeEvent<HTMLInputElement>, setPassword: Dispatch<SetStateAction<string>>) {
  setPassword(e.currentTarget.value);
}

export function PasswordInput({
  password,
  compareTo,
  setPassword,
  isConfirm,
  setIsFormValid,
}: {
  password: string;
  compareTo?: string;
  setPassword: Dispatch<SetStateAction<string>>;
  isConfirm?: boolean;
  setIsFormValid: Dispatch<SetStateAction<boolean>>;
}) {
  const [message, setMessage] = useState<string[]>([]);
  const baseStyles =
    'placeholder:text-black col-span-2 bg-white border-none text-gray-900 text-sm rounded-none block w-full p-2.5 focus:ring-0 focus:outline-none focus:border-transparent';
  const [styles, setStyles] = useState(baseStyles);

  const validStyles =
    ' border border-green-500 shadow-[0_0_2px_1px_rgba(34,197,94,0.8)]';
  const invalidStyles =
    ' border border-red-500 shadow-[0px_0px_2px_1px_rgba(235,10,10,0.5)]';

  useEffect(() => {
    let validationErrors: PasswordError[] = [];
    if (password) {
      if (compareTo) {
        validationErrors = isValidPassword(password, compareTo);
      } else {
        validationErrors = isValidPassword(password);
      }
    }
    if (validationErrors.length === 0) {
      setStyles(`${baseStyles} ${validStyles}`);
      setIsFormValid(true);
      setMessage([]);
    } else {
      setStyles(`${baseStyles} ${invalidStyles}`);
      setIsFormValid(false);
      if (validationErrors && validationErrors.length > 0) {
        setMessage([]);
        validationErrors.forEach((err) => {
          switch (err) {
            case PasswordError.Spaces:
              if (!isConfirm)
                setMessage((prev) => [
                  'Password must not contain only spaces.',
                  ...prev,
                ]);
              break;
            case PasswordError.Length:
              if (!isConfirm)
                setMessage((prev) => [
                  'Password must be greater than 9 characters.',
                  ...prev,
                ]);
              break;
            case PasswordError.Mismatch:
              if (isConfirm)
                setMessage((prev) => ['Passwords must match.', ...prev]);
              break;
          }
        });
      }
    }

    if ((password === '' && compareTo) || (compareTo === '' && password)) {
      setStyles(`${baseStyles} ${invalidStyles}`);
      setIsFormValid(false);
      if (isConfirm) setMessage((prev) => ['Passwords must match.', ...prev]);
    }

    if (password === '' && compareTo === '') {
      setIsFormValid(true);
      setStyles(baseStyles);
      setMessage([]);
    }
  }, [password, compareTo]);

  return (
    <>
      <input
        type="password"
        id="password"
        name="password"
        value={password}
        onChange={(e) => {
          handleChange(e, setPassword);
        }}
        className={styles}/>
      {message.length > 0 &&
        message.map((msg) => <p className="text-red-500">{msg}</p>)}
    </>
  );
}
