import type { OrgRoleWithInfo } from '@lib/constants';
import type { BasicRoleInfo } from '@lib/types';
import { type RoleLevel } from '@prisma/client';
import * as argon from 'argon2';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError } from 'remix-toast';
import { ZodError } from 'zod';
import * as Sentry from '@sentry/remix';

export const decodePermissions = (
  orgRoles: Array<{
    userId: string;
    orgRoleId: string;
    orgRole: {
      id: number;
      uuid: string;
      organizationId: string;
      contactPhone: string;
      contactEmail: string;
      isDeleted: boolean;
      roleId: string;
      role: {
        id: number;
        uuid: string;
        name: string;
        abbreviation: string;
        description: string;
        permissions: string;
        level: RoleLevel;
        isRequired: boolean;
        isDeleted: boolean;
        createdByOrgId: string;
      }
    }
  }>
): [BasicRoleInfo[], string] => {
  //const { roles } = await db.user.findFirstOrThrow({ where: { id: userId }, select: { roles: true } });
  const basicInfoRole: BasicRoleInfo[] = [];
  const permissions = new Map<string, string[]>();

  if (!Array.isArray(orgRoles)) throw new Error('Roles should be an array.');

  orgRoles.forEach((item) => {
    if (item.orgRole.role.permissions.length > 0) {
      const newPerms = new Map<string, string[]>(
        JSON.parse(atob(item.orgRole.role.permissions))
      );
      newPerms.forEach((value, key) => {
        if (!permissions.has(key)) permissions.set(key, value);
      });
      basicInfoRole.push({
        id: item.orgRoleId,
        name: item.orgRole.role.name,
        description: item.orgRole.role.description,
        abbreviation: item.orgRole.role.abbreviation,
        owningOrgId: item.orgRole.organizationId,
        roleId: item.orgRole.roleId,
      });
    }
  });

  return [basicInfoRole, JSON.stringify(Array.from(permissions))];
};

export const hashPassword = async (plainText: string) => argon.hash(plainText);

export function isSuperAdmin(roles: BasicRoleInfo[]) {
  return !!roles.find((role) => role.abbreviation === 'SUPERADMIN');
}

export function isAdmin(roles: BasicRoleInfo[]) {
  return !!roles.find((role) => role.abbreviation === 'ADMIN');
}

export function handleActionError(e: unknown, toastMsg: string) {
  console.error(e);
  Sentry.captureException(e);

  if (e instanceof Error) {
    if (e instanceof AuthorizationError) {
      return jsonWithError(
        { status: 'error', error: 'Authorization Error.' },
        'User is not authorized.'
      );
    }
    if (e instanceof ZodError) {
      return jsonWithError(
        { status: 'error', message: 'validation error', errors: e.errors },
        toastMsg
      );
    }
    return jsonWithError(
      { status: 'error', message: e.message },
      toastMsg
    );
  }
}
