import { Form, Label } from '@components/ui';
import ErrorReport from '@components/ui/ErrorReport';
import { Input } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { requireUserAsObject } from '@services/user.server';
import React from 'react';
import { getOrgRoleWithUsers } from './.server/role.queries';

export { roleEditActionFunction as action } from './.server/role.queries';

export const loader = async ({ params, request }: LoaderFunctionArgs) => {
  const { id: userId } = await requireUserAsObject(request, {
    permissions: Attribute.ORGROLES_READWRITE,
  });

  const { roleId } = params;
  if (!roleId) throw json({ status: 'error', error: 'roleId is required.' }, 500);
  if (!userId) throw json({ status: 'error', error: 'user id is required.' }, 500);

  const orgRole = await getOrgRoleWithUsers(roleId);

  return json({
    orgRole,
  });
};

const EditRoleDefaultPage = () => {
  const { orgRole } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="post">
        <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
          <Modal.Title>
            Edit Organization Role{' '}
            <p className="text-lg">{orgRole.role.name}</p>
          </Modal.Title>
          <Input.Hidden name="orgRoleId" value={orgRole.uuid} />
          <Input.Hidden name="_action" value="edit" />
          <Form.Row>
            <Label htmlFor="contactPhone">Phone:</Label>
            <Input.Text
              name="contactPhone"
              defaultValue={orgRole.contactPhone}
              required/>
          </Form.Row>
          <Form.Row>
            <Label htmlFor="contactEmail">Email:</Label>
            <Input.Email
              name="contactEmail"
              defaultValue={orgRole.contactEmail} />
          </Form.Row>
        </div>
        <Modal.Buttons link=".." value="edit">
          Save
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditRoleDefaultPage;
