import { LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { Modal } from '@components/ui/modals';
import { RolePermissions } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useFetcher, useLoaderData, useSearchParams } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import {
  assign,
  deleteChecklist,
  getTemplates,
} from './.server/template.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, { permissions: RolePermissions.SUPERADMIN });
  const { userId } = params;
  if (!userId) throw json({ status: 'error', error: 'userId is required.' });

  const { user, templates } = await getTemplates(userId);

  return json({
    user,
    templates,
  });
};

export const action = async ({ request, params }: ActionFunctionArgs) => {
  const checklistCreateSchema = z.object({
    templateId: z.string().optional(),
    userId: z.string(),
    checklistId: z.string().optional(),
    _action: z.union([z.literal('create'), z.literal('delete')]),
  });

  try {
    const { id: loggedInUserId } = await requireUserAsObject(request, {
      options: { redirectOnFailure: false },
      permissions: RolePermissions.SUPERADMIN,
    });

    const { templateId, userId, _action, checklistId } =
      checklistCreateSchema.parse({
        ...Object.fromEntries((await request.formData()).entries()),
        userId: params.userId,
      });

    if (_action === 'delete') {
      if (!checklistId)
        return json({ status: 'error', error: 'checklistId is required.' });

      await deleteChecklist(checklistId, userId);

      return jsonWithSuccess(
        {
          status: 'success',
          message: 'Template unassigned successfully',
        },
        'Checklist Unassigned!'
      );
    }
    if (_action === 'create') {
      if (!templateId)
        return json({ status: 'error', error: 'templateId is required.' });

      await assign({
        templateId,
        userId,
        assignedById: loggedInUserId,
      });

      return jsonWithSuccess(
        {
          status: 'success',
          message: 'Checklist assigned successfully',
        },
        'Checklist Assigned!'
      );
    }
  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          {
            status: 'error',
            message: 'There were validation errors.',
            issues: e.issues,
          },
          'There was an error.'
        );
      }
      return jsonWithError(
        {
          status: 'error',
          message: e.message,
        },
        'There was an error.'
      );
    }
  }
};

const UserAssignChecklist = () => {
  const { user, templates } = useLoaderData<typeof loader>();
  const [searchParams] = useSearchParams();
  const unassignChecklist = useFetcher();
  const assignChecklist = useFetcher();
  //
  // const url = searchParams.get('org') ? `?org=${searchParams.get('org')}` : '';

  return (
    <Modal>
      <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
        <Modal.Title>{user?.name}</Modal.Title>
        <h2 className="text-lg font-bold border-b border-black pb-1 mb-3">
          Available Templates
        </h2>
        <ul className="max-h-[75vh] overflow-y-auto">
          {templates?.map((template) => (
            <li
              key={template.id}
              className="flex justify-between items-center hover:bg-gray-500 hover:text-slate-100 px-3 py-2 border-b border-black hover:">
              {template.name}
              <div>
                {template.assigned && (
                  <unassignChecklist.Form method="post" className="inline">
                    <input
                      type="hidden"
                      name="checklistId"
                      value={template.checklistId}/>
                    <button
                      type="submit"
                      name="_action"
                      value="delete"
                      className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-red-300 sm:mt-0 sm:w-auto">
                      Unassign
                    </button>
                  </unassignChecklist.Form>
                )}
                <assignChecklist.Form method="post" className="inline ml-4">
                  <input type="hidden" name="templateId" value={template.id} />
                  <button
                    type="submit"
                    name="_action"
                    value="create"
                    disabled={template.assigned}
                    className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-green-200 sm:mt-0 sm:w-auto disabled:bg-gray-300 disabled:text-slate-500">
                    {template.assigned ? 'Assigned' : 'Assign'}
                  </button>
                </assignChecklist.Form>
              </div>
            </li>
          ))}
        </ul>
      </div>
      <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
        <LinkButton variant="green" to={`/template/assign`}>
          Done
        </LinkButton>
      </div>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default UserAssignChecklist;
