import type { PrismaClient } from '@prisma/client';

export interface IRepository<BaseT, CreateT, UpdateT> {
  prisma: PrismaClient;
  getAll(): Promise<BaseT[]>;

  get(id: string): Promise<BaseT>;

  create(data: CreateT): Promise<string>;

  update(id: string, data: UpdateT): Promise<any>;

  delete(id: string): Promise<void>;
}

/**
 * Base Repository class that each model will inherit
 */
export abstract class Repository<BaseT, CreateT, UpdateT>
implements IRepository<BaseT, CreateT, UpdateT>
{
  public prisma: PrismaClient;

  constructor(prismaClient: PrismaClient) {
    this.prisma = prismaClient;
  }

  public abstract getAll(): Promise<BaseT[]>;

  public abstract get(id: string): Promise<BaseT>;

  public abstract create(data: CreateT): Promise<string>;

  public abstract update(id: string, data: UpdateT): Promise<void>;

  public abstract delete(id: string): Promise<void>;

  buildUpdateData<T extends Record<string, any>>(dto: T, exclude?: string[]): Partial<T> {
    return Object.fromEntries(
      Object.entries(dto).filter(([key, value]) => value !== undefined && !exclude?.includes(key))
    ) as Partial<T>;
  }
}
