import {
  DeleteButton,
  LinkButton,
  PauseButton,
  PlayButton,
} from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import type { ChecklistType } from '@lib/constants';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useFetcher, useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import {
  getInprocessingMembers,
  getOrgBeingViewed,
} from './.server/org.queries';
import {
  ArchiveBoxArrowDownIcon,
  EyeIcon,
  PauseIcon,
  PlayIcon,
} from '@heroicons/react/24/outline';
import { AuthorizationError } from 'remix-auth';
import { jsonWithSuccess, jsonWithError } from 'remix-toast';
import { z } from 'zod';
import {
  pauseChecklist,
  resumeChecklist,
  archiveChecklist,
} from '../.server/tasks.queries';
import type { Checklist} from '@prisma/client';
import { ChecklistStatus } from '@prisma/client';
import { Input } from '@components/ui';
import { CloudArrowDownIcon } from '@heroicons/react/16/solid';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  const { currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.MOVES_READ,
  });

  const { orgId } = params;

  if (!orgId) throw new Error('org id is required.');

  const inbound = await getInprocessingMembers(orgId);
  const { orgBeingViewed } = await getOrgBeingViewed({
    defaultOrgId: currentOrg.uuid,
    searchParams: new URL(request.url).searchParams,
    params,
  });

  return json({
    inbound,
    orgBeingViewed,
  });
};

export async function action({ request }: ActionFunctionArgs) {
  const updateTaskSchema = z.object({
    itemId: z.string(),
    _action: z.union([
      z.literal('pause'),
      z.literal('resume'),
      z.literal('archive'),
    ]),
  });

  try {
    const approvedUserId = await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.TASKS_READWRITE,
    });

    const { itemId, _action } = updateTaskSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    if (_action === 'pause') {
      await pauseChecklist(itemId, approvedUserId);
      return jsonWithSuccess({ status: 'success' }, 'Checklist Paused');
    }
    if (_action === 'resume') {
      await resumeChecklist(itemId, approvedUserId);
      return jsonWithSuccess({ status: 'success' }, 'Checklist Resumed');
    }
    if (_action === 'archive') {
      await archiveChecklist(itemId, approvedUserId);
      return jsonWithError({ status: 'success' }, 'Checklist Archived!');
    }
  } catch (error) {
    console.error('Error during approval: ', error);
    if (error instanceof Error) {
      if (error instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      return jsonWithError(
        { status: 'error', error: 'Failed to perform requested action' },
        'There was an error.'
      );
    }
  }
}

const convertToCSV = (
  data: {
    name: string;
    checklist: Checklist & {
      type: ChecklistType;
      count: { totalItems: number; completedItems: number };
    };
  }[]
) => {
  const headers = [
    'Name',
    'Status',
    'Type',
    'Checklist Progress',
    'Last Updated',
  ];
  const rows = data.map(({ checklist: { count, ...checklist }, ...user }) => {
    const updatedAt = checklist.updatedAt
      ? new Date(checklist.updatedAt).toDateString()
      : 'N/A';

    let status = 'Unkown';
    switch (checklist.status) {
      case 'Archived':
        status = 'Archived';
        break;
      case 'InProgress':
        status = 'In Progress';
        break;
      case 'Paused':
        status = 'Paused';
        break;
    }

    const totalItems = count.totalItems || 0;
    const completedItems = count.completedItems || 0;
    const progress =
      totalItems > 0 ? Math.round((completedItems / totalItems) * 100) : 0;

    return [
      user.name || 'Unknown',
      status,
      checklist.type || 'N/A',
      `${progress}%`,
      updatedAt,
    ].join(',');
  });

  return [headers.join(','), ...rows].join('\n');
};

const downloadCSV = (data: any[], filename = 'checklist.csv') => {
  if (!data || data.length === 0) {
    console.error('No data to export.');
    return;
  }

  const csvContent = convertToCSV(data);
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const url = URL.createObjectURL(blob);

  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  link.style.visibility = 'hidden';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};

const InProcessingMembers = () => {
  const { inbound, orgBeingViewed } = useLoaderData<typeof loader>();
  const fetcher = useFetcher();

  return (
    <>
      <PageHeader
        org={orgBeingViewed}
        pageTitle="Inprocessing Members"
        navigateTo="inprocess"/>

      {inbound.length > 0 ? (
        <>
          <div className="w-full flex justify-end">
            <button
              type="button"
              onClick={() => downloadCSV(inbound)}
              className={`inline-flex justify-right px-4 py-1 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-white`}>
              <CloudArrowDownIcon className="w-5 h-5 mr-2" />
              Export
            </button>
          </div>

          <table className="text-left table-auto w-full border-neutral-300 border-1">
            <thead>
              <tr className="border-b border-black">
                <th></th>
                <th className="text-left">Name</th>
                <th>Status</th>
                <th>Type</th>
                <th className="text-center">Checklist Progress</th>
                <th className="w-56">Last Updated</th>
                <th className="w-56"></th>
              </tr>
            </thead>
            <tbody>
              {inbound
                .sort((a, b) => a.name.localeCompare(b.name))
                .map((user) => {
                  const updatedAt = new Date(
                    user.checklist.updatedAt
                  ).toDateString();
                  const percentage = Math.round(
                    (user.checklist.count.completedItems /
                    user.checklist.count.totalItems) *
                    100
                  );

                  return (
                    <tr
                      key={user.id}
                      className="table-row border-b even:bg-zinc-50">
                      <td className="w-5 px-2">
                        {user.checklist.status === ChecklistStatus.Paused ? (
                          <PauseIcon className="h-5 w-5" />
                        ) : (
                          <PlayIcon className="h-5 w-5" />
                        )}
                      </td>
                      <td className="py-2">{user.name}</td>
                      <td>
                        {user.checklist.status === ChecklistStatus.Archived &&
                        'Archived'}
                        {user.checklist.status === ChecklistStatus.InProgress &&
                        'In Progress'}
                        {user.checklist.status === ChecklistStatus.Paused &&
                        'Paused'}
                      </td>
                      <td>{user.checklist.type}</td>
                      <td className="text-center">
                        {Number.isNaN(percentage) ? '0%' : `${percentage}%`}
                      </td>
                      <td>{updatedAt}</td>
                      <td className="text-right flex gap-2 mt-1">
                        <fetcher.Form method="POST" className="flex gap-2">
                          <Input.Hidden
                            name="itemId"
                            value={user.checklist.id}></Input.Hidden>
                          {user.checklist.status !== ChecklistStatus.Paused ? (
                            <PauseButton
                              type="submit"
                              value="pause"
                              name="_action"
                              className="flex justify-center items-center w-12">
                              <PauseIcon className="h-5 w-5" />
                            </PauseButton>
                          ) : (
                            <PlayButton
                              type="submit"
                              value="resume"
                              name="_action"
                              className="flex justify-center items-center w-12">
                              <PlayIcon className="h-5 w-5" />
                            </PlayButton>
                          )}
                          <DeleteButton
                            type="submit"
                            value="archive"
                            name="_action"
                            className="flex justify-center items-center w-12">
                            <ArchiveBoxArrowDownIcon className="h-5 w-5" />
                          </DeleteButton>
                        </fetcher.Form>
                        <LinkButton
                          to={`/org/${orgBeingViewed.uuid}/inprocess/${user.checklist.id}/view`}
                          className="flex justify-center items-center w-12">
                          <EyeIcon className="h-5 w-5" />
                        </LinkButton>
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </>
      ) : (
        `No members to show.`
      )}
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default InProcessingMembers;
