import ErrorReport from '@components/ui/ErrorReport';
import { Input, Form, Label } from '@components/ui';
import { Modal } from '@components/ui/modals';
import { RTEProvider } from '@components/ui/RichText/Editor';
import { ChecklistType } from '@lib/constants';
import { Attribute } from '@lib/utils';
import type { ActionFunction, ActionFunctionArgs } from '@remix-run/node';
import { useSearchParams  } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { createTemplate } from './.server/template.queries';
import { useManageTemplateData } from '@lib/hooks/useMatchesData';

export const action: ActionFunction = async ({
  request,
}: ActionFunctionArgs) => {
  const createSchema = z.object({
    name: z.string().min(1),
    description: z.string({ required_error: 'Description is required' }).min(1),
    type: z.nativeEnum(ChecklistType),
    orgId: z.string(),
  });

  try {
    await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.TEMPLATES_READWRITEDELETE,
    });
    const formData = Object.fromEntries((await request.formData()).entries());
    const { name, description, type, orgId } = createSchema.parse({
      name: formData.name,
      description: formData.description,
      type: formData['checklistType'],
      orgId: formData.orgId,
    });

    //console.log('--------------', description)

    await createTemplate({
      name,
      description,
      type,
      owningOrganizationId: orgId,
    });

    return redirectWithSuccess(
      `/template${orgId ? `?org=${orgId}` : ''}`,
      'Template Added!'
    );
  } catch (err) {
    if (err instanceof AuthorizationError) {
      return jsonWithError(
        { status: 'error', error: 'Authorization Error.' },
        'User is not authorized.'
      );
    }
    if (err instanceof ZodError) {
      err.issues.forEach((issue) =>
        console.error(
          `There was an error validating on Template creation!\n${issue.path} - ${issue.message}`
        )
      );
    }

    return jsonWithError(
      {
        status: 'error',
        error: 'There was an error adding the template.',
      },
      'There was an error adding the template.'
    );
  }
};

const CreateTemplatePage = () => {
  // TODO: Add confirm to delete
  const [searchParams] = useSearchParams();
  const { orgBeingViewed } = useManageTemplateData();
  const templatesAvail = orgBeingViewed.templates;

  const orgId = searchParams.get('org');
  const dropDownData = [
    { id: 'PCS', name: 'PCS' },
    { id: 'PCA', name: 'PCA' },
    { id: 'SQ', name: 'SQ'},
  ];

  const typesToExclude = new Set(templatesAvail.map(template => template.type));

  const filteredDropDownData = dropDownData.filter(
    (item) => item.id === 'SQ' || !typesToExclude.has(item.id)
  );

  return (
    <Modal>
      <RTEProvider>
        <Form method="post">
          <Input.Hidden value={orgId ?? ''} name="orgId" />
          <Modal.Body>
            <Modal.Title>Create Template</Modal.Title>
            <Form.Row>
              <Label htmlFor="checklistType">Checklist Type</Label>
              <Input.Select name="checklistType" options={filteredDropDownData} />
            </Form.Row>
            <Form.Row>
              <Label htmlFor="name">Name</Label>
              <Input.Text name="name" />
            </Form.Row>
            <Form.Row>
              <Label htmlFor="description">Description</Label>
              <Input.RichText name="description" />
            </Form.Row>
          </Modal.Body>
          <Modal.Buttons
            link={`/template${orgId ? `?org=${orgId}` : ''}`}>
          Save
          </Modal.Buttons>
        </Form>
      </RTEProvider>
    </Modal>
  );
};

export const ErrorBoundary = ErrorReport;
export default CreateTemplatePage;
