import React, { useState, useCallback, useEffect } from 'react';
import type { CrumbProps } from '../types';
import { useBreadcrumbs } from '../context';
import { getDisplayName } from '../utils/getDisplayName';
import { OrganizationSelect } from './OrganizationSelect';
import { SelectOption } from './SelectOption';

export function Crumb({ tree, setNewParent, ancestors }: CrumbProps) {
  const [selected, setSelected] = useState('');
  const { manualSet, setManualSet, orgId } = useBreadcrumbs();

  const handleClick = useCallback((e: React.MouseEvent) => {
    e.preventDefault();
    if (setManualSet) {
      setManualSet(true);
    }
    if (selected) {
      setSelected('');
      setNewParent(tree?.uuid ?? '');
    }
  }, [selected, setManualSet, setNewParent, tree?.uuid]);

  const handleSelectChange = useCallback((value: string) => {
    setSelected(value);
    setNewParent(value);
  }, [setNewParent]);

  useEffect(() => {
    if (!manualSet && ancestors.length > 0 && tree?.children) {
      const currentParent = tree.children.find((child) =>
        ancestors.includes(child.uuid)
      );

      if (currentParent && currentParent.uuid !== orgId?.current) {
        setSelected(currentParent.uuid);
        setNewParent(currentParent.uuid);
      } else if (!currentParent) {
        setSelected('');
      }
    }
  }, [ancestors, tree, manualSet, orgId?.current, setNewParent]);

  const filteredChildren = tree?.children?.filter(
    (child) => child.uuid !== orgId?.current
  );

  const selectedChild = tree?.children?.find((child) => child.uuid === selected);

  return (
    <>
      <div
        onClick={handleClick}
        className={`${
          tree?.children && selected
            ? 'text-sm text-zinc-500 hover:text-red-800 hover:cursor-pointer'
            : 'text-base'
        } inline-flex`}>
        {getDisplayName(tree)}
      </div>

      {tree?.children && <span className="text-zinc-600 text-xs">{' / '}</span>}

      {selected && (
        <Crumb
          setNewParent={setNewParent}
          tree={selectedChild}
          ancestors={ancestors}/>
      )}

      {filteredChildren && filteredChildren?.length > 0 && (
        <div className="mt-2">
          {!selected && (
            <OrganizationSelect
              selected={selected}
              onChange={handleSelectChange}>
              {filteredChildren.map((child) => (
                <SelectOption key={child.uuid} value={child.uuid}>
                  {child.name}
                </SelectOption>
              ))}
            </OrganizationSelect>
          )}
          {selected && selected !== 'DAF' && <div className="ml-1" />}
        </div>
      )}
    </>
  );
}