import { useFetcher } from '@remix-run/react';

type ToggleButtonTypeOptions = {
  text: string;
  styles: string;
  formValue: string;
};

type ToggleButtonType = {
  complete: ToggleButtonTypeOptions;
  incomplete: ToggleButtonTypeOptions;
  ready: ToggleButtonTypeOptions;
  notReady: ToggleButtonTypeOptions;
};

export function ToggleButton({
  type,
  itemId,
  loggedInUser,
}: {
  type: keyof ToggleButtonType;
  itemId: string;
  loggedInUser: string;
}) {
  const fetcher = useFetcher();

  const baseClasses =
    'inline-flex disabled:cursor-not-allowed flex-1 justify-center items-center rounded-md px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2';

  const redButtonClasses = `${baseClasses} disabled:bg-red-400 bg-red-600 hover:bg-red-500 focus-visible:outline-red-600`;
  const greenButtonClasses = `${baseClasses} disabled:bg-green-300 bg-green-600 hover:bg-green-500 focus-visible:outline-green-600`;

  const types = {
    complete: {
      text: 'Mark Complete',
      styles: greenButtonClasses,
      formValue: 'update',
    },
    incomplete: {
      text: 'Unsign',
      styles: redButtonClasses,
      formValue: 'incomplete',
    },
    ready: {
      text: 'Mark Ready',
      styles: greenButtonClasses,
      formValue: 'markready',
    },
    notReady: {
      text: 'Mark Not Ready',
      styles: redButtonClasses,
      formValue: 'notready',
    },
  };

  return (
    <fetcher.Form
      method="POST"
      className="text-white text-center flex-1 inline-flex"
      action="/checklist">
      <input type="hidden" value={itemId} name="itemId" />
      <input type="hidden" value={loggedInUser} name="userId" />
      <button
        type="submit"
        name="_action"
        value={types[type].formValue}
        disabled={fetcher.state !== 'idle'}
        className={`${baseClasses} ${types[type].styles}`}>
        {fetcher.state === 'idle' ? types[type].text : 'Processing...'}
      </button>
    </fetcher.Form>
  );
}
