import { Models } from '@lib/models.server';

export async function getRootNode(orgId: string) {
  return Models.org.findDescendantsTree(orgId);
}

export async function getRoot() {
  return Models.org.findRoot();
}
export async function getRootTree() {
  const id = (await Models.org.findRoot()).uuid;

  return Models.org.findDescendantsTree(id);
}

export async function getRootDescendants() {
  const id = (await Models.org.findRoot()).uuid;

  return Models.org.findDescendants(id);
}
