function createTemplateArray(
  userChecklists: ChecklistWithTemplate[],
  orgTemplates: { id: string; name: string }[],
) {
  const templatesAssigned = userChecklists.map((checklist) => ({
    id: checklist.template.id,
    name: checklist.template.name,
    checklistId: checklist.id,
    assigned: true,
  }));

  const templates = new Map<
    string,
    { id: string; name: string; assigned?: boolean; checklistId?: string }
  >();

  if (templatesAssigned) {
    orgTemplates
      .concat(templatesAssigned)
      .forEach(
        (item: {
          name: string;
          id: string;
          checklistId?: string;
          assigned?: boolean;
        }) => {
          if (templates.has(item.id))
            templates.set(item.id, {
              ...item,
              assigned: true,
              checklistId: item.checklistId,
            });
          else
            templates.set(item.id, {
              ...item,
              assigned: item.assigned ?? false,
            });
        },
      );
  }

  return templates ? Array.from(templates.values()) : undefined;
}

export async function getTemplates(userId: string) {
  const user = await getUserChecklists(userId);

  if (!user.currentOrg) throw new Error("can't find current org");

  const orgTemplates = await getTemplatesByOrg(user.currentOrg?.uuid);

  const templates = createTemplateArray(user?.checklists, orgTemplates);

  return { templates, user };
}

export async function deleteChecklist(checklistId: string, userId: string) {
  await Models.checklist.delete(checklistId, userId);
}

type AssignFunctionArgs = {
  templateId: string;
  userId: string;
  assignedById: string;
};

function checklistTypeExists(checklists: any[], moveType: MoveType | null) {
  return (
    checklists.findIndex(
      (checklist: ChecklistWithTemplate) =>
        checklist.template.type === moveType,
    ) === -1
  );
}

export async function assign({
  templateId,
  userId,
  assignedById,
}: AssignFunctionArgs) {
  const userProfile = await Models.userProfile.getWithChecklistsById(userId);
  await assignChecklist(userProfile, userProfile.moveType, assignedById, {
    templateId,
  });
}

export async function getInheritedChecklistItems(
  currentOrgId: string,
  type: ChecklistType,
) {
  async function* getInheritedItems(currentOrgId: string, type: ChecklistType) {
    let currentOrg = await Models.org.get(currentOrgId);

    if (type !== 'SQ') {
      while (currentOrg) {
        const nextOrg = await Models.org.getWithTemplates(
          currentOrg.parentOrgId,
          type,
        );

        if (nextOrg && nextOrg.uuid !== 'DAF') {
          const name = nextOrg?.name;
          const items =
            nextOrg.templates.length > 0
              ? nextOrg.templates.flatMap((template) => template.items)
              : [];

          yield { name, items, owningOrg: nextOrg.uuid };

          currentOrg = { ...nextOrg };

          if (!currentOrg.parentOrgId) {
            break;
          }
        } else {
          break;
        }
      }
    }
  }

  const generator = getInheritedItems(currentOrgId, type);
  const listOfItems = [];

  for await (const inheritedItem of generator) {
    listOfItems.push(inheritedItem);
  }
  return listOfItems;
}

export async function templateItemLoader({
  request,
  params,
}: LoaderFunctionArgs) {
  const { currentOrg: userOrg } = await requireUserAsObject(request, {
    permissions: Attribute.TEMPLATES_READWRITE,
  });

  const roles = await getRolesForTemplateItem({
    searchParams: new URL(request.url).searchParams,
    defaultOrgId: userOrg.uuid,
    params,
  });

  const { templateId: checklistId } = params;
  if (!checklistId)
    throw new Error('template id and org id is required');

  return roles;
}
export async function editTemplateItemLoader(loaderArgs: LoaderFunctionArgs) {
  const { params } = loaderArgs;
  const editSentryScope = new Sentry.Scope();

  if (!params.itemId) {
    Sentry.captureException('edit template itemId is required.');
    throw new Error('itemId is required.');
  }

  try {
    const roles = await templateItemLoader(loaderArgs);

    const item = await getItemWithRole(params.itemId);

    return json({ roles, item });
  } catch (e) {
    console.error(e);
    Sentry.captureException(e, editSentryScope);
    throw new Error(e.message);
  }
}

export async function createTemplateItemLoader(loaderArgs: LoaderFunctionArgs) {
  const roles = await templateItemLoader(loaderArgs);

  return json({ roles });
}

export async function getRolesForTemplateItem({
  searchParams,
  params,
  defaultOrgId,
}: {
  searchParams: URLSearchParams;
  params: LoaderFunctionArgs['params'];
  defaultOrgId: string;
}) {
  const { orgBeingViewed: org } = await getOrgBeingViewed({
    searchParams,
    defaultOrgId,
    params,
  });

  return getRolesByOrg(org.uuid, {
    includeUser: true,
  });
}
