import { Await, useNavigation } from '@remix-run/react';
import type { ReactNode} from 'react';
import { Suspense } from 'react';

type AwaitedMetricsProps<T> = {
  resolve: Promise<T>;
  fallback: ReactNode;
  children: ( data: T ) => ReactNode;
};

export function AwaitedMetric<T>( {
  resolve,
  fallback,
  children,
}: AwaitedMetricsProps<T> ) {
  const { state: navState } = useNavigation();
  return (
    <>
      { navState !== 'idle' && fallback }
      { navState === 'idle' && (
        <Suspense fallback={ fallback }>
          <Await resolve={ resolve }>{ children }</Await>
        </Suspense>
      ) }
    </>
  );
}