import ErrorReport from '@components/ui/ErrorReport';
import { Input, Label, Form } from '@components/ui';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData, useParams } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import React from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { deleteOrg, getOrg } from './.server/org.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, { permissions: Attribute.ORGS_READWRITEDELETE });
  const { orgId } = params;
  if (!orgId) throw json({ status: 'error', error: 'orgId required' });
  return json({ org: await getOrg(orgId) });
};

// TODO: Add try catch
export async function action({ request, params }: ActionFunctionArgs) {
  const orgDeleteSchema = z.object({
    orgId: z.string().min(1),
  });

  try {
    await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.ORGS_READWRITEDELETE,
    });

    // const { orgId } = orgDeleteSchema.parse(
    //   Object.fromEntries((await request.formData()).entries())
    // );
    const { orgId } = params;

    await deleteOrg(orgId);

    return redirectWithSuccess('/org', 'Organization Deleted!');
  } catch (e) {
    console.error(e);

    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          { status: 'error', message: 'validation error', errors: e.errors },
          'There was an error removing the base.'
        );
      }
      return jsonWithError(
        { status: 'error', message: e.message },
        'There was an error removing the base.'
      );
    }
  }
}

const DeleteOrgDefaultPage = () => {
  const { org } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="POST">
        <Input.Hidden name="orgId" value={org?.uuid} />
        <Modal.Body>
          <Modal.Title>Delete Organization:</Modal.Title>
          <Form.Row>
            <p>
              Please enter
              <code className="bg-zinc-300 p-1 m-1 rounded-lg py-1">
                {org.name}
              </code>
              below to confirm the deletion.
            </p>
          </Form.Row>
          <Form.Row>
            <Label htmlFor="orgNameconf1">Type Organization Name:</Label>
            <Input.Text
              type="text"
              id="orgNameconf1"
              pattern={org.name}
              required/>
          </Form.Row>
          <Form.Row>
            <Input.Checkbox
              name="confirm"
              label="Confirm Organization Deletion"
              required/>
          </Form.Row>
        </Modal.Body>
        <Modal.Buttons link="/org" value="delete">
          Delete
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default DeleteOrgDefaultPage;
