/*
 This page starts movement actions for users.
 Show a form that displays
 - the moving user's name
 - the moving user's current org
 and has inputs for
 - Where the user is going, if known
 - type of move (default to type searchQuery)

 Once confirmed
 - remove the user from their current org and populate the next org if known and previous org

 */
import ErrorReport from '@components/ui/ErrorReport';
import { UserMoveModal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import { handleActionError } from '@lib/utils/utils.server';
import { MoveType } from '@prisma/client';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { requireUser } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { getOrgsByBase } from '../.server/org.queries';
import { getUser, startMemberMove } from './.server/user.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, { permissions: Attribute.MOVES_READWRITE });
  const typeParam = new URL(request.url).searchParams.get('type');
  // if (!typeParam) throw new Error('type is required');

  const { userId } = params;
  if (!userId) throw new Error('user id required');

  const user = await getUser(userId);

  let orgs = [];
  if (!user.currentOrg || !user.currentOrg.base) {
    throw new Error('Current organization not found.');
  } else {
    // if (typeParam === 'PCA') {
    //   // get orgs by base
    //   orgs = await context.organization.getAllAtBase(user.currentOrg.baseId);
    // } else if (typeParam === 'PCS') {
    //   // get orgs at other bases
    //   // TODO: Group by base
    //   orgs = await context.organization.getAllNotAtBase(user.currentOrg.baseId);
    // } else {
    //   throw new Error('Incorrect type');
    // }

    orgs = await getOrgsByBase(user.currentOrg.base.uuid);
  }

  return json({ user, filteredOrgs: orgs });
};

export const action = async ({ request }: ActionFunctionArgs) => {
  const actionSchema = z.object({
    moveType: z.nativeEnum(MoveType),
    nextOrg: z.string().nullable(),
    currentOrgId: z.string(),
    userId: z.string(),
  });

  try {
    await requireUser(request, { permissions: Attribute.MOVES_READWRITE });

    let {
      moveType,
      nextOrg,
      userId,
      currentOrgId: previousOrg,
    } = actionSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    if (nextOrg === '') nextOrg = null;

    await startMemberMove(userId, { moveType, nextOrg, previousOrg });

    return redirectWithSuccess('/user', 'Move Started!');
  } catch (e) {
    return handleActionError(e, 'There was an error starting the move.');
  }
};

export default function StartUserMove() {
  return (
    <UserMoveModal
      variant="start"
      cancelUrl={'/user'}/>
  );
}

export const ErrorBoundary = ErrorReport;
