import ActionMenu, { ActionMenuLink } from '@components/ui/ActionMenu';
import { LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { Table, type TableItem } from '@components/ui/table/Table';
import {
  useManageLayoutData,
  useRootLayoutData,
} from '@lib/hooks/useMatchesData';
import { Attribute } from '@lib/utils';
import { json } from '@remix-run/node';
import type { SerializeFrom, LoaderFunctionArgs } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import { requireUserAsObject } from '@services/user.server';
import useIsAdmin from '@lib/hooks/useIsAdmin';
import { getOrgBeingViewed } from '../.server/org.queries';
import { getUsersByOrg } from './.server/user.queries';
import { TailwindWidth, UserRank } from '@lib/constants';
import { useMemo } from 'react';
import type { Checklist, ChecklistStatus, Organization } from '@prisma/client';
import {
  ArchiveBoxArrowDownIcon,
  ArrowUpOnSquareIcon,
  CheckIcon,
  LockClosedIcon,
  LockOpenIcon,
  PauseCircleIcon,
  PauseIcon,
  PlayIcon,
} from '@heroicons/react/24/outline';
import { Badge } from '@components/ui/Badge';
import { SolidCheckmark } from '@components/ui/Icons';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  const { id: userId, currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.MEMBERS_READWRITEDELETE,
  });

  const { orgParam } = await getOrgBeingViewed({
    defaultOrgId: currentOrg?.uuid,
    searchParams: new URL(request.url).searchParams,
  });

  const users = await getUsersByOrg(orgParam, {
    excludeUserId: userId,
    includeChecklists: true,
  });

  return json({
    users,
  });
};

type UserRowProps = {
  user: SerializeFrom<typeof loader>['users'][number];
  isAdmin: boolean;
  orgBeingViewed: Organization;
};

function UserRow({ user, isAdmin, orgBeingViewed }: UserRowProps) {
  const hasChecklist = useMemo(
    () => user._count.checklists > 0,
    [user._count.checklists],
  );

  const activeChecklist = useMemo(
    () =>
      user.checklists.find(
        (checklist) => checklist.template?.type === user.moveType,
      ),
    [user.checklists, user.moveType],
  );

  return (
    <Table.Row item={user} key={user.id}>
      <Table.ButtonCell className="flex justify-end">
        <ActionMenu>
          <ActionMenuLink text="Manage Account" url={`${user.id}/edit`} />
          {user.moveType || hasChecklist ? (
            <>
              {hasChecklist ? ( // Move confirmed and checklist assigned
                activeChecklist && (
                  <ActionMenuLink
                    text="View Checklist"
                    url={`/org/${orgBeingViewed.uuid}/inprocess/${
                      activeChecklist?.id
                    }/view`}
                  />
                )
              ) : (
                // Move awaiting verification
                <ActionMenuLink text="Edit Move" url={`${user.id}/move/edit`} />
              )}
            </>
          ) : (
            <ActionMenuLink
              text="Start Move"
              url={{
                pathname: `${user.id}/move/start`,
                searchParams: { type: 'PCS' },
              }}
            />
          )}
          {isAdmin && (
            <ActionMenuLink
              text="Delete User Account"
              type="danger"
              url={`${user.id}/delete`}
            />
          )}
        </ActionMenu>
      </Table.ButtonCell>
    </Table.Row>
  );
}

function ClipboardListIcon({
  alt,
  ...props
}: {
  className: string;
  alt: string;
}) {
  return (
    <svg
      {...props}
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      {alt && <title>{alt}</title>}
      <path stroke="none" d="M0 0h24v24H0z" fill="none" />
      <path d="M9 5h-2a2 2 0 0 0 -2 2v12a2 2 0 0 0 2 2h10a2 2 0 0 0 2 -2v-12a2 2 0 0 0 -2 -2h-2" />
      <path d="M9 3m0 2a2 2 0 0 1 2 -2h2a2 2 0 0 1 2 2v0a2 2 0 0 1 -2 2h-2a2 2 0 0 1 -2 -2z" />
      <path d="M9 12l.01 0" />
      <path d="M13 12l2 0" />
      <path d="M9 16l.01 0" />
      <path d="M13 16l2 0" />
    </svg>
  );
}

const ManageUserIndex = () => {
  const { users } = useLoaderData<typeof loader>();
  const { orgBeingViewed } = useManageLayoutData();
  const roles = useRootLayoutData('roles');
  const { isAdmin } = useIsAdmin(roles);

  const tableContents: TableItem[] = [
    {
      header: '',
      key: '',
      width: TailwindWidth.W_40,
      render: (_, user) => {
        const hasChecklist = user._count.checklists > 0;
        const movePending = !hasChecklist && !!user.moveType;

        const activeChecklist: Checklist | undefined = user.checklists.find(
          (checklist) => checklist.template?.type === user.moveType,
        );

        function calcStatusVariant() {
          if (!activeChecklist) {
            return null;
          }
          switch (activeChecklist.status) {
            case 'Paused':
              return {
                variant: 'amber',
                Icon: <PauseCircleIcon className="size-5" />,
              };
            case 'Locked':
              return {
                variant: 'red',
                Icon: <LockOpenIcon className="size-5" />,
              };
            case 'InProgress':
              return {
                variant: undefined,
                Icon: <ClipboardListIcon className="size-5" />,
              };
            case 'Completed':
              return {
                variant: 'green',
                Icon: <CheckIcon className="size-5" />,
              };
            case 'Archived':
              return {
                variant: 'purple',
                Icon: <ArchiveBoxArrowDownIcon className="size-5" />,
              };
          }
          return null;
        }

        const status = calcStatusVariant();

        return (
          <div className="text-nowrap text-center">
            {hasChecklist && activeChecklist && (
              <div className="flex items-center justify-center w-full gap-2">
                <LinkButton
                  variant={status?.variant}
                  title={status?.text}
                  className="flex items-center justify-center gap-1 text-xs"
                  to={`/org/${orgBeingViewed.uuid}/inprocess/${activeChecklist?.id}/view`}
                >
                  {status?.Icon}
                  {activeChecklist.template.type}
                </LinkButton>
                <p className="text-center">{status?.text ?? ''}</p>
              </div>
            )}
            {movePending && (
              <LinkButton
                title="Pending Move"
                variant="amber"
                className="inline-flex items-center gap-1 text-xs"
                to={`${user.id}/move/edit`}
              >
                <ArrowUpOnSquareIcon className="size-5" title="Pending Move" />
                Edit Move
              </LinkButton>
            )}
            {!movePending && !hasChecklist && (
              <LinkButton
                title="Pending Move"
                variant="green"
                className="inline-flex items-center gap-1 text-xs"
                to={{
                  pathname: `${user.id}/move/start`,
                  searchParams: { type: 'PCS' },
                }}
              >
                <PlayIcon className="size-5" title="Pending Move" />
                Start Move
              </LinkButton>
            )}
          </div>
        );
      },
    },
    {
      header: 'Rank',
      key: 'rank',
      width: 'w-24',
      textAlign: 'text-center',
      render: (rank: string) => {
        return <div className="text-right">{UserRank[rank].abb}</div>;
      },
    },
    {
      header: 'Name',
      key: 'name',
    },
    {
      header: 'Email',
      key: 'workEmail',
    },
  ];

  return (
    <>
      <div className="mb-5">
        <PageHeader org={orgBeingViewed} pageTitle="Manage Accounts" />
        <div>
          {users.length > 0 && (
            <Table cells={tableContents}>
              <Table.HeaderRow></Table.HeaderRow>
              <Table.Body>
                {users
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((user) => {
                    return (
                      <UserRow
                        key={user.id}
                        user={user}
                        isAdmin={isAdmin}
                        orgBeingViewed={orgBeingViewed}/>
                    );
                  })}
              </Table.Body>
            </Table>
          )}
          {users.length === 0 && <p>No Users found.</p>}
        </div>
      </div>
      <Outlet />
    </>
  );
};
export const ErrorBoundary = ErrorReport;
export default ManageUserIndex;
