import { singleton } from '@lib/utils/singleton.server';
import { BaseRepository } from '@repositories/base.server';
import { ChecklistRepository } from '@repositories/checklist.server';
import { ChecklistItemRepository } from '@repositories/checklistItem.server';
import { MetricsRepository } from '@repositories/metrics.server';
import { OrganizationRepository } from '@repositories/organization.server';
import { OrganizationRoleRepository } from '@repositories/organizationRole.server';
import { RoleRepository } from '@repositories/role.server';
import { TemplateRepository } from '@repositories/template.server';
import { TemplateItemRepository } from '@repositories/templateItem.server';
import { UserRepository } from '@repositories/user.server';
import dbServer from '@services/db.server';

export let Models: {
  userProfile: UserRepository;
  org: OrganizationRepository;
  base: BaseRepository;
  checklist: ChecklistRepository;
  template: TemplateRepository;
  checklistItem: ChecklistItemRepository;
  orgRole: OrganizationRoleRepository;
  role: RoleRepository;
  templateItem: TemplateItemRepository;
  metrics: MetricsRepository;
};

/**
 * In production, we only ever want a single copy of each repository. However, in development that breaks HMR.
 */

if (process.env.NODE_ENV === 'production') {
  Models = {
    base: singleton<BaseRepository>( 'baseRepository', () => new BaseRepository( dbServer ) ),
    checklistItem: singleton( 'checklistItemRepository', () => new ChecklistItemRepository( dbServer ) ),
    checklist: singleton( 'checklistRepository', () => new ChecklistRepository( dbServer ) ),
    org: singleton( 'orgRepository', () => new OrganizationRepository( dbServer ) ),
    orgRole: singleton( 'orgRoleRepository', () => new OrganizationRoleRepository( dbServer ) ),
    role: singleton( 'roleRepository', () => new RoleRepository( dbServer ) ),
    templateItem: singleton( 'templateItemRepository', () => new TemplateItemRepository( dbServer ) ),
    template: singleton( 'templateRepository', () => new TemplateRepository( dbServer ) ),
    userProfile: singleton( 'userRepository', () => new UserRepository( dbServer ) ),
    metrics: singleton ( 'metricsRepository', () => new MetricsRepository( dbServer ) ),
  };
} else {
  Models = {
    base: new BaseRepository( dbServer ),
    checklistItem: new ChecklistItemRepository( dbServer ),
    checklist: new ChecklistRepository( dbServer ),
    org: new OrganizationRepository( dbServer ),
    orgRole: new OrganizationRoleRepository( dbServer ) ,
    role: new RoleRepository( dbServer ),
    templateItem: new TemplateItemRepository( dbServer ) ,
    template: new TemplateRepository( dbServer ),
    userProfile: new UserRepository( dbServer ),
    metrics: new MetricsRepository( dbServer ),
  };
}
