import type { UserSessionData } from '@lib/constants';
import type { CategoryPermission } from '@lib/types';
import {
  type AttributeCategory,
  compareMaps,
  isUserSessionData,
  objectToMap,
  type Operation,
  verifyAttributes,
} from '@lib/utils';
import { Models } from '@lib/models.server';
import { authenticator } from '@services/auth.server';
import { destroySession, getSession } from '@services/session.server';
import { AuthorizationError } from 'remix-auth';
import { redirectWithError } from 'remix-toast';

type RequireUserOptions = {
  failureRedirect?: string;
  redirectOnFailure?: boolean;
  returnUserObject?: boolean;
};

type RequireUserProps = {
  options?: RequireUserOptions;
  permissions?: CategoryPermission | CategoryPermission[];
};

// Define Content Security Policy (CSP) directives
const cspDirectives = [
  'default-src \'self\'',
  'object-src \'none\'',
  'upgrade-insecure-requests',
];

const cspHeader = cspDirectives.join('; ');

export async function requireUser<T extends RequireUserProps>(
  request: Request,
  props?: T
): Promise<T['options'] extends { returnUserObject: true } ? UserSessionData : string> {
  const { permissions, options = {} } = props ?? { options: {} };
  const { failureRedirect = '/', redirectOnFailure = true } = options;

  const { userId } = await authenticator.isAuthenticated(request, {
    failureRedirect: '/login',
  });

  let user;

  try {
    user = await Models.userProfile.getSessionObject(userId);
  } catch (e) {
    if (redirectOnFailure && failureRedirect) {
      const headers = new Headers();

      headers.append('Set-Cookie', await destroySession(await getSession(request.headers.get('cookie'))));
      headers.append('Content-Security-Policy', cspHeader); // Apply CSP header

      throw await redirectWithError(
        failureRedirect,
        'Access Denied: You do not have permission to view this page or perform this action. Please contact the administrator for assistance.',
        { headers }
      );
    } else {
      throw new AuthorizationError('User not authorized.');
    }
  }

  if (permissions) {
    if (!user.permissions) {
      if (redirectOnFailure) {
        throw redirectWithError(
          failureRedirect,
          'Access Denied: You do not have permission to view this page or perform this action. Please contact the administrator for assistance.'
        );
      } else throw new AuthorizationError('User not authorized.');
    }

    // Convert strings to Map()s
    const userPermissions = new Map<AttributeCategory, Operation[]>(JSON.parse(user.permissions));

    // Convert object to map
    const requiredPermissions = objectToMap(permissions);

    verifyAttributes(userPermissions);

    if (!compareMaps(userPermissions, requiredPermissions)) {
      if (redirectOnFailure) {
        throw redirectWithError(
          failureRedirect,
          'Access Denied: You do not have permission to view this page or perform this action. Please contact the administrator for assistance.'
        );
      } else throw new AuthorizationError('User not authorized.');
    }
  }

  // Apply CSP when returning user data
  const headers = new Headers();
  headers.append('Content-Security-Policy', cspHeader);

  if (options.returnUserObject) {
    const hasAdmin = user.roles.find((orgRole) => orgRole.abbreviation === 'ADMIN');

    if (hasAdmin) {
      user.currentOrg = await Models.org.get(hasAdmin?.owningOrgId);
    }

    return user as T['options'] extends { returnUserObject: true } ? UserSessionData : never;
  }

  return userId as T['options'] extends { returnUserObject?: true } ? never : never;
}

export async function requireUserAsObject(
  request: Request,
  {
    options,
    permissions,
  }: {
    options?: RequireUserOptions;
    permissions?: CategoryPermission | CategoryPermission[];
  } = { options: { returnUserObject: true } }
): Promise<UserSessionData> {
  try {
    if (!options) options = {};

    options.returnUserObject = true;

    const userData = await requireUser(request, { options, permissions });

    if (!isUserSessionData(userData)) {
      throw new Error('User data is not an object');
    }

    return userData;
  } catch (e) {
    throw e;
  }
}

