import { Badge, type BadgeProps } from '@components/ui/Badge';
import { CalendarCheckmark, StartTimer } from '@components/ui/Icons';
import { Pagination, usePagination } from '@lib/hooks/usePagination';
import type { GetUserChecklistsResponse } from '@repositories/checklist.server';
import ChecklistItemCard from './ItemCard';

const ChecklistView = ({
  checklist,
  loggedInUser,
}: {
  checklist: GetUserChecklistsResponse[0];
  loggedInUser: string;
}) => {
  const {
    currentItems,
    currentPage,
    totalPages,
    setCurrentPage,
    nextPage,
    prevPage,
    itemPos,
    setItemsPerPage,
    itemsPerPage,
  } = usePagination(checklist.items, 10);

  const totalItems = checklist.items.length;
  const completeItemsCount = checklist.items.filter(
    (item) => item.isComplete
  ).length;
  const totalPercentage = (completeItemsCount / totalItems) * 100;

  const startDate = new Date(checklist.createdAt);
  const dueDate = new Date(startDate);
  dueDate.setDate(dueDate.getDate() + 30);

  const today = new Date(Date());
  const thirtyDays = new Date();
  thirtyDays.setDate(today.getDate() + 30);
  const twoWeeks = new Date();
  twoWeeks.setDate(today.getDate() + 14);
  const fiveDays = new Date();
  fiveDays.setDate(today.getDate() + 5);
  let dateBadgeVariant: BadgeProps['variant'] = 'none';
  if (dueDate < thirtyDays) {
    if (dueDate < twoWeeks) {
      if (dueDate < fiveDays) {
        if (dueDate < today) {
          dateBadgeVariant = 'danger';
        } else dateBadgeVariant = 'warning';
      } else dateBadgeVariant = 'primary';
    }
  }
  return (
    <>
      <section key={ checklist.id } className="pt-3 mb-20 pb-5">
        <div className="pb-2 border-b-1 border-b border-gray-300">
          <div className="space-y-1">
            <ul className="flex items-center justify-between">
              <li className="font-medium text-secondary-700">Progress</li>
              <li className="text-sm text-secondary-500">
                { completeItemsCount } / { totalItems } Completed
              </li>
              <li className="text-sm text-secondary-500">
                { totalPercentage.toFixed() }%
              </li>
            </ul>
            <div className="relative flex h-2 w-full overflow-hidden rounded-full bg-secondary-200">
              <div
                role="progressbar"
                aria-valuenow={ totalPercentage }
                aria-valuemin={ 0 }
                aria-valuemax={ 100 }
                style={ { width: `${ totalPercentage }%` } }
                className="flex h-full items-center justify-center bg-green-500 text-xs text-white"></div>
            </div>
          </div>
          <h2 className="text-2xl font-bold mt-5">
            Checklist: { checklist.name }
          </h2>
          <div className="text-sm px-2 flex gap-x-2 items-center my-2">
            <Badge className="gap-x-2"><StartTimer className="size-4"/>
              Started { startDate.toLocaleString( 'en-US', {
                month: 'numeric',
                day: 'numeric',
                year: 'numeric',
              } ) }</Badge>
            <Badge className="gap-x-2" variant={ dateBadgeVariant }>
              <CalendarCheckmark className="size-5" />
              Due { dueDate.toLocaleString( 'en-US', {
                month: 'numeric',
                day: 'numeric',
                year: 'numeric',
              } ) }</Badge>
          </div>
        </div>

        <ul className="list-none">
          { currentItems.map( ( item ) => (
            <ChecklistItemCard
              key={ item.id }
              item={ item }
              loggedInUser={ loggedInUser }/>
          ) ) }
        </ul>

        <Pagination
          currentPage={ currentPage }
          totalPages={ totalPages }
          setCurrentPage={ setCurrentPage }
          itemsPerPage={ itemsPerPage }
          setItemsPerPage={ setItemsPerPage }
          itemPos={ itemPos }
          nextPage={ nextPage }
          prevPage={ prevPage }/>
      </section>
    </>
  );
};

export default ChecklistView;
