import { LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import useFilteredUsers from '@lib/hooks/useFilteredUsers';
import { RolePermissions } from '@lib/utils';
import { json, type LoaderFunctionArgs } from '@remix-run/node';
import { Outlet, useLoaderData, useSearchParams } from '@remix-run/react';
import { SearchContext } from '@routes/_withNav+/_withSideBar+/_layout';
import { requireUserAsObject } from '@services/user.server';
import { getOrgBeingViewed } from './.server/org.queries';
import { getUsersByOrg } from './user+/.server/user.queries';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  const { id: userId, currentOrg } = await requireUserAsObject(request, {
    permissions: RolePermissions.ADMIN,
  });

  const { orgBeingViewed, orgParam } = await getOrgBeingViewed({
    defaultOrgId: currentOrg.uuid,
    searchParams: new URL(request.url).searchParams,
  });

  const users = await getUsersByOrg(orgParam, {
    includeChecklists: true,
  });

  return json({
    orgBeingViewed,
    users,
    currentUser: { id: userId },
  });
};

const TemplateAssignView = () => {
  const { users, orgBeingViewed, currentUser } =
    useLoaderData<typeof loader>();
  const [searchParams] = useSearchParams();

  const orgParam = searchParams.get('org');

  const {filteredUsers} = useFilteredUsers({searchContext: SearchContext, initialUserList: users});

  return (
    <>
      <div className="mb-5">
        <PageHeader org={orgBeingViewed} pageTitle="Assign Template" />
        <div className="mb-5">
          {filteredUsers.length > 0 && (
            <ul>
              {filteredUsers.map((user) => (
                <li
                  className="flex flex-wrap justify-between items-center p-2 border-b border-b-1 border-black even:bg-zinc-50"
                  key={`template-${user.id}`}>
                  <div>
                    <p className="font-bold text-lg flex">
                      {user.name}
                      {user.id === currentUser.id && (
                        <span className="font-normal text-xs rounded-xl bg-blue-500 text-white px-3 py-[0.15rem] ml-1 self-center">
                          You
                        </span>
                      )}
                    </p>
                    <p>Checklists Assigned: {user._count.checklists}</p>
                  </div>
                  <LinkButton
                    variant="green"
                    to={`/template/assign/${user.id}`}>
                    Manage Checklists
                  </LinkButton>
                </li>
              ))}
            </ul>
          )}
          {filteredUsers.length === 0 && <p>No Users found.</p>}
        </div>
      </div>
      <Outlet />
    </>
  );
};
export const ErrorBoundary = ErrorReport;
export default TemplateAssignView;
