import useGenerateSearchParams from '@lib/hooks/useGenerateSearchParams';
import type { SideBarNavLink } from '@lib/types';
import {
  Link,
  useLocation,
} from '@remix-run/react';

type SidebarLinkProps = {
  links: SideBarNavLink['links'];
  header: string;
};

type IsActiveByPathFn = (path: string, pathToCompare: string) => boolean;

type IsActiveByStringFn = (
  str: string | RegExp,
  strToCompare: string,
) => boolean;

const isPathEqual: IsActiveByPathFn = (href, pathToCompare) =>
  pathToCompare.toLowerCase() === href;

const compareByRegex = (regex: RegExp, path: string) => !!path.match(regex);

const searchPath: IsActiveByStringFn = (urlPattern, path) => {
  if (typeof urlPattern === 'string')
    return path.toLowerCase().includes(urlPattern.toLowerCase());
  else return compareByRegex(urlPattern, path);
};

export default function SidebarLink({ links, header }: SidebarLinkProps) {
  const location = useLocation();

  let isActive: boolean;
  const extraMenuListItemClasses = 'mt-4 mb-6';
  const extraMenuListItemHeaderClasses = 'text-xl mb-2';
  const extraNavLinkClass =
    'flex items-center rounded p-2 px-4 hover:bg-slate-400';
  const extraNavLinkActive = 'bg-gray-500 text-white';
  const extraMenuIconClasses = 'h-6 w-6 mr-3';

  const hasLinks = links.length > 0;

  const searchParams = useGenerateSearchParams({keysToRemove: 'includeChildren'});

  return (
    hasLinks && (
      <li className={extraMenuListItemClasses}>
        <h3 className={extraMenuListItemHeaderClasses}>{header}</h3>
        <ul>
          {links.map((link, linkIndex) => {
            if (link.urlPattern) {
              isActive = searchPath(link.urlPattern, location.pathname);
            } else {
              isActive = isPathEqual(link.href, location.pathname);
            }
            return (
              <li
                key={`${link.text}-${linkIndex}`}
                className={link.extraClasses ?? ''}>
                <Link
                  prefetch="intent"
                  to={ { pathname: link.href, search: searchParams }}
                  className={`${extraNavLinkClass}${
                    isActive ? ` ${extraNavLinkActive}` : ''
                  } `}>
                  {link.icon && <link.icon className={extraMenuIconClasses} />}
                  {link.text}
                </Link>
              </li>
            );
          })}
        </ul>
      </li>
    )
  );
}
