import ErrorReport from '@components/ui/ErrorReport';
import {
  Select,
} from '@components/ui/Input';
import { Input, Form, Label } from '@components/ui';
import { Modal } from '@components/ui/modals';
import { RTEProvider } from '@components/ui/RichText/Editor';
import { ChecklistType } from '@lib/constants';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData, useParams } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { getOrgDescendants } from './.server/org.queries';
import { getTemplateWithOrg, updateTemplate } from './.server/template.queries';
import { useManageLayoutData } from '@lib/hooks/useMatchesData';

export const loader = async ({ params, request }: LoaderFunctionArgs) => {
  const { currentOrg: org } = await requireUserAsObject(request, {
    permissions: Attribute.TEMPLATES_READWRITE,
  });

  const { templateId } = params;

  if (!templateId)
    throw json({ status: 'error', error: 'ID is required' }, { status: 400 });

  const template = await getTemplateWithOrg(templateId);

  if (!template) throw json({ error: 'no checklist found.' }, { status: 404 });

  const childOrgs = await getOrgDescendants(org.uuid);

  return json({ template, childOrgs });
};

export const action = async ({
  request,
  params,
}: ActionFunctionArgs) => {
  const { templateId } = params;
  if (!templateId) return json({ status: 'error', error: 'No checklist id.' });

  const checklistEditSchema = z.object({
    id: z.string().min(1),
    name: z.string().min(1),
    description: z.string().min(1),
    redirectOrg: z.string().optional(),
    type: z.nativeEnum(ChecklistType),
    // owningOrgId: z.string().optional(),
  });

  try {
    await requireUser(request, {
      options: {
        redirectOnFailure: false,
      },
      permissions: Attribute.TEMPLATES_READWRITE,
    });
    const formData = await request.formData();
    const { id, name, description, type, redirectOrg } = 
      checklistEditSchema.parse({
        id: formData.get('templateId'),
        name: formData.get('name'),
        description: formData.get('description'),
        type: formData.get('checklistType'),
        redirectOrg: formData.get('redirectOrg'),
        // owningOrgId: formData.get('owningOrg'),
      });

    await updateTemplate(id, {
      type,
      description,
      name,
      // owningOrganizationId: owningOrgId,
    });

    return redirectWithSuccess(`..?org=${redirectOrg}`, 'Template saved!');
  } catch (err) {
    console.error(err);
    if (err instanceof AuthorizationError) {
      return jsonWithError(
        { status: 'error', error: 'Authorization Error.' },
        'User is not authorized.'
      );
    }
    if (err instanceof ZodError) {
      err.issues.forEach((issue) =>
        console.error(
          `There was an error validating on Template creation!\n${issue.path} - ${issue.message}`
        )
      );
      return jsonWithError({
        status: 'error',
        error: {
          message: 'There was an error adding the template.',
          items: err.issues,
        },
      }, 'There was an error adding the template!');
    }

    return jsonWithError({ status: 'error', error: 'There was an error adding the template.' }, 'There was an error adding the template!');
  }
};

const EditTemplate = () => {
  const { templateId } = useParams();
  const { template, childOrgs } = useLoaderData<typeof loader>();
  const { orgBeingViewed } = useManageLayoutData();

  //Gross hardcode
  const checklistTypes = [
    { id: ChecklistType.PCS, name: 'PCS' },
    { id: ChecklistType.PCA, name: 'PCA' },
    { id: ChecklistType.SQ, name: 'SQ'},
  ];

  return (
    <Modal>
      <RTEProvider>
        <Form method="post">
          <Input.Hidden name="redirectOrg" value={orgBeingViewed.uuid} />
          <Input.Hidden name="templateId" value={templateId} />
          <Modal.Body>
            <Modal.Title>Edit Template</Modal.Title>
            <Form.Row>
              <Label htmlFor="name">Name</Label>
              <Input.Text name="name" defaultValue={template.name}></Input.Text>
            </Form.Row>
            <Form.Row>
              <Label htmlFor="checklistType">Checklist Type</Label>
              <Select
                name="checklistType"
                defaultValue={template.type}
                options={checklistTypes} />
            </Form.Row>
            <Form.Row>
              <Label htmlFor="description">Description</Label>
              <Input.RichText content={template.description} name="description"></Input.RichText>
            </Form.Row>
            {/* <Form.Row>
              <Label htmlFor="owningOrg">Owning Organization</Label>
              <Select
                name="owningOrg"
                defaultValue={
                  childOrgs.find(
                    (org: { id: string }) =>
                      org.id === template.organizationId
                  )?.id
                }
                options={childOrgs} />
            </Form.Row> */}
          </Modal.Body>
          <Modal.Buttons
            link={`/template/${templateId}?org=${template.owningOrganization.id}`}
            value="edit">
          Save
          </Modal.Buttons>
        </Form>
      </RTEProvider>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditTemplate;
