import { Models } from '@lib/models.server';
import { MoveType, Rank } from '@prisma/client';
import { PrismaClientKnownRequestError } from '@prisma/client/runtime/library';
import { json, redirect } from '@remix-run/node';
import type { UserRegisterDto } from '@repositories/dtos';
import { assignChecklist } from '@routes/_withNav+/_withSideBar+/_manage+/user+/.server/user.queries';
import { commitDataSession, getDataSession } from '@services/cookie.server';
import type { ZodTypeAny } from 'zod';
import { z, ZodError } from 'zod';

export async function getOrgs() {
  return Models.org.getAll({ excludeDaf: true });
}

export async function registerUser(data: UserRegisterDto, loggedInUser?: string, currentOrg?: string) {
  if (loggedInUser && currentOrg) {
    const user = await Models.userProfile.create( data );
    if (data.assignChecklist)
    {
      await assignChecklist(user, data.moveType, loggedInUser, { orgId: currentOrg });
    }
    return user.id;
  }
  return Models.userProfile.register(data);
}

export enum ErrorType {
  Generic,
  AlreadyExists,
  Validation,
}

export async function registerUserActionFn(
  request: Request,
  redirectUrl: string,
  loggedInUser?: string,
  orgId?: string
) {
  try {
    const account = validateRequest(
      Object.fromEntries((await request.formData()).entries())
    );
    // return json(account);
    await registerUser(account, loggedInUser, orgId);
    const session = await getDataSession(request.headers.get('cookie'));

    session.flash(
      'message',
      'Account created successfully. Please wait for CSS Verification.'
    );

    return redirect(redirectUrl, {
      headers: {
        'Set-Cookie': await commitDataSession(session),
        'X-Frame-Options': 'SAMEORIGIN',
        'Content-Security-Policy': 'frame-ancestors: none',
      },
    });
  } catch (err) {
    console.error(err);
    if (err instanceof ZodError) {
      return json({ status: 'error', error: err.errors }, {
        headers: {
          'X-Frame-Options': 'SAMEORIGIN',
        },
        status: 422,
      });
    } else if (err instanceof PrismaClientKnownRequestError) {
      return json(
        {
          status: 'error',
          error: {
            type: ErrorType.AlreadyExists,
            message: `The email provided is already in use.`,
          },
        },
        {
          headers: {
            'X-Frame-Options': 'SAMEORIGIN',
          },
          status: 400,
        }
      );
    } else {
      return json(
        {
          status: 'error',
          error: {
            type: ErrorType.Generic,
            message: `The email provided is already in use.`,
          },
        },
        {
          headers: {
            'X-Frame-Options': 'SAMEORIGIN',
          },
          status: 400,
        }
      );
    }
  }
}

const zodRadioButton: [ZodTypeAny, ZodTypeAny, ...ZodTypeAny[]] = [
  z.literal('true').transform(() => true),
  z.literal('false').transform(() => false),
];

export const registerSchema = z.object({
  name: z.string(),
  org: z.string(),
  personalPhone: z.string().nullable(),
  workPhone: z.string().nullable(),
  workEmail: z.string(),
  personalEmail: z.string().optional(),
  hasDependents: z.union(zodRadioButton),
  isDormResident: z.union(zodRadioButton),
  hasEFMP: z.union(zodRadioButton),
  password: z.string(),
  rank: z.nativeEnum(Rank),
  sponsor: z.string().optional(),
  supervisor: z.string().optional(),
  assignedOrg: z.string().optional(),
  moveType: z.nativeEnum(MoveType).optional(),
  assignChecklist: z.preprocess((value) => value === 'true', z.boolean()),
  emailDomain: z.union([
    z.literal('us.af.mil'),
    z.literal('us.navy.mil'),
    z.literal('uscg.mil'),
    z.literal('mail.mil'),
    z.literal('health.mil'),
    z.literal('spaceforce.mil'),
    z.literal('army.mil'),
    z.literal('usmc.mil'),
  ]),
});

export function validateRequest(obj: { [p: string]: FormDataEntryValue }) {
  const {
    assignChecklist,
    assignedOrg,
    emailDomain,
    hasDependents,
    hasEFMP,
    isDormResident,
    moveType,
    name,
    org,
    password,
    // personalEmail,
    // personalPhone,
    rank,
    // sponsor,
    // supervisor,
    workEmail,
    workPhone,
  } = registerSchema.parse(obj);

  return {
    password,
    moveType,
    assignChecklist,
    profile: {
      rank,
      name,
      workEmail: workEmail + '@' + emailDomain,
      currentOrgId: org,
      workPhone,
      image: '',
      hasDependents,
      isDormResident,
      hasEFMP,
      sponsorReach: false,
      assignedOrg,
    },
  };
}
