import ErrorReport from '@components/ui/ErrorReport';
import { Modal } from '@components/ui/modals';
import { Form, Input } from '@components/ui';
import { Attribute } from '@lib/utils';
import {
  type ActionFunctionArgs,
  json,
  type LoaderFunctionArgs,
} from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z from 'zod';
import { getBase } from './.server/base.queries';
import { createOrg } from './.server/org.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, { permissions: Attribute.BASES_READ });
  if (!params.baseId) throw new Error('baseId is required.');
  return json({ base: await getBase(params.baseId) });
};

export const action = async ({ request, params }: ActionFunctionArgs) => {
  try {
    const { id: loggedInUserId } = await requireUserAsObject(request, { permissions: Attribute.BASES_READWRITE });

    if ( !params.baseId )
      return jsonWithError(
        { status: 'error', error: 'baseId is requried.' },
        'There was an error.'
      );
    const paramsRegEx = new RegExp( params.baseId );

    const addSchema = z.object( {
      _action: z.string().regex( /add/ ),
      baseId: z.string().regex( paramsRegEx ),
      name: z.string(),
      abbreviation: z.string(),
      userId: z.string(),
    } );

    const { baseId, name, abbreviation, userId} = addSchema.parse( {
      userId: loggedInUserId,
      ...Object.fromEntries( await request.formData() ),
    } );

    
    await createOrg( { baseId, name, abbreviation, userId, isWing: true , isBase: false, createdAt: new Date(Date.now()), createdBy: userId, updatedAt: new Date(Date.now()), updatedBy: userId} );

    return redirectWithSuccess( `/base`, 'Organization Created.' );
  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      return jsonWithError(
        { status: 'error', error: e.message },
        'There was an error.'
      );
    }
  }
};

const AddOrgToBase = () => {
  const { base } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="POST">
        <Modal.Body>
          <Input.Hidden name="baseId" value={base.uuid} />
          <Modal.Title>New Wing at {base.name}</Modal.Title>
          <div className="mb-3">
            <label
              htmlFor="name"
              className="mb-2 block font-medium text-gray-700">
              Name:
            </label>
            <input
              type="text"
              id="name"
              name="name"
              className="mt-1 h-10 w-full rounded border bg-gray-50 px-4 text-slate-950"/>
            <span className="text-sm">ex: 60th Air Mobility Wing</span>
          </div>
          <div className="mb-3">
            <label
              htmlFor="abbreviation"
              className="mb-2 block font-medium text-gray-700">
              Abbreviation:
            </label>
            <input
              type="text"
              id="abbreviation"
              name="abbreviation"
              className="mt-1 h-10 w-full rounded border bg-gray-50 px-4 text-slate-950"/>
            <span className="text-sm">ex: 60AMW</span>
          </div>
        </Modal.Body>
        <Modal.Buttons link="/base" value="add">Add</Modal.Buttons>
      </Form>
    </Modal>
  );
};

export const ErrorBoundary = ErrorReport;
export default AddOrgToBase;
