import { type PrismaClient, type Role, RoleLevel } from '@prisma/client';
import type { FindAllOptions } from '@repositories/organizationRole.server';
import dbServer from '@services/db.server';
import type { RoleDto } from './dtos';
import type { IRepository } from './Repository.server';

interface IRoleRepository extends IRepository<Role, RoleDto, RoleDto> {
  getAllCustom(): Promise<any>;

  findAllNotAssigned(orgId: string): Promise<any>;
}

export class RoleRepository implements IRoleRepository {
  public prisma;

  constructor(prismaClient: PrismaClient) {
    this.prisma = prismaClient;
  }

  public async getAll(): Promise<Role[]> {
    return this.prisma.role.findMany({
      where: {
        abbreviation: { notIn: ['USER', 'ADMIN', 'SUPERADMIN'] },
      },
      orderBy: {
        name: 'asc',
      },
    });
  }

  public async findByOrg(id: string, options: FindAllOptions) {
    const notInArray = [];
    if (!options?.includeAdmin) notInArray.push('ADMIN');
    if (!options?.includeSuperAdmin) notInArray.push('SUPERADMIN');

    const roles = this.prisma.role.findMany({
      where: {
        OR: [
          { level: RoleLevel.BASE },
          {
            orgRoles: {
              some: {
                organizationId: id,
              },
            },
          },
          { abbreviation: options.includeUser ? 'USER' : undefined },
        ],
        abbreviation: {
          notIn: notInArray,
        },
      },
    });

    return roles;
  }

  public async getAllCustom() {
    return this.prisma.role.findMany({
      where: {
        AND: {
          isRequired: false,
          createdByOrgId: { not: null },
          level: 'UNIT',
          abbreviation: { notIn: ['USER', 'ADMIN', 'SUPERADMIN'] },
        },
      },
      include: {
        createdByOrg: {
          select: {
            id: true,
            name: true,
            abbreviation: true,
          },
        },
      },
    });
  }

  public async findAllNotAssigned(orgId: string, isWing: boolean = false /** or isBase */) {
    return this.prisma.role.findMany({
      where: {
        AND: {
          orgRoles: { none: {organizationId: orgId} },
          level: isWing ? undefined : 'UNIT',
          abbreviation: { notIn: ['USER', 'ADMIN', 'SUPERADMIN'] },
        },
      },
      include: {
        orgRoles: true,
      },
    });
  }

  public get(id: string): Promise<Role> {
    return this.prisma.role.findFirstOrThrow({
      where: { uuid: id },
    });
  }

  public create(data: RoleDto): Promise<string> {
    throw new Error('Method not implemented.');
  }

  public update(id: string, data: RoleDto): Promise<void> {
    throw new Error('Method not implemented.');
  }

  public async delete(id: string) {
    await this.prisma.role.delete({
      where: { uuid: id },
    });
  }
}
