import { Dialog, DialogPanel, DialogTitle, Transition, TransitionChild } from '@headlessui/react';
import { useNavigate, useSearchParams } from '@remix-run/react';
import type { RefProp } from 'node_modules/@headlessui/react/dist/utils/render';
import type { Ref } from 'react';
import React, { forwardRef } from 'react';
import { ModalCancelBtn } from '../Buttons';

type ModalFnProps = {
  open?: boolean;
  onClose?: () => void;
} & React.HTMLAttributes<HTMLDivElement>;

function ModalFn(
  { children, open=true, onClose }: ModalFnProps,
  ref: React.ForwardedRef<HTMLDivElement>
) {
  const navigate = useNavigate();
  const handleOnClose = () => {
    if (onClose) {
      onClose();
    }
    else {
      navigate(-1);
    }
  };
  
  return (
    <Transition show={true} as={React.Fragment}>
      <Dialog
        as="div"
        className="relative z-10"
        open={open}
        onClose={handleOnClose}
        ref={ref}>
        {/* <TransitionChild
          as={React.Fragment}
          enter="ease-in duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          
        </TransitionChild> */}
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75" />
        <div className="fixed inset-0 z-10 w-screen overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            {/* <TransitionChild
              as={React.Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95">
              
            </TransitionChild> */}
            <DialogPanel className="relative transform rounded-lg bg-white text-left shadow-xl sm:my-8 sm:w-full sm:max-w-lg">
              {children}
            </DialogPanel>
          </div>
        </div>
      </Dialog>
    </Transition>
  );
}

type DialogBodyFnProps = React.HTMLAttributes<HTMLDivElement> & {
  children: React.ReactNode;
}

type DialogBodyFnRef = Ref<HTMLDivElement>

const DialogBodyFn = (props: DialogBodyFnProps, ref: DialogBodyFnRef) => (
  <div ref={ref} className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
    {props.children}
  </div>
);

type DialogTitleFnProps = RefProp<typeof DialogTitle> & {
  className?: string;
  children: React.ReactNode;
};

type DialogTitleFnRef = Ref<HTMLHeadingElement>;

const DialogTitleFn = ({ className: userStyles, ...props }: DialogTitleFnProps, ref: DialogTitleFnRef) => {
  // const { className, ...props } = props;
  let styles = 'text-base rounded-t-lg font-semibold leading-6 text-gray-900 mb-3';
  if (userStyles) {
    styles = userStyles + ' ' + styles;
  }
  
  return (
    <DialogTitle
      {...props}
      ref={ref}
      as="h3"
      className={styles}>
      {props.children}
    </DialogTitle>
  );
};

type ButtonsFnProps = {
  hideCancelBtn?: boolean;
  navigateToUrl?: string | number;
  variant?: 'solid' | 'outline';
} & React.ButtonHTMLAttributes<HTMLButtonElement>;

type ButtonsFnRef = React.ForwardedRef<HTMLButtonElement>;


const ButtonsFn = (allProps: ButtonsFnProps, ref: ButtonsFnRef) => {
  let { hideCancelBtn, navigateToUrl, type, variant = 'solid', ...props } = allProps;
  if (!navigateToUrl) navigateToUrl = -1;
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();


  function performNavigate() {
    if(typeof navigateToUrl === 'string') {
      searchParams.delete('type');
      navigate({
        pathname: navigateToUrl,
        search: searchParams.toString(),
      });
    } else {
      navigate(navigateToUrl ?? -1);
    }
  }

  function handleModalCancel() {
    performNavigate();
  }

  function handleClick() {
    if (type === 'button') {
      performNavigate();
    }
  }

  const baseStyles = 'inline-flex w-full justify-center rounded-md px-3 py-2 text-sm font-semibold shadow-sm disabled:cursor-not-allowed sm:ml-3 sm:w-auto';
  let solidClasses = 'bg-green-600 hover:bg-green-500 disabled:bg-green-400 text-white';
  let outlineClasses = 'text-green-600 hover:text-white border border-green-600 hover:bg-green-500 disabled:bg-neutral-400';
  let classes = '';

  if (props.value === 'delete') {
    solidClasses = 'bg-red-600 text-white hover:bg-red-500 disabled:bg-red-400';
    outlineClasses = 'outline-red-600 hover:bg-red-500';
  }

  if (variant === 'outline') {
    classes = baseStyles + ' ' + outlineClasses;
  } else if (variant === 'solid') {
    classes = baseStyles + ' ' + solidClasses;
  }

  return (
    <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
      <button
        {...props}
        ref={ref}
        type={type}
        onClick={handleClick}
        name={props.name ?? '_action'}
        className={classes}/>
      <Transition show={!hideCancelBtn}>
        <ModalCancelBtn onClick={handleModalCancel}>Cancel</ModalCancelBtn>
      </Transition>
    </div>
  );
};

export let Modal = Object.assign(forwardRef(ModalFn), {
  Title: forwardRef(DialogTitleFn),
  Body: forwardRef(DialogBodyFn),
  Buttons: forwardRef(ButtonsFn),
});
