import { DeleteButton, LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { RichTextView } from '@components/ui/RichText/Viewer';
import type { ChecklistType } from '@lib/constants';
import { Attribute, isPrismaKnownError } from '@lib/utils';
import { Prisma } from '@prisma/client';
import type { ActionFunction, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Outlet, useFetcher, useLoaderData, useParams } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React, { useEffect, useState } from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import {
  deleteTemplateItem,
  getInheritedChecklistItems,
  getTemplateWithItems,
} from './.server/template.queries';
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/outline';
import { ChevronDownIcon, ChevronUpIcon } from '@heroicons/react/24/solid';
import { getOrgBeingViewed } from './.server/org.queries';
import useMatchesData, { useManageLayoutData } from '@lib/hooks/useMatchesData';

// TODO: Errors in this loader should throw not be added to an array
export const loader = async ({ params, request }: LoaderFunctionArgs) => {
  const { currentOrg: org } = await requireUserAsObject(request, {
    permissions: Attribute.TEMPLATES_READWRITE,
  });
  const errors: { message: string }[] = [];
  const { templateId } = params;
  if (!templateId) throw new Error('templateId is required');

  
  const currentSelectedTemplate = await getTemplateWithItems(templateId);

  currentSelectedTemplate.items.sort((a, b) => {
    if (a.reference === null && b.reference !== null) {
      return 1;
    } else if (a.reference !== null && b.reference === null) {
      return -1;
    } else {
      return 0;
    }
  });

  let inheritedItems;
  if (org.id && currentSelectedTemplate?.type !== undefined) {
    inheritedItems = await getInheritedChecklistItems(
      currentSelectedTemplate.organizationId,
      currentSelectedTemplate?.type as ChecklistType
    );
  } else {
    errors.push({ message: 'No org id specified' });
  }

  inheritedItems?.reverse();
  return json({ template: currentSelectedTemplate, inheritedItems, errors });
};

export const action: ActionFunction = async ({ request }) => {
  const deleteItemSchema = z.object({
    id: z.string(),
    _action: z.string().regex(/delete/),
  });
  try {
    await requireUser(request, {
      permissions: Attribute.TEMPLATES_READWRITE,
      options: {
        redirectOnFailure: false,
      },
    });

    const { id } = deleteItemSchema.parse(
      Object.fromEntries(await request.formData())
    );

    await deleteTemplateItem(id);
    return jsonWithSuccess({ status: 'success' }, 'Item Deleted!');
  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          {
            status: 'error',
            message: 'There was a validation error',
            issues: e.issues,
          },
          'There was an error.'
        );
      } else if (
        e instanceof Prisma.PrismaClientKnownRequestError &&
        isPrismaKnownError(e)
      ) {
        if (e.code === 'P2003')
          return jsonWithError(
            {
              status: 'error',
              message:
                'There are still items linked to this template item. Delete the items first before deleting the template.',
            },
            'There are still items linked to the template.'
          );
      }
      return jsonWithError(
        { status: 'error', message: e.message },
        'There was an error.'
      );
    }
  }
};

const ITEMS_PER_PAGE = 10;

export default function TemplateDetailsView() {
  const { templateId: checklistId } = useParams();
  const { template, inheritedItems, errors } = useLoaderData<typeof loader>();
  const deleteFetcher = useFetcher();
  const { orgBeingViewed } = useManageLayoutData();

  const [isExpanded, setIsExpanded] = useState(false);

  const [currentPage, setCurrentPage] = useState(1);
  const totalPages = Math.ceil(template?.items.length / ITEMS_PER_PAGE);
  const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
  const endIndex = startIndex + ITEMS_PER_PAGE;
  const itemsToShow = template?.items.slice(startIndex, endIndex);

  useEffect(() => {
    if (itemsToShow.length === 0 && currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  }, [itemsToShow.length, currentPage]);

  const goToPrevPage = () => {
    setCurrentPage(currentPage - 1);
  };
  const goToNextPage = () => {
    setCurrentPage(currentPage + 1);
  };

  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <>
      <LinkButton className="my-5" variant="gray" to={`/template`}>
        Back
      </LinkButton>
      <div className="flex border-b border-gray-200 pb-5 my-5 items-center justify-between">
        <div>
          <div className="flex items-baseline">
            {/* <h1 className="text-2xl font-bold">Template</h1> */}
            {/* <p className="ml-1.5">for {template.name}</p> */}
            <h1 className="text-2xl font-bold">{template.name}</h1>
            <p className="ml-1.5 text-2xl font-bold">Template</p>
          </div>
          <div className="max-h-[10.75rem] overflow-y-auto">
            <RichTextView>{template.description}</RichTextView>
          </div>
        </div>
        <div>
          <LinkButton className="mx-3" variant="green" to={`item/create`}>
            Create New Item
          </LinkButton>
          <LinkButton to={`/template/${checklistId}/edit`}>Edit</LinkButton>
        </div>
      </div>

      <section className="">
        {template?.items.length > 0 ? (
          <>
            <table className="text-left w-[1fr] rounded-t-lg shadow-lg border-collapse">
              <thead>
                <tr className="border-b border-zinc-600 text-zinc-800 uppercase bg-zinc-100 dark:bg-zinc-700 dark:text-zinc-400">
                  <th className="w-72 px-6 py-3 rounded-tl-lg">Name</th>
                  <th className="py-3">Description</th>
                  <th className="text-center px-2">Dorm</th>
                  <th className="text-center px-2">EFMP</th>
                  <th className="text-center px-2">Dependents</th>
                  <th></th>
                  <th className="rounded-tr-lg"></th>
                </tr>
              </thead>
              <tbody>
                {itemsToShow.map((item) => (
                  <tr
                    key={item.id}
                    className="table-row border-b even:bg-zinc-50">
                    <td className="py-4 px-6 align-middle line-clamp-2">
                      <div className="flex items-center">
                        <p className="line-clamp-2">
                          {item.reference ? item.reference.name : item.name}
                        </p>
                      </div>
                    </td>
                    <td className="pr-5">
                      <div>
                        <RichTextView className="line-clamp-2 w-[35vw]">
                          {item.description === ''
                            ? item.reference?.description
                            : item.description}
                        </RichTextView>
                      </div>
                    </td>
                    <td className="text-center border-x border-zinc-400">
                      {item.forDormResidents ? '\u2714' : '\u2716'}
                    </td>
                    <td className="text-center border-x border-zinc-400">
                      {item.forHasEFMP ? '\u2714' : '\u2716'}
                    </td>
                    <td className="text-center border-x border-zinc-400">
                      {item.forHasDependents ? '\u2714' : '\u2716'}
                    </td>
                    <td className="text-right pl-5">
                      <LinkButton
                        to={`/template/${checklistId}/item/${item.uuid}/edit`}
                        variant="gray">
                        Edit
                      </LinkButton>
                    </td>
                    <td className="pr-5">
                      {!item.referenceId && (
                        <deleteFetcher.Form
                          method="post"
                          className="inline ml-3">
                          <input type="hidden" name="id" value={item.uuid} />
                          <DeleteButton
                            name="_action"
                            value="delete"
                            type="submit">
                            Delete
                          </DeleteButton>
                        </deleteFetcher.Form>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            {/* If you want to edit the total items shown per page ctrl + f "ITEMS_PER_PAGE"*/}
            <div className="flex justify-center mt-5">
              <nav aria-label="Pagination">
                <ul className="flex space-x-2">
                  {currentPage > 1 && (
                    <li className="flex">
                      <button
                        onClick={goToPrevPage}
                        disabled={currentPage === 1}
                        className={`inline-flex items-center space-x-2 rounded-full border border-gray-300 bg-white p-3 font-medium text-black hover:bg-gray-50 ${
                          currentPage === 1 ? 'cursor-not-allowed' : ''
                        }`}>
                        <ChevronLeftIcon className="stroke-black w-5 h-5" />
                      </button>
                    </li>
                  )}
                  <li>
                    <span className="inline-flex items-center space-x-1 rounded-md bg-white px-4 py-2 text-black text-lg">
                      Page <b className="mx-1">{currentPage}</b> of{' '}
                      <b className="ml-1">{totalPages}</b>
                    </span>
                  </li>
                  {currentPage < totalPages && (
                    <li className="flex">
                      <button
                        onClick={goToNextPage}
                        disabled={currentPage === totalPages}
                        className={`inline-flex items-center space-x-2 rounded-full border border-gray-300 bg-white p-3 font-medium text-black hover:bg-gray-50 ${
                          currentPage === totalPages ? 'cursor-not-allowed' : ''
                        }`}>
                        <ChevronRightIcon className="stroke-black w-5 h-5" />
                      </button>
                    </li>
                  )}
                </ul>
              </nav>
            </div>
          </>
        ) : (
          'No items to view. Please use the Create button to add tasks.'
        )}
      </section>
      {inheritedItems && inheritedItems.length > 0 && (
        <>
          <hr className="my-8 h-px border-0 bg-gray-300" />
          <section className="border mt-5 rounded">
            <div
              className="flex items-center justify-between text-xl font-bold py-1 px-4 hover:cursor-pointer hover:bg-zinc-300 w-full bg-zinc-100 rounded shadow"
              onClick={toggleExpand}>
              <h1>Inherited Items</h1>
              {isExpanded ? (
                <ChevronUpIcon className="size-8" />
              ) : (
                <ChevronDownIcon className="size-8" />
              )}
            </div>
            {isExpanded && (
              <ul>
                {inheritedItems.map(({ name, items, owningOrg }, index) => {
                  return (
                    <li
                      key={`${name}-${index}`}
                      className="mx-3 my-4 first:mt-0">
                      <h3 className="text-lg font-bold py-3">{name}</h3>
                      {items.length > 0 ? (
                        <div className="w-full border border-zinc-300 py-2 rounded-lg shadow-lg text-sm text-left rtl:text-right text-gray-500 dark:text-gray-400">
                          {items.map((item) => (
                            <div
                              className="flex gap-4 items-center w-full justify-between pt-3 even:bg-slate-100 pl-5 border-b last:border-b-0 border-b-1"
                              key={item.uuid}>
                              <div className="w-full">
                                <h4 className="font-medium text-gray-900 whitespace-nowrap dark:text-white">
                                  {item.reference
                                    ? item.reference.name
                                    : item.name}
                                </h4>
                                <div className="line-clamp-2 my-4 pr-4 w-full">
                                  <RichTextView>
                                    {item.description === ''
                                      ? item.reference?.description
                                      : item.description}
                                  </RichTextView>
                                </div>
                              </div>
                              <div className="pr-3 flex-none">
                                {/* <button className="border border-2 ">Add to template</button> */}
                                {/* <LinkButton
                                  className="mx-3"
                                  variant="green"
                                  to="item/create">
                                  Move to Template
                                </LinkButton> */}
                              </div>
                            </div>
                          ))}
                        </div>
                      ) : (
                        'No items to inherit.'
                      )}
                    </li>
                  );
                })}
              </ul>
            )}
          </section>
        </>
      )}

      <Outlet />
    </>
  );
}
export const ErrorBoundary = ErrorReport;
