/* eslint-disable import/prefer-default-export */
import type { ChecklistType, TemplateItem } from '@prisma/client';
import type { TemplateItemDto, TemplateItemUpdateDto } from './dtos';
import { type IRepository, Repository } from './Repository.server';
import { Models } from '@lib/models.server';

interface ITemplateItemRepository
  extends IRepository<TemplateItem, TemplateItemDto, TemplateItemUpdateDto> {
  getWithRole(id: string): Promise<any>;
}

export class TemplateItemRepository
  extends Repository<TemplateItem, TemplateItemDto, TemplateItemUpdateDto>
  implements ITemplateItemRepository
{
  public getAll(): Promise<TemplateItem[]> {
    throw new Error('Method not implemented.');
  }

  public get(id: string): Promise<TemplateItem> {
    throw new Error('Method not implemented.');
  }

  public async getWithRole(id: string) {
    return this.prisma.templateItem.findFirstOrThrow({
      where: { uuid: id },
      include: {
        reference: true,
        requiredRole: true,
      },
    });
  }

  public async create({
    templateId,
    requiredRoleId,
    name,
    description = '',
    forHasDependents,
    forHasEFMP,
    forDormResidents,
  }: TemplateItemDto): Promise<string> {

    const template = await this.prisma.template.findFirstOrThrow({
      where: {
        id: templateId,
      },
      include: {
        owningOrganization: true,
        userChecklists: true,
      },
    });

    if (template.owningOrganization.uuid === 'DAF') {
      const item = await this.prisma.templateItem.create({
        data: {
          name,
          description,
          forHasDependents,
          forHasEFMP,
          forDormResidents,
          isTopLevel: true,
          template: { connect: { id: templateId } },
          requiredRole: { connect: { uuid: requiredRoleId } },
        },
        select: {
          uuid: true,
        },
      });

      const orgFlatArray = await Models.org.findDescendants(template.owningOrganization.uuid, false, template.type as ChecklistType);

      const createInheritedItem = [];

      for (const org of orgFlatArray.filter(org => org.isWingLevel)) {
        if (org.templates && org.templates.length > 0) {
          createInheritedItem.push( this.prisma.template.update( {
            where: { id: org.templates[0].id },
            data: {
              items: {
                create: {
                  name: `Inherited from ${ name } (Level: ${ org.name })`,
                  forHasDependents: forHasDependents,
                  forHasEFMP: forHasEFMP,
                  forDormResidents: forDormResidents,
                  reference: { connect: { uuid: item.uuid } },
                  requiredRole: { connect: { uuid: requiredRoleId } },
                },
              },
            },
          } ) );
        }
      }
      await Promise.all(createInheritedItem);
      return item.uuid;
    }

    const templateItemCreate = await this.prisma.templateItem.create({
      data: {
        name,
        description,
        forHasDependents,
        forHasEFMP,
        forDormResidents,
        template: { connect: { id: templateId } },
        requiredRole: { connect: { uuid: requiredRoleId } },
      },
      select: {
        uuid: true,
      },
    });

    // for await (const check of template.userChecklists) {
    //   await this.prisma.checklistItem.create({
    //     data: {
    //       updatedAt: new Date(Date.now()),
    //       templateItemId: templateItemCreate.uuid,
    //       checklistId: check.id,
    //     },
    //   });
    // }

    const templateArray = [template.id, ...template.childTemplates];

    for await (const templateId of templateArray) {
      const templ = await this.prisma.template.findFirstOrThrow({
        where: {
          id: templateId,
        },
        include: {
          userChecklists: true,
        },
      });

      for await (const check of templ.userChecklists) {
        await this.prisma.checklistItem.create({
          data: {
            updatedAt: new Date(Date.now()),
            templateItemId: templateItemCreate.uuid,
            checklistId: check.id,
          },
        });
      }
    }

    // for await (const check of template.userChecklists) {
    //   await this.prisma.checklistItem.deleteMany({
    //     where: {
    //       templateItemId: templateItemCreate.uuid,
    //     },
    //   });
    // }

    return templateItemCreate.uuid;
  }

  public async update(
    id: string,
    updateDto: TemplateItemUpdateDto
  ): Promise<void> {
    const data = this.buildUpdateData(updateDto);

    await this.prisma.templateItem.update({
      where: { uuid: id },
      data,
    });
  }

  public async delete(id: string): Promise<void> {
    await this.prisma.templateItem.delete({ where: { uuid: id } });
  }

  public async getAllTemplateItems(): Promise<TemplateItem[]> {
    return this.prisma.templateItem.findMany();
  }
}
