import { DeleteButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import {
  ExclamationTriangleIcon,
} from '@heroicons/react/24/outline';
import { Attribute } from '@lib/utils';
import { handleActionError } from '@lib/utils/utils.server';
import {
  type ActionFunctionArgs,
  json,
  type LoaderFunctionArgs,
} from '@remix-run/node';
import { Form, useLoaderData, useNavigate } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import { z } from 'zod';
import { deleteAccount, getUser } from './.server/user.queries';
import { Dialog, DialogPanel } from '@headlessui/react';
import React from 'react';

export async function loader({ request }: LoaderFunctionArgs) {
  await requireUser(request, {
    permissions: Attribute.MEMBERS_READWRITEDELETE,
  });

  const searchParams = new URL(request.url).searchParams;
  const userId = searchParams.get('userId');

  console.log(searchParams, userId);

  if (!userId) throw new Error('userId is required');

  const user = await getUser(userId);

  return json({ user });
}

export async function action({ request }: ActionFunctionArgs) {
  const orgParam = new URL(request.url).searchParams.get('org');
  const updateAccountStatus = z.object({
    userId: z.string(),
    _action: z.literal('delete'),
  });
  try {
    await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.MEMBERS_READWRITEDELETE,
    });

    const { userId } = updateAccountStatus.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    await deleteAccount(userId);

    return redirectWithSuccess(
      `/user/onboard${orgParam ? `?org=${orgParam}` : ''}`,
      'Account deleted!'
    );
  } catch (e) {
    return handleActionError(e, 'Account could not be deleted');
  }
}

export default function DeleteAccountPage() {
  const { user } = useLoaderData<typeof loader>();
  const navigate = useNavigate();

  return (
    <Dialog as="div" className="relative z-10" open={true} onClose={() => { navigate(-1); }}>
      <div className="fixed inset-0 bg-gray-500 bg-opacity-75" />
      <div className="fixed inset-0 z-10 w-screen overflow-y-auto">
        <div className="flex rounded-xl min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
          <DialogPanel className="relative transform rounded-lg bg-white rounded-t-xl text-left shadow-xl sm:my-8 sm:w-full sm:max-w-2xl">
            <Form method="post">
              <input type="hidden" name="userId" value={user.id} />
              <div className="relative bg-white rounded-t-xl p-2 pb-4">
                <div className="flex flex-col items-center justify-center mt-8">
                  <ExclamationTriangleIcon className="w-10 h-10 stroke-red-600" />
                  <h1 className="text-red-600 text-2xl">
                      Delete Account
                  </h1>
                </div>
              </div>
              <div className="bg-gray-200 pb-3 rounded-b-xl">
                <div className="flex flex-col justify-center items-center p-5 gap-3">
                  <h2 className="mb-2 text-md font-semibold text-gray-900 dark:text-white">
                      Are you sure you want to delete{' '}
                    <span className="bg-gray-400/60 px-2 py-1 rounded-lg text-lg">
                      {user.name}'s
                    </span>{' '}
                      account?
                  </h2>
                  <p>Please enter the word <span className="bg-gray-400/60 px-2 py-0.5 rounded-lg">delete</span> to confirm.</p>
                  <input type="text" required pattern="delete" className="block text-sm invalid:border-red-300 valid:border-green-500 rounded-md border-gray-300 shadow-sm focus:border-primary-400 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50 disabled:text-gray-500" />
                </div>
                <div className="flex items-center justify-center p-2">
                  <DeleteButton
                    type="submit"
                    name="_action"
                    value="delete"
                    className="rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 sm:ml-3 sm:w-auto">
                      Delete Account
                  </DeleteButton>
                  <button type="button" onClick={() => { navigate(-1); }} className="rounded-md bg-white px-3 py-2 text-sm font-semibold text-zinc-800 shadow-sm hover:bg-neutral-300 sm:ml-3 sm:w-auto">Cancel</button>
                </div>
              </div>
            </Form>
          </DialogPanel>
        </div>
      </div>
    </Dialog>
  );
}

export const ErrorBoundary = ErrorReport;
