import { LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { Attribute } from '@lib/utils';
import { isAdmin, isSuperAdmin } from '@lib/utils/utils.server';
import { json, type LoaderFunctionArgs } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import { requireUserAsObject } from '@services/user.server';
import { getOrgBeingViewed } from './.server/org.queries';
import { getOrgRolesByOrg } from './.server/role.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  const { currentOrg: org, roles: userRoles } = await requireUserAsObject(
    request,
    { permissions: Attribute.ORGS_READ }
  );

  const { orgBeingViewed } = await getOrgBeingViewed({
    defaultOrgId: org.uuid,
    params,
    searchParams: new URL(request.url).searchParams,
  });

  const includeSuperAdmin = isSuperAdmin(userRoles);
  const includeAdmin = isAdmin(userRoles) || includeSuperAdmin;

  const roles = await getOrgRolesByOrg(orgBeingViewed.uuid, { includeAdmin, includeSuperAdmin });

  return json({
    orgBeingViewed,
    roles,
  }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
};

const ManageRoleIndex = () => {
  const { roles, orgBeingViewed } = useLoaderData<typeof loader>();

  return (
    <>
      <div>
        <PageHeader org={orgBeingViewed} pageTitle="Role Management" />
        <LinkButton
          variant="green"
          to={`/role/create`}>
          Add Organizational Role
        </LinkButton>
        <div className="mt-5">
          {roles.length > 0 && (
            <table className="text-left table-auto w-full border-neutral-300 border-1">
              <thead>
                <tr className="border-b border-black">
                  <th className="p-3">Role:</th>
                  <th>Email:</th>
                  <th>Phone:</th>
                  <th className="text-center">Assigned Users:</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {roles
                  .sort((a, b) => a.role.name.localeCompare(b.role.name))
                  .map((orgRole) => {
                    const isAdmin =
                      orgRole.role.abbreviation === 'SUPERADMIN' ||
                      orgRole.role.abbreviation === 'ADMIN';

                    return (
                      <tr
                        key={orgRole.uuid}
                        className="table-row border-b even:bg-zinc-50">
                        <td className="p-3">{orgRole.role.name}</td>
                        <td>{!isAdmin && orgRole.contactEmail}</td>
                        <td>{!isAdmin && orgRole.contactPhone}</td>
                        <td className="text-center">{orgRole._count.users}</td>
                        <td className="text-right pr-5 gap-x-3 space-x-2">
                          <LinkButton
                            to={`/role/${orgRole.uuid}/users`}>
                            Users
                          </LinkButton>
                          {!isAdmin && (
                            <>
                              <LinkButton
                                variant="gray"
                                to={`/role/${orgRole.uuid}/edit`}>
                                Edit
                              </LinkButton>
                              <LinkButton
                                variant="red"
                                to={`/role/${orgRole.uuid}/delete`}>
                                Delete
                              </LinkButton>
                            </>
                          )}
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          )}
          {roles.length === 0 && <p>No roles found.</p>}
        </div>
      </div>
      <Outlet context={{orgBeingViewed}}/>
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default ManageRoleIndex;
