import { DeleteButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { Attribute, isPrismaKnownError } from '@lib/utils';
import type { Role } from '@prisma/client';
import { RoleLevel } from '@prisma/client';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useFetcher, useLoaderData } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { deleteCustomRole, getCustomRoles } from './.server/role.queries';

export async function loader({ request }: LoaderFunctionArgs) {
  await requireUser(request, { permissions: Attribute.ROLES_READWRITE });
  const customRoles = await getCustomRoles();

  return json({ customRoles });
}

export async function action({ request }: ActionFunctionArgs) {
  const deleteSchema = z.object({
    roleId: z.string(),
    _action: z.literal('delete'),
  });

  try {
    await requireUser(request, { permissions: Attribute.ROLES_READWRITE });
    const { roleId, _action } = deleteSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    if (_action === 'delete') {
      await deleteCustomRole(roleId);
      return jsonWithSuccess({ status: 'success' }, 'Role Deleted!');
    } else {
      throw new Error('Action not supported.');
    }
  } catch (e) {
    console.error(e);
    if (e instanceof AuthorizationError) {
      return jsonWithError(
        { status: 'error', message: 'User is not authorized.' },
        'User is not authorized.'
      );
    }
    if (isPrismaKnownError(e)) {
      if (e.code === 'P2003')
        return jsonWithError(
          {
            status: 'error',
            message: 'There are still organizational roles linked.',
          },
          'There are still organizational roles linked.'
        );
    } else if (e instanceof ZodError) {
      return jsonWithError(
        {
          status: 'error',
          message: 'There was a validation error.',
          issues: e.issues,
        },
        'There was an error.'
      );
    } else if (e instanceof Error) {
      return jsonWithError(
        { status: 'error', message: 'There was an error.' },
        'There was an error.'
      );
    }
  }
}
const ManageRoles = () => {
  const { customRoles } = useLoaderData<typeof loader>();
  const deleteFetcher = useFetcher();
  return (
    <>
      <h1 className="text-2xl mb-6">Manage Roles</h1>
      <div>
        {customRoles.length > 0 ? (
          <table className="text-left table-auto w-full border-neutral-300 border-1">
            <thead>
              <tr className="border-b border-black">
                <th className="text-left pl-3">Name</th>
                <th>Abbreviation</th>
                <th>Description</th>
                <th>Level</th>
                <th>Created By Organization</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {customRoles.map((role: Role) => (
                <tr key={role.uuid} className="table-row border-b">
                  <td className="pl-3 py-4">{role.name}</td>
                  <td>{role.abbreviation}</td>
                  <td>{role.description}</td>
                  <td>
                    {role.level === RoleLevel.BASE && 'Base'}
                    {role.level === RoleLevel.UNIT && 'Unit'}
                  </td>
                  <td>{role.createdByOrg.name}</td>
                  <td className="text-right pr-5">
                    <deleteFetcher.Form method="POST">
                      <input type="hidden" name="roleId" value={role.uuid} />
                      <DeleteButton type="submit" value="delete" name="_action">
                        Delete
                      </DeleteButton>
                    </deleteFetcher.Form>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          'No Custom Roles to edit.'
        )}
      </div>
    </>
  );
};
export const ErrorBoundary = ErrorReport;
export default ManageRoles;
