import type {
  OrgRoleWithChecklistItems,
  OrgRoleWithInfo,
  OrgRoleWithUsers,
} from '@lib/constants';
import type {
  OrgRoleWithCount,
  RoleWithItems,
  TaskTabChecklistItem,
  UsedByChecklistItems,
} from '@lib/types';
import {
  type OrganizationsRoles,
  type PrismaClient,
} from '@prisma/client';
import type {
  OrganizationRoleCreateDto,
  OrganizationRoleUpdateDto,
  OrganizationRoleWithRoleDto,
} from './dtos';
import type { IRepository } from './Repository.server';

export type FindAllOptions = {
  includeUser?: boolean;
  includeAdmin?: boolean;
  includeSuperAdmin?: boolean;
};

interface IOrganizationRoleRepository
  extends IRepository<
    OrganizationsRoles,
    OrganizationRoleCreateDto,
    OrganizationRoleUpdateDto
  > {
  createWithNewRole({
    roleAbbreviation,
    roleName,
    roleDescription,
    organizationId,
    contactEmail,
    contactPhone,
  }: OrganizationRoleWithRoleDto): Promise<string>;

  getWithUsers(id: string): Promise<OrgRoleWithUsers>;

  getTaskItems(id: string): Promise<void>;

  findAllByOwningOrgId(
    orgId: string,
    options?: FindAllOptions,
  ): Promise<OrgRoleWithCount[]>;

  addUser({
    orgRoleId,
    userId,
  }: {
    orgRoleId: string;
    userId: string;
  }): Promise<void>;

  removeUser({
    orgRoleId,
    userId,
  }: {
    orgRoleId: string;
    userId: string;
  }): Promise<void>;

  getTemplateItemForChildOrgs(
    orgId: string,
    where: any,
  ): Promise<OrgRoleWithChecklistItems[]>;

  getWithChecklists(
    byOrgOrUser: 'org' | 'user',
    id: string,
    orgId: string,
  ): Promise<Map<string, RoleWithItems>>;

  getByUserIdWithChecklists(userId: string, orgId: string): Promise<any>;

  getByOrgIdWithChecklists(orgId: string): Promise<any>;

  getChildOrgRoles(
    orgId: string,
    userRoles?: Array<string>,
  ): Promise<OrgRoleWithInfo[]>;
}

