  public async get(uuid: string): Promise<OrgRoleWithInfo> {
    return this.prisma.organizationsRoles.findFirstOrThrow({
      where: { uuid },
      include: {
        role: {
          select: {
            uuid: true,
            name: true,
            description: true,
            permissions: true,
            abbreviation: true,
            id: true,
          },
        },
      },
    });
  }

  public async getAllOrgRoles(): Promise<OrganizationsRoles[]> {
    return this.prisma.organizationsRoles.findMany();
  }

  public async create({
    organizationId,
    roleId,
    contactEmail,
    contactPhone,
  }: OrganizationRoleCreateDto): Promise<string> {
    return (
      await this.prisma.organizationsRoles.create({
        data: {
          contactPhone,
          contactEmail,
          organizationId,
          roleId,
        },
        select: {
          uuid: true,
        },
      })
    ).uuid;
  }

  public async createWithNewRole({
    roleAbbreviation,
    roleName,
    baseAgency,
    roleDescription,
    organizationId,
    contactEmail,
    contactPhone,
  }: OrganizationRoleWithRoleDto): Promise<string> {
    return (
      await this.prisma.organizationsRoles.create({
        data: {
          isBaseAgency: baseAgency ?? false,
          contactPhone,
          contactEmail,
          organization: {
            connect: {
              uuid: organizationId,
            },
          },
          role: {
            create: {
              name: roleName,
              level: baseAgency ? 'BASE' : 'UNIT',
              description: roleDescription,
              abbreviation: roleAbbreviation,
              permissions: btoa('[["tasks", ["READ", "WRITE"]]]'),
              createdByOrgId: organizationId,
            },
          },
        },
        select: {
          uuid: true,
        },
      })
    ).uuid;
  }

