  /**
   * Find all Organization Roles belonging to the specified Organization ID. Includes options for including the default USER role.
   * By using an object like { includeUser: true }
   */
  public async findAllByOwningOrgId(
    orgId: string,
    options?: FindAllOptions
  ): Promise<OrgRoleWithCount[]> {
    const notInArray = [];
    if (!options?.includeUser) notInArray.push('USER');
    if (!options?.includeAdmin) notInArray.push('ADMIN');
    if (!options?.includeSuperAdmin) notInArray.push('SUPERADMIN');

    return this.prisma.organizationsRoles.findMany({
      where: {
        OR: [
          {
            organizationId: orgId,
          },
          {
            role: {
              abbreviation: options?.includeUser ? 'USER' : undefined,
            },
          },
        ],
        role: {
          abbreviation: {
            notIn: notInArray,
          },
        },
      },
      include: {
        role: true,
        _count: {
          select: {
            users: true,
          },
        },
      },
    });
  }
  
