import { Breadcrumbs } from '@components/OrgBreadcrumbs';
import ErrorReport from '@components/ui/ErrorReport';
import { InputRow, TextInput, TextLabel, Input } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import usePermissions from '@lib/hooks/usePermissions';
import { Attribute, RolePermissions } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Form, useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React, { useEffect, useState } from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z from 'zod';
import { createOrg } from './.server/org.queries';
import { useRootLayoutData } from '@lib/hooks/useMatchesData';
import { Models } from '@lib/models.server';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  await requireUser(request, {
    permissions: Attribute.ORGS_READWRITE,
  });

  const searchQuery = new URL(request.url).searchParams;

  let orgParam = searchQuery.get('org');

  const ancestors = await Models.org.findAncestors(orgParam ?? '');
  const ancestorStrings = ancestors.map((org) => org.uuid);
  return json({ ancestors: ancestorStrings });
};

export async function action({ request }: ActionFunctionArgs) {
  try {
    const orgCreateSchema = z.object({
      orgName: z.string().min(3).max(80),
      orgId: z.string().optional(),
      baseId: z.string().optional(),
      isWing: z.preprocess((value) => value === 'on', z.boolean()),
      abbreviation: z.string(),
      isOrg: z.preprocess((value) => value === 'on', z.boolean()),
      isBase: z.preprocess((value) => value === 'on', z.boolean()),
    });

    const {
      orgId: parentOrgId,
      orgName: name,
      abbreviation,
      baseId,
      isWing,
      isBase,
    } = orgCreateSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );
    let userId;
    if (isBase) {
      // require Attributes.BASES_READWRITE
      ({ id: userId } = await requireUserAsObject(request, {
        permissions: Attribute.BASES_READWRITE,
        options: { redirectOnFailure: false },
      }));
    } else {
      ({ id: userId } = await requireUserAsObject(request, {
        permissions: Attribute.ORGS_READWRITE,
        options: { redirectOnFailure: false },
      }));
    }

    if (!isBase && parentOrgId === 'DAF')
      return jsonWithError(
        { status: 'error', error: 'organization location required' },
        'Organization can not be at base level.'
      );

    if (!isWing && parentOrgId === 'default')
      return jsonWithError(
        { status: 'error', error: 'parent org is required' },
        'There was error creating the organization.'
      );

    if (!userId)
      return jsonWithError(
        { status: 'error', error: 'user required' },
        'There was error creating the organization.'
      );

    await createOrg({
      userId,
      parentOrgId,
      name,
      baseId: baseId,
      abbreviation,
      isWing,
      isBase,
      createdAt: new Date(Date.now()),
      createdBy: userId,
      updatedAt: new Date(Date.now()),
      updatedBy: userId,
    });
  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', message: 'Authorization Error' },
          'Authorization Error.'
        );
      }

      return jsonWithError(
        { status: 'error', message: 'There was an error.' },
        'There was an error.'
      );
    }
  }
  return redirectWithSuccess('/org', 'Organization Created Successfully!');
}

type AdminRowProps = {
  baseIsChecked: boolean;
  setBaseIsChecked: Function;
  orgChecked: boolean;
  userPermissions: any;
  ancestors: string[];
  parentNode: any;
};

function AdminRow({
  baseIsChecked,
  setBaseIsChecked,
  userPermissions,
  ancestors,
  parentNode,
}: AdminRowProps) {
  const checkPermissions = usePermissions();
  const [orgIsChecked, setOrgIsChecked] = useState(false);
  const [newParent, setNewParent] = useState(parentNode.uuid);

  useEffect(() => {
    setOrgIsChecked(!checkPermissions(userPermissions, RolePermissions.ADMIN));
  }, [userPermissions, checkPermissions]);

  return (
    <div className="">
      {checkPermissions(userPermissions, RolePermissions.ADMIN) && (
        <InputRow className="inline-flex w-full justify-around px-10">
          <Input.Radiobox
            name="isBase"
            label="Create New Base"
            checked={baseIsChecked}
            onChange={() => {
              setBaseIsChecked(!baseIsChecked);
              setOrgIsChecked(false);
            }}/>
          <Input.Radiobox
            name="isOrg"
            label="Create New Org"
            checked={orgIsChecked}
            onChange={() => {
              setOrgIsChecked(!orgIsChecked);
              setBaseIsChecked(false);
            }}/>
        </InputRow>
      )}
      {orgIsChecked && (
        <>
          <InputRow className="">
            <input type="hidden" value={newParent} name="orgId"></input>
            <TextLabel htmlFor="orgId">Where is it located?</TextLabel>
            <Breadcrumbs
              tree={parentNode}
              setNewParent={setNewParent}
              ancestors={ancestors} />
          </InputRow>
          <InputRow>
            <Input.Checkbox label="Is this a Wing-level Organization?" name="isWing" />
          </InputRow>
        </>
      )}
    </div>
  );
}

const CreateOrgDefaultPage = () => {
  const { parentNode, permissions: userPermissions } = useRootLayoutData();
  const [baseIsChecked, setBaseIsChecked] = useState(true);
  const checkPermissions = usePermissions();
  const { ancestors } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="post">
        <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
          <Modal.Title>Create Organization</Modal.Title>
          <AdminRow
            baseIsChecked={baseIsChecked}
            setBaseIsChecked={setBaseIsChecked}
            userPermissions={userPermissions}
            orgChecked={true}
            ancestors={ancestors}
            parentNode={parentNode}/>
          <InputRow>
            <TextLabel htmlFor="orgName">
              {baseIsChecked &&
              checkPermissions(userPermissions, RolePermissions.ADMIN)
                ? 'Base Name: '
                : 'Organization Name: '}
            </TextLabel>
            <TextInput
              id="orgName"
              name="orgName"
              maxLength={50}
              pattern="^.{3,}$"
              required/>
          </InputRow>
          <InputRow>
            <TextLabel htmlFor="abbreviation">Abbreviation:</TextLabel>
            <TextInput id="abbreviation" name="abbreviation" required />
          </InputRow>
        </div>
        <Modal.Buttons link="/org" value="create">
          Create
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default CreateOrgDefaultPage;
