import { Models } from '@lib/models.server';
import exp from 'constants';

export async function getUserChecklists(userId: string) {
  return Models.checklist.getUserChecklists(userId);
}

export async function completeItem(itemId: string, userId: string) {
  await Models.checklistItem.markComplete(itemId, userId);
}

export async function sendMessage(itemId: string, userId: string, postedById: string, internal: boolean) {
  await Models.checklistItem.sendMessage(itemId, userId, postedById, internal);
}

export async function markRead(commentIds: number[], userId: string){
  await Models.checklistItem.markRead(commentIds, userId);
}

export async function sendUserMessage(itemId: string, userId: string, postedById: string) {
  await Models.checklistItem.sendUserMessage(itemId, userId, postedById);
}

export async function userApprovalRequest(itemId: string) {
  await Models.checklistItem.userApprovalRequest(itemId);
}

export async function userApprovalRequestUnMark(itemId: string) {
  await Models.checklistItem.userApprovalRequestUnMark(itemId);
}
