import ErrorReport from '@components/ui/ErrorReport';
import RegisterForm from '@components/ui/forms/RegisterForm';
import type { ActionFunctionArgs} from '@remix-run/node';
import { json } from '@remix-run/node';
import { Link, useLoaderData } from '@remix-run/react';
import { getRootDescendants, getRootTree } from '@routes/_withNav+/_withSideBar+/_manage+/.server/manage.queries';
import {
  getOrgs, registerUserActionFn,
} from './register.queries.server';


export const loader = async () => {
  const orgs = await getOrgs();
  const parentNode = await getRootTree();
  const orgFlatArray = await getRootDescendants();
  return json({ orgs, parentNode, orgFlatArray }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
      'Content-Security-Policy': 'frame-ancestors: none',
    },
  });
};

export async function action({ request }: ActionFunctionArgs) {
  return registerUserActionFn(request, '/login');
}

const AccountRegister = () => {
  const { orgs, parentNode, orgFlatArray } = useLoaderData<typeof loader>();

  return (
    <main className="container mx-auto h-full p-5">
      <div className="flex gap-x-4 flex-col items-center">
        <Link to="/login" className="hover:underline underline-offset-2 border-0 border-zinc-300 rounded-lg px-3 py-2 hover:bg-zinc-200">Back to Login</Link>
      </div>
      <h1 className="text-2xl text-gray-900">
        Register for an account
      </h1>
      <RegisterForm orgs={orgs} btnAction="create" registerOrCreate="register" orgParentNode={parentNode} orgFlatArray={orgFlatArray} />
    </main>
  );
};

export const ErrorBoundary = ErrorReport;
export default AccountRegister;
