import type { Base, PrismaClient } from '@prisma/client';
import type { BaseCreateDto, BaseUpdateDto } from './dtos';
import type { IRepository } from './Repository.server';

interface IBaseRepository extends IRepository<Base, BaseCreateDto, BaseUpdateDto> {
  getAllWithOrgs(): Promise<any>;
  getWithOrgs(id: string): Promise<any>;
}

export class BaseRepository implements IBaseRepository {
  public prisma: PrismaClient;

  constructor(prisma: PrismaClient) {
    this.prisma = prisma;
  }

  async getAll() {
    return this.prisma.base.findMany();
  }

  async get(id: string) {
    return this.prisma.base.findFirstOrThrow({
      where: {
        id,
      },
    });
  }

  public async getAllWithOrgs() {
    return this.prisma.organization.findMany({
      where: {
        isBase: true,
      },
      include: {
        assignedOrganizations: true,
      },
    });
  }

  public async getWithOrgs(id: string) {
    return this.prisma.base.findFirstOrThrow({
      where: {
        id,
      },
      include: {
        organizations: true,
      },
    });
  }

  async create(data: BaseCreateDto): Promise<string> {
    return (
      await this.prisma.base.create({
        data: {
          name: data.name,
          createdAt: data.createdAt,
          updatedAt: data.updatedAt,
          createdById: data.createdBy,
          updatedById: data.updatedBy,
        },
        select: {
          id: true,
        },
      })
    ).id;
  }

  async update(id: string, data: Partial<BaseUpdateDto>) {
    await this.prisma.base.update({
      where: {
        id,
      },
      data: {
        name: data.name,
        updatedAt: data.updatedAt,
        updatedById: data.updatedBy,
      },
    });
  }

  async delete(id: string) {
    await this.prisma.base.delete({ where: { id } });
  }
}
