import { AcceptButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { Attribute } from '@lib/utils';
import { handleActionError } from '@lib/utils/utils.server';
import { MoveType } from '@prisma/client';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useFetcher, useLoaderData } from '@remix-run/react';
import { requireUserAsObject } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import {
  acceptMemberMove,
  getInboundMembers,
  getOrgBeingViewed,
  getOrgWithTemplates,
} from './.server/org.queries';
import { assign } from './.server/template.queries';
import { Models } from '@lib/models.server';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  const { currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.MOVES_READ,
  });

  const { orgId } = params;
  if (!orgId) throw new Error('org id is required.');
  const inbound = await getInboundMembers(orgId);

  const { orgBeingViewed } = await getOrgBeingViewed({
    defaultOrgId: currentOrg.uuid,
    searchParams: new URL(request.url).searchParams,
    params,
  });

  return json({
    inbound,
    orgBeingViewed,
  });
};

export const action = async ({ request }: ActionFunctionArgs) => {
  const assignSchema = z.object({
    userId: z.string(),
    currentOrg: z.string(),
    previousOrgId: z.string(),
    moveType: z.nativeEnum(MoveType),
    _action: z.string().regex(/cssAccept/),
  });

  try {
    const { id: loggedInUserId } = await requireUserAsObject(request, {
      permissions: Attribute.MOVES_READWRITE,
      options: { redirectOnFailure: false },
    });
    const {
      _action: action,
      userId,
      currentOrg,
      previousOrgId,
      moveType,
    } = assignSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );
    if (action === 'cssAccept') {
      // TODO: This needs to grab the first "moveType" available
      const org = await getOrgWithTemplates(currentOrg, moveType);
      // await acceptMemberMove(userId, currentOrg, previousOrgId);
      if (!org.templates[0].id)
        return jsonWithError(
          {
            status: 'error',
            message: `No templates found for ${moveType} type within ${org.name}.`,
          },
          'There was an error accepting the move.'
        );

      await assign({
        templateId: org.templates[0].id,
        userId: userId as string,
        assignedById: loggedInUserId,
      });

      await acceptMemberMove(userId, currentOrg, previousOrgId);

      return jsonWithSuccess(
        { status: 'success' },
        `Member assigned ${moveType} checklist!`
      );
    }
  } catch (e) {
    return handleActionError(e, 'There was an error accepting the move.');
  }
};

const InboundMembers = () => {
  const { inbound, orgBeingViewed } = useLoaderData<typeof loader>();

  const acceptFetcher = useFetcher();
  // TODO: Filter by PCS and / or PCA

  return (
    <>
      <PageHeader
        org={orgBeingViewed}
        pageTitle="Inbound Members"
        navigateTo="inbound"/>
      {inbound.length > 0 ? (
        <table className="text-left table-auto w-full border-neutral-300 border-1">
          <thead>
            <tr className="border-b border-black">
              <th className="text-left pl-3">Name</th>
              <th>Type</th>
              <th>Previous Organization</th>
              <th>Last Updated</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {inbound
              .sort((a, b) => a.name.localeCompare(b.name))
              .map((user) => {
                const updatedAt = new Date(user.updatedAt).toDateString();
                return (
                  <tr
                    key={user.id}
                    className="table-row border-b even:bg-zinc-50">
                    <td className="pl-3 py-4">{user.name}</td>
                    <td>{user.moveType}</td>
                    <td>{user.previousOrg?.name}</td>
                    <td>{updatedAt}</td>
                    <td className="text-center">
                      <acceptFetcher.Form method="post">
                        <input type="hidden" value={user.id} name="userId" />
                        <input
                          type="hidden"
                          value={user.moveType ?? ''}
                          name="moveType"/>
                        <input
                          type="hidden"
                          value={orgBeingViewed.uuid}
                          name="currentOrg"/>
                        {/* TODO: Actually assign the checklist onSubmit */}
                        <input
                          type="hidden"
                          name="previousOrgId"
                          value={user.previousOrg?.uuid ?? ''}/>
                        <AcceptButton
                          type="submit"
                          name="_action"
                          value="cssAccept">
                          Accept Move
                        </AcceptButton>
                      </acceptFetcher.Form>
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      ) : (
        `No members to show.`
      )}
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default InboundMembers;
