function getBackgroundClass(length: number): string {
  const classes = [
    'bg-zinc-400', // Default white for length 0
    'bg-slate-200',
    'bg-slate-300',
    'bg-slate-400',
    'bg-slate-100',
    'bg-slate-200',
    'bg-slate-300',
    'bg-slate-400',
    // Add more colors as needed
  ];
  return classes[length] || 'bg-zinc-400'; // Default to white if length exceeds the defined classes
}

