import { DeleteButton, PrimaryButton } from '@components/ui/Buttons';
import { DatabaseUpdatedIcon, SolidCheckmark, StartTimer } from '@components/ui/Icons';
import { ChevronRightIcon, CalendarDaysIcon } from '@heroicons/react/24/solid';
import { ChatBubbleBottomCenterTextIcon } from '@heroicons/react/24/outline';
import { Link, useFetcher } from '@remix-run/react';
import type { GetUserChecklistsResponse } from '@repositories/checklist.server';
import { Status } from '@routes/_withNav+/_noSideBar+/checklist.$itemId+/status';
import { ChecklistItemStatus } from '@prisma/client';

const ChecklistItemCard = ({
  item,
  loggedInUser,
}: {
  item: GetUserChecklistsResponse[number]['items'][number];
  loggedInUser: string;
}) => {
  const itemCompletedByMember = (itemRequiredRole: string) =>
    itemRequiredRole === 'USER';
  const notCompletedByMember = (itemRequiredRole: string) =>
    itemRequiredRole !== 'USER';

  const dueDate = new Date(item.createdAt);
  dueDate.setDate(dueDate.getDate() + 30);

  const markCompleteFetcher = useFetcher();
  const markInCompleteFetcher = useFetcher();
  const userRequestApprovalFetcher = useFetcher();

  const unread = (comments: any) => {
    let total = 0;
    for (const item of comments) {
      if (!item.readBy.includes(loggedInUser)) total++;
    }
    return total;
  };

  return (
    <li key={item.id} className=" border-b border-1 border-neutral-200">
      <div className="flex items-stretch gap-y-5 gap-x-10 py-3 pl-2 pr-3">
        <div className="flex flex-col basis-5/6">
          <div className="flex items-center justify-between">
            <div className="flex-grow pr-10">
              <h3 className="font-bold mb-1 w-full pb-3 pl-2 border-b">
                {item.reference.name ?? item.name}
              </h3>
              <div className="text-sm text-zinc-600 pt-2 pl-5 flex items-center gap-x-2">
                <div className="inline-flex items-center gap-1 text-nowrap">
                  <StartTimer className="size-6" title="Date Started" />
                  {new Date(item.createdAt).toDateString()}
                </div>
                <div className="inline-flex items-center gap-1.5 text-nowrap">
                  <CalendarDaysIcon title="Due Date" className="size-6" />
                  {dueDate.toDateString()}
                </div>
                <div className="inline-flex gap-1.5 items-center text-nowrap">
                  <DatabaseUpdatedIcon className="size-6" title="Date Last Updated" />
                  {new Date(item.updatedAt).toDateString()}
                </div>
                <div className="inline-flex gap-1.5 items-center text-nowrap">
                  {item.status === ChecklistItemStatus.Completed && (
                    <SolidCheckmark title="Date Completed" className="size-6 text-green-500"/>
                  )}
                  {item.status === ChecklistItemStatus.Completed && (
                    new Date(item.dateCompleted ?? dueDate).toDateString()
                  )}
                </div>
                <Link
                  className="inline-flex items-center gap-1.5 hover:bg-zinc-300 hover:rounded-lg p-1"
                  to={`${item.id}/comments`}>
                  <ChatBubbleBottomCenterTextIcon title="Comments" className="size-6" />
                  {item.comments && (
                    <>
                      {item.comments.length}
                      {unread(item.comments) > 0 && (
                        <span className="relative flex size-2">
                          <span className="animate-[ping_1s_ease-in-out_5] absolute inline-flex h-full w-full rounded-full bg-red-400 opacity-75"></span>
                          <span className="relative inline-flex rounded-full size-2 bg-red-500"></span>
                        </span>
                      )}
                    </>
                  )}
                  {!item.comments && '0'}
                </Link>
              </div>
            </div>
            <Status status={item.status}></Status>
          </div>
        </div>
        <div className="basis-1/6 flex items-center">
          {itemCompletedByMember(item.role.abbreviation) && (
            <>
              {item.status === ChecklistItemStatus.WaitingMember && (
                <markCompleteFetcher.Form method="POST" className="w-full">
                  <input type="hidden" value={item.id} name="itemId" />
                  <input type="hidden" value={loggedInUser} name="userId" />
                  <PrimaryButton
                    type="submit"
                    value="update"
                    name="_action"
                    className="w-full py-1">
                    { markCompleteFetcher.state === 'idle'
                      ? 'Mark Complete'
                      : 'Processing...' }
                  </PrimaryButton>
                </markCompleteFetcher.Form>
              )}
              {item.status === ChecklistItemStatus.Completed && (
                <div className="flex flex-col justify-between w-full">
                  <div>
                    <markInCompleteFetcher.Form method="POST">
                      <input type="hidden" value={ item.id } name="itemId"/>
                      <input
                        type="hidden"
                        value={ loggedInUser }
                        name="userId"/>
                      <DeleteButton
                        type="submit"
                        name="_action"
                        value="incomplete"
                        disabled={ markInCompleteFetcher.state !== 'idle' }
                        className="w-full">
                        { markInCompleteFetcher.state === 'idle'
                          ? 'Unsign'
                          : 'Processing...' }
                      </DeleteButton>
                    </markInCompleteFetcher.Form>
                  </div>
                </div>
              ) }
            </>
          ) }
          { notCompletedByMember( item.role.abbreviation ) && (
            <>
              { item.status === ChecklistItemStatus.WaitingMember && (
                <userRequestApprovalFetcher.Form
                  method="POST"
                  className="text-white text-center rounded-lg p-2 w-full">
                  <input type="hidden" name="itemId" value={ item.id }/>
                  <input type="hidden" name="userId" value={ loggedInUser }/>
                  <PrimaryButton
                    type="submit"
                    value="markready"
                    name="_action"
                    className="w-full">
                    { userRequestApprovalFetcher.state === 'idle'
                      ? 'Mark Ready'
                      : 'Processing...' }
                  </PrimaryButton>
                </userRequestApprovalFetcher.Form>
              ) }
              { (item.status !== ChecklistItemStatus.WaitingMember) && (
                <dl className="">
                  <dt className="font-bold">{item.status === ChecklistItemStatus.Completed ? 'Completed by' : 'Point of Contact'}:</dt>
                  <dd>{item.role.name}</dd>
                </dl>
              )}
            </>
          )}
        </div>
        <div className="flex items-center">
          <Link
            to={`${item.id}`}
            className="hover:ring-1 rounded py-1.5 px-1 hover:bg-zinc-200">
            <ChevronRightIcon className="h-10 w-10" />
          </Link>
        </div>
      </div>
    </li>
  );
};

export default ChecklistItemCard;
