import {
  ChecklistItemStatus,
  type ChecklistItem,
  type PrismaClient,
} from '@prisma/client';
import type { ChecklistItemDto } from './dtos';
import type { IRepository } from './Repository.server';

export interface IChecklistItemRepository
  extends IRepository<ChecklistItem, ChecklistItemDto, ChecklistItemDto> {
  markComplete(itemId: string, userId: string): Promise<void>;

  markIncomplete(itemId: string, userId: string): Promise<void>;

  getAllItemTimes(): Promise<any>;

  getAllInUsers(): Promise<any>;

  getCompletedCount(): Promise<any>;
}

export class ChecklistItemRepository implements IChecklistItemRepository {
  public prisma: PrismaClient;

  constructor(prisma: PrismaClient) {
    this.prisma = prisma;
  }

  public async getAll(): Promise<ChecklistItem[]> {
    return this.prisma.checklistItem.findMany();
  }

  public get(id: string): Promise<ChecklistItem> {
    throw new Error('Method not implemented.');
  }

  public create(data: ChecklistItemDto): Promise<string> {
    throw new Error('Method not implemented.');
  }

  public update(id: string, data: ChecklistItemDto): Promise<never> {
    throw new Error('Method not implemented.');
  }

  public delete(id: string): Promise<never> {
    throw new Error('Method not implemented.');
  }

  public async getWithComments(
    id: string,
    options?: { internalOnly: boolean; orgId: string }
  ) {
    const where = options?.internalOnly
      ? {
        id,
        comments: {
          some: {
            internal: true,
          },
        },
      }
      : { id };

    const itemQuery = this.prisma.checklistItem.findFirstOrThrow({
      where: {
        id,
      },
      include: {
        completedBy: true,
        // isDenied: true,
        templateItem: {
          include: {
            requiredRole: {
              include: {
                orgRoles: {
                  where: {
                    organizationId: options?.orgId,
                  },
                },
              },
            },
            reference: true,
          },
        },
        checklist: {
          include: {
            user: true,
          },
        },
      },
    });

    const commentsQuery = this.prisma.checklistItemComments.findMany({
      where: {
        checklistItemId: id,
        internal: options?.internalOnly ?? false,
      },
      select: {
        id: true,
        postedBy: true,
        readBy: true,
        internal: true,
        checklistItemId: true,
        datePosted: true,
        message: true,
        postedById: true,
      },
      orderBy: {
        datePosted: 'asc',
      },
    });

    const transactions: [typeof itemQuery, typeof commentsQuery] = [
      itemQuery,
      commentsQuery,
    ];

    return this.prisma.$transaction(transactions);
  }

  public async markComplete(
    itemId: string,
    userId: string,
    completedById?: string
  ): Promise<void> {
    const item = await this.prisma.checklistItem.update({
      where: {
        id: itemId,
      },
      data: {
        completedById: completedById ?? userId,
        isComplete: true,
        dateCompleted: new Date(Date.now()),
        updatedAt: new Date(Date.now()),
        status: ChecklistItemStatus.Completed,
      },
      select: {
        completedById: true,
        checklist: {
          select: {
            id: true,
            userId: true,
            _count: {
              select: {
                items: {
                  where: {
                    isComplete: false,
                  },
                },
              },
            },
          },
        },
      },
    });

    // All checklist items are complete, update the checklist and user move
    if (item.checklist._count.items === 0) {
      const checklistUpdate = this.prisma.checklist.update({
        where: { id: item.checklist.id },
        data: {
          isComplete: true,
          dateCompleted: new Date(Date.now()),
          updatedAt: new Date(Date.now()),
        },
      });

      const userUpdate = this.prisma.userProfile.update({
        where: { id: item.checklist.userId },
        data: {
          moveType: null,
          nextOrgId: null,
          updatedAt: new Date(Date.now()),
        },
      });

      await this.prisma.$transaction([checklistUpdate, userUpdate]);
    }
  }

  public async markIncomplete(
    itemId: string,
    userId: string,
    updatedById?: string
  ): Promise<void> {
    const updatedBy = updatedById
      ? {
        connect: {
          id: updatedById,
        },
      }
      : undefined;

    await this.prisma.checklistItem.update({
      where: {
        id: itemId,
      },
      data: {
        updatedBy,
        completedBy: {
          disconnect: true,
        },
        isComplete: false,
        dateCompleted: null,
        updatedAt: new Date(Date.now()),
        status: ChecklistItemStatus.WaitingMember,
      },
      select: {
        checklist: {
          select: {
            id: true,
            _count: {
              select: {
                items: {
                  where: {
                    isComplete: true,
                  },
                },
              },
            },
          },
        },
      },
    });

    // if ( item.checklist._count.items === 0 ) {
    //   const checklistUpdate = ChecklistRepository.complete( item.checklist.id );

    //   const userUpdate = UserRepository.completeMove( userId );

    //   await this.prisma.$transaction( [ checklistUpdate, userUpdate ] );
    // }
  }

  public async getAllItemTimes() {
    return this.prisma.checklistItem.findMany({
      select: {
        createdAt: true,
        updatedAt: true,
        dateCompleted: true,
      },
    });
  }

  public async sendMessage(
    itemId: string,
    postedById: string,
    message: string,
    internal: boolean
  ): Promise<void> {
    await this.prisma.checklistItemComments.create({
      data: {
        checklistItemId: itemId,
        postedById: postedById,
        message: message,
        datePosted: new Date(Date.now()),
        internal,
        readBy: [postedById],
      },
    });
  }

  public async markRead(
    commentId: number[],
    postedById: string
  ): Promise<void> {
    await this.prisma.checklistItemComments.updateMany({
      where: {
        id: {
          in: commentId,
        },
      },
      data: {
        readBy: {
          push: postedById,
        },
      },
    });

    // await this.prisma.checklistItemComments.update({
    //   where: {
    //     id: commentId,
    //   },
    //   data: {
    //     readBy: {
    //       push: postedById,
    //     },
    //   },
    // });
  }

  public async sendUserMessage(
    itemId: string,
    postedById: string,
    message: string
  ): Promise<void> {
    await this.prisma.checklistItemComments.create({
      data: {
        checklistItemId: itemId,
        postedById: postedById,
        message: message,
        datePosted: new Date(Date.now()),
        readBy: { set: [postedById] },
      },
    });
  }

  public async denyTaskWithMessage(
    itemId: string,
    postedById: string,
    message: string
  ): Promise<void> {
    const commentCreation = this.prisma.checklistItemComments.create({
      data: {
        checklistItemId: itemId,
        postedById: postedById,
        message: message === '' ? 'Item Denied' : message,
        datePosted: new Date(Date.now()),
        readBy: [postedById],
      },
    });

    const changeUserComplete = this.prisma.checklistItem.update({
      where: {
        id: itemId,
      },
      data: {
        userComplete: false,
        isDenied: true,
        updatedAt: new Date(Date.now()),
        status: ChecklistItemStatus.Denied,
      },
    });

    await this.prisma.$transaction([commentCreation, changeUserComplete]);
  }

  public async getTaskFromId(taskId: string) {
    return this.prisma.checklistItem.findFirst({
      // select: {},
      where: {
        id: taskId,
      },
      include: {
        templateItem: true,
      },
    });
  }

  public async userApprovalRequest(itemId: string): Promise<void> {
    await this.prisma.checklistItem.update({
      where: {
        id: itemId,
      },
      data: {
        userComplete: true,
        updatedAt: new Date(Date.now()),
        userCompleteDate: new Date(Date.now()),
        isDenied: false,
        status: ChecklistItemStatus.WaitingApproval,
      },
      select: {
        checklist: {
          select: {
            id: true,
            _count: {
              select: {
                items: {
                  where: {
                    userComplete: false,
                  },
                },
              },
            },
          },
        },
      },
    });
  }

  public async userApprovalRequestUnMark(itemId: string): Promise<void> {
    await this.prisma.checklistItem.update({
      where: {
        id: itemId,
      },
      data: {
        userComplete: false,
        updatedAt: new Date(Date.now()),
        status: ChecklistItemStatus.WaitingMember,
      },
      select: {
        checklist: {
          select: {
            id: true,
            _count: {
              select: {
                items: {
                  where: {
                    userComplete: true,
                  },
                },
              },
            },
          },
        },
      },
    });
  }

  public async getAllInUsers() {
    return this.prisma.checklist.findMany({
      where: {
        isComplete: false,
      },
      distinct: ['userId'],
      select: {
        userId: true,
        user: {
          select: {
            name: true,
          },
        },
      },
    });
  }

  public async getCompletedCount() {
    return this.prisma.checklistItem.count({
      select: {
        _all: true,
      },
      where: {
        isComplete: {
          equals: true,
        },
      },
    });
  }
}
