import { Form, Input, Label } from '@components/ui';
import { Modal } from '@components/ui/modals/Modal';
import { useLoaderData, useNavigate, useSearchParams } from '@remix-run/react';
import { useState } from 'react';
import { MoveType } from '@prisma/client';
import type { editUserMoveLoaderFn } from '@routes/_withNav+/_withSideBar+/_manage+/.server/move.queries';

type EditUserModalProps = {
  cancelUrl?: string | number;
  variant: 'edit' | 'start';
};

export default function UserMoveModal({
  cancelUrl,
  variant,
}: EditUserModalProps) {
  const { user, filteredOrgs } = useLoaderData<typeof editUserMoveLoaderFn>();
  const [searchParams, setSearch] = useSearchParams();
  const navigate = useNavigate();

  const moveTypeQuery = searchParams.get('type') ?? MoveType.PCS;

  const [variantState] = useState(variant);

  const isPcsMove =
    moveTypeQuery === MoveType.PCS || user.moveType === MoveType.PCS;

  const [ isNextOrg, setIsNextOrg ] = useState('');

  const handleNextOrg = (event) => {
    setIsNextOrg(event.target.value);
  };

  const isDisabled = isNextOrg !== 'default' ? false : true;

  return (
    <Modal
      onClose={() => {
        if (cancelUrl) {
          if (typeof cancelUrl === 'string') {
            searchParams.delete('type');
            navigate( {
              pathname: cancelUrl,
              search: searchParams.toString(),
            } );
          }
        } else {
          navigate( -1 );
        }
      }}>
      <Form method="post">
        <Input.Hidden name="userId" value={user.id} />
        <Input.Hidden name="currentOrgId" value={user.currentOrg.uuid} />
        <Modal.Body>
          <Modal.Title>
            {variantState === 'start' ? 'Start' : 'Edit'} move for {user.name}
          </Modal.Title>
          <Form.Row>
            <Label htmlFor="pcsMoveType">Type of Move</Label>
            <Input.Radiobox
              label="PCS"
              name="moveType"
              id="pcsMoveType"
              value={MoveType.PCS}
              defaultChecked={isPcsMove}
              onClick={() => {
                setSearch((prev) => {
                  prev.set('type', MoveType.PCS);
                  return prev;
                });
              }}/>
            <Input.Radiobox
              label="PCA"
              name="moveType"
              id="pcaMoveType"
              value={MoveType.PCA}
              defaultChecked={!isPcsMove}
              onClick={() => {
                setSearch((prev) => {
                  prev.set('type', MoveType.PCA);
                  return prev;
                });
              }}/>
          </Form.Row>
          <Form.Row>
            <Label htmlFor="nextOrg">Next Organization</Label>
            <Input.Select
              name="nextOrg"
              options={filteredOrgs}
              valueKey="uuid"
              defaultValue={user.nextOrg?.uuid}
              onChange={(event) => handleNextOrg(event)}/>
          </Form.Row>
        </Modal.Body>

        <Modal.Buttons link="/org" value="edit" navigateToUrl={-1} disabled={isDisabled}>
          {variantState === 'start' ? 'Start' : 'Save'}
        </Modal.Buttons>
      </Form>
    </Modal>
  );
}
