import { DeleteButton, LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { RichTextView } from '@components/ui/RichText/Viewer';
import { Table } from '@components/ui/table/Table';
import { TailwindWidth } from '@lib/constants';
import { useRootLayoutData } from '@lib/hooks/useMatchesData';
import { Attribute, isPrismaKnownError } from '@lib/utils';
import { Prisma, type Template } from '@prisma/client';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Outlet, useFetcher, useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, jsonWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { getOrgBeingViewed } from './.server/org.queries';
import { deleteTemplate } from './.server/template.queries';
import useIsAdmin from '@lib/hooks/useIsAdmin';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  const { currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.TEMPLATES_READWRITEDELETE,
  });

  const { orgBeingViewed } = await getOrgBeingViewed(
    {
      defaultOrgId: currentOrg.uuid,
      searchParams: new URL(request.url).searchParams,
    },
    true
  );

  return json({
    orgBeingViewed: { ...orgBeingViewed },
  }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
};

export const action = async ({ request }: ActionFunctionArgs) => {
  try {
    await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.TEMPLATES_READWRITEDELETE,
    });

    const deleteTemplateSchema = z.object({
      id: z.string(),
    });

    const { id } = deleteTemplateSchema.parse(
      Object.fromEntries(await request.formData())
    );

    const result = await deleteTemplate(id);

    if (result === -1)
      return jsonWithError(
        {
          status: 'error',
          message:
            'There are still items in this template. Delete the items first before deleting the template.',
        },
        'There are still items in the template.',
        {
          headers: {
            'X-Frame-Options': 'SAMEORIGIN',
          },
        }
      );

    return jsonWithSuccess({ status: 'ok' }, 'Template Deleted!', {
      headers: {
        'X-Frame-Options': 'SAMEORIGIN',
      },
    });
  } catch (e) {
    console.error(e);
    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.',
          {
            headers: {
              'X-Frame-Options': 'SAMEORIGIN',
            },
          }
        );
      } else if (e instanceof ZodError) {
        return jsonWithError(
          {
            status: 'error',
            message: 'There was a validation error',
            issues: e.issues,
          },
          'There was an error.',
          {
            headers: {
              'X-Frame-Options': 'SAMEORIGIN',
            },
          }
        );
      } else if (
        e instanceof Prisma.PrismaClientKnownRequestError &&
        isPrismaKnownError(e)
      ) {
        if (e.code === 'P2003')
          return jsonWithError(
            {
              status: 'error',
              message:
                'There are still items in this template. Delete the items first before deleting the template.',
            },
            'There are still items in the template.',
            {
              headers: {
                'X-Frame-Options': 'SAMEORIGIN',
              },
            }
          );
      } else {
        return jsonWithError(
          { status: 'error', message: e.message },
          'There was an error.',
          {
            headers: {
              'X-Frame-Options': 'SAMEORIGIN',
            },
          }
        );
      }
    }
  }
};

export default function TemplateIndex() {
  const deleteFetcher = useFetcher();

  const { orgBeingViewed } = useLoaderData<typeof loader>();
  const rolesData = useRootLayoutData('roles');
  const { isAdmin } = useIsAdmin(rolesData);

  const tableContents = [
    {
      header: 'Name',
      key: 'name',
    },
    {
      header: 'Description',
      key: 'description',
      width: TailwindWidth['W_3/5'],
      render: function (description: string) {
        return (
          <div className="line-clamp-2">
            <RichTextView>{description}</RichTextView>
          </div>
        );
      },
    },
    {
      header: 'Active',
      key: '_count.userChecklists',
      render: (count: string) => count || 0,
    },
  ];

  return (
    <section>
      <PageHeader org={orgBeingViewed} pageTitle="Available Templates:" />
      <div className="my-5">
        <LinkButton
          variant="green"
          to={{
            pathname: `/template/create`,
            searchParams: { org: orgBeingViewed.uuid },
          }}>
          Create New Template
        </LinkButton>
      </div>
      {orgBeingViewed.templates!.length > 0 ? (
        <Table cells={tableContents}>
          <Table.HeaderRow />
          <Table.Body>
            {orgBeingViewed.templates?.map((template: Template) => (
              <Table.Row key={template.id} item={template}>
                <Table.ButtonCell className="text-right">
                  <LinkButton
                    variant="gray"
                    to={{
                      pathname: `/template/${template.id}`,
                      searchParams: { org: orgBeingViewed.uuid },
                    }}>
                    View
                  </LinkButton>
                  {!isAdmin && (
                    <deleteFetcher.Form method="post" className="inline m-3">
                      <input type="hidden" name="id" value={template.id} />
                      <DeleteButton name="_action" value="delete" type="submit">
                        Delete
                      </DeleteButton>
                    </deleteFetcher.Form>
                  )}
                </Table.ButtonCell>
              </Table.Row>
            ))}
          </Table.Body>
        </Table>
      ) : (
        'No templates to show.'
      )}
      <Outlet />
    </section>
  );
}

export const ErrorBoundary = ErrorReport;
