import type { UserUpdateDto } from '@repositories/dtos';
import { Models } from '@lib/models.server';


export async function getUser(id: string) {
  return Models.userProfile.get(id);
}

export async function getAccount(profileId: string) {
  return Models.userProfile.getAccountByProfile(profileId);
}

export async function updateProfile(id: string, data: UserUpdateDto) {
  return Models.userProfile.update(id, data);
}
