import type { BaseCreateDto, BaseUpdateDto, OrganizationCreateDto } from '@repositories/dtos';
import { Models } from '@lib/models.server';

export async function getBasesWithOrgs() {
  return Models.base.getAllWithOrgs();
}

export async function createBase(data: OrganizationCreateDto) {
  await Models.org.create(data);
}

export async function getBase(id: string) {
  return Models.org.get(id);
}

export async function updateBase(id: string, data: BaseUpdateDto) {
  await Models.base.update(id, data);
}
