import { LinkButton } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { Attribute } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { getBasesWithOrgs } from './.server/base.queries';
import PageHeader from '@components/ui/PageHeader';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  await requireUser(request, { permissions: Attribute.BASES_READWRITE });
  return json({ bases: await getBasesWithOrgs() }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
    },
  });
};

const BaseIndex = () => {
  const { bases } = useLoaderData<typeof loader>();
  return (
    <>
      <PageHeader pageTitle="Bases" />
      <LinkButton variant="green" to={`/base/create`} className="mb-7">
        Add Base
      </LinkButton>
      <table className="text-left table-auto w-full border-neutral-300 border-1 mt-7">
        <thead>
          <tr className="border-b border-black">
            <th className="pl-3">Name</th>
            <th>Organizations</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {bases.map((base) => (
            <tr key={base.id} className="table-row border-b even:bg-zinc-50">
              <td className="py-4 pl-3">{base.name}</td>
              <td>{base.assignedOrganizations.length}</td>
              <td className="text-right pr-5">
                <LinkButton variant="gray" to={`/base/${base.uuid}/edit`}>
                  Edit
                </LinkButton>
                <LinkButton
                  variant="green"
                  className="ml-3"
                  to={`/base/${base.uuid}/add`}>
                  Add Wing
                </LinkButton>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <Outlet />
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default BaseIndex;
