import ErrorReport from '@components/ui/ErrorReport';
import { Checkbox, InputRow, TextInput, TextLabel } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Form, useLoaderData } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import React from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { deleteRole, getOrgRole } from './.server/role.queries';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, {
    permissions: Attribute.ORGROLES_READWRITEDELETE,
  });

  const { roleId } = params;
  if (!roleId) throw json({ status: 'error', error: 'roleId is required.' });
  const orgRole = await getOrgRole(roleId);
  return json({ orgRole });
};

export async function action({ request }: ActionFunctionArgs) {
  const roleDelSchema = z.object({
    roleId: z.string().min(3),
  });
  const orgId = new URL(request.url).searchParams.get('org');
  try {
    await requireUser(request, {
      options: { redirectOnFailure: false },
      permissions: Attribute.ORGROLES_READWRITEDELETE,
    });
    const { roleId } = roleDelSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );
    await deleteRole(roleId);
  } catch (e) {
    console.error(e);

    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          { status: 'error', message: 'validation error', errors: e.errors },
          'There was an error deleting the role.'
        );
      }
      return jsonWithError(
        {
          status: 'error',
          message:
            process.env.NODE_ENV === 'development'
              ? 'This role is required.'
              : 'There was an error.',
        },
        'There was an error deleting the role.'
      );
    }
  }

  return redirectWithSuccess(
    `/role${orgId ? `?org=${orgId}` : ''}`,
    'Role Deleted!'
  );
}

const DeleteOrgRoleDefaultPage = () => {
  const data = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="POST">
        <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
          <Modal.Title>Remove Organization Role:</Modal.Title>
          <input type="hidden" name="roleId" value={data.orgRole?.uuid} />
          <InputRow>
            <p>
              Please enter
              <code className="bg-zinc-300 p-1 m-1 rounded-lg py-1">
                {data.orgRole?.role.name}
              </code>
              below to confirm the deletion.
            </p>
          </InputRow>
          <InputRow>
            <TextLabel htmlFor="orgname">Selected Role:</TextLabel>
            <TextInput
              id="orgname"
              pattern={data.orgRole?.role.name}
              required/>
          </InputRow>

          <InputRow>
            <Checkbox
              id="helper-checkbox"
              name="helper-checkbox"
              aria-describedby="helper-checkbox-text"
              label="Confirm Role Deletion"
              required/>
          </InputRow>
        </div>
        <Modal.Buttons link=".." value="delete">
          Delete
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default DeleteOrgRoleDefaultPage;
