import { ComboboxOption } from '@headlessui/react';
import { CheckIcon, ChevronRightIcon } from '@heroicons/react/20/solid';
import type { Organization } from '@prisma/client';

export default function OrgComboboxSearchResults({
  filteredOrgs,
}: {
  filteredOrgs: Organization[];
}) {
  const results = filteredOrgs.map((org) => (
    <ComboboxOption
      value={org}
      key={org.uuid}
      className="divide-y divide-slate-500 group flex cursor-default data-[focus]:cursor-pointer items-center gap-2 rounded-lg select-none data-[focus]:bg-slate-900/10 data-[selected]:bg-blue-500/30">
      {({ selected }) => (
        <div className="flex items-center gap-2 p-4">
          {selected ? (
            <CheckIcon className="h-5 w-5" />
          ) : (
            <ChevronRightIcon className="h-5 w-5" />
          )}
          <strong className="text-slate-900 text-sm font-medium dark:text-slate-200">
            {org.name}
          </strong>
        </div>
      )}
    </ComboboxOption>
  ));
  if (results.length > 0) {
    return results;
  } else {
    return (
      <p className="p-4 divide-y divide-slate-500 group flex cursor-default data-[focus]:cursor-pointer items-center gap-2 rounded-lg select-none data-[focus]:bg-slate-900/10 data-[selected]:bg-blue-500/30 text-slate-900 text-sm font-medium dark:text-slate-200">
        No results
      </p>
    );
  }
}
