import ErrorReport from '@components/ui/ErrorReport';
import PageHeader from '@components/ui/PageHeader';
import { Attribute } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { requireUserAsObject } from '@services/user.server';
import { getOrgBeingViewed, getSponsorsByOrg, getSubordinates, getSupervisors } from './.server/org.queries';
import { CloudArrowDownIcon, MagnifyingGlassCircleIcon, XMarkIcon } from '@heroicons/react/24/outline';
import { Dialog, DialogPanel } from '@headlessui/react';
import { useState } from 'react';
import { downloadCSV } from '@components/Controls/SupervisorExport';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  const { currentOrg } = await requireUserAsObject(request, {
    permissions: Attribute.MOVES_READ,
  });
  const { orgId } = params;
  if (!orgId) throw new Error('org id is required.');

  const supervisor = await getSupervisors(orgId);
  const subordinates = await getSubordinates(orgId);
  const sponsor = await getSponsorsByOrg(orgId);

  const { orgBeingViewed } = await getOrgBeingViewed({
    defaultOrgId: currentOrg.uuid,
    searchParams: new URL(request.url).searchParams,
    params,
  });

  return json({
    supervisor,
    subordinates,
    sponsor,
    orgBeingViewed,
  });
};

const SupervisorReport = () => {
  const { orgBeingViewed, supervisor, subordinates, sponsor } = useLoaderData<typeof loader>();

  const [isOpen, setIsOpen] = useState(false);
  const [supervisorId, setSupervisorId] = useState<string | null>(null);

  const filteredSubordinates = supervisorId
    ? subordinates.filter((subordinate) => subordinate.supervisorId === supervisorId)
    : [];

  const filteredSponsors = supervisorId
    ? sponsor.filter((sponsor) => sponsor.sponsorId === supervisorId )
    : [];

  return (
    <>
      <PageHeader
        org={orgBeingViewed}
        pageTitle="Supervisors Reports"
        navigateTo="supervisors"/>

      <div className="w-full flex justify-end">
        <button
          type="button"
          onClick={() => downloadCSV(supervisor, subordinates, sponsor)}
          className="inline-flex justify-right px-4 py-1 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-white">
          <CloudArrowDownIcon className="w-5 h-5 mr-2" />
          Export
        </button>
      </div>

      <table className="text-left table-auto w-full border-neutral-300">
        <thead>
          <tr className="border-b border-black">
            <th>Name</th>
            <th>Rank</th>
            <th>Email</th>
            <th className="text-center">Supervisor Info</th>
          </tr>
        </thead>
        <tbody>
          {supervisor.length > 0 ? (
            [...supervisor]
              .sort((a, b) => (a?.name ?? '').localeCompare(b?.name ?? ''))
              .map((sup) => (
                <tr key={sup?.id}>
                  <td>{sup?.name}</td>
                  <td>{sup?.rank}</td>
                  <td>{sup?.workEmail}</td>
                  <td className="flex justify-center">
                    <button
                      onClick={() => {
                        setIsOpen(true);
                        setSupervisorId(sup?.id);
                      }}>
                      <MagnifyingGlassCircleIcon className="h-10 w-10" />
                    </button>
                  </td>
                </tr>
              ))
          ) : (
            <tr>
              <td colSpan={4} className="text-center">
                No supervisors found.
              </td>
            </tr>
          )}

        </tbody>
      </table>

      <div>
        {isOpen && (
          <Dialog open={isOpen} onClose={() => setIsOpen(false)}>
            <div className="fixed inset-0 bg-black bg-opacity-30" aria-hidden="true"></div>
            <div className="fixed inset-0 flex items-center justify-center">
              <DialogPanel className="bg-white p-4 rounded-md">
                <div className="flex justify-center items-center w-full">
                  <p className="text-center text-lg flex-grow">Supervisor Information</p>
                  <XMarkIcon
                    onClick={() => setIsOpen(false)}
                    className="size-10 cursor-pointer ml-4"
                  />
                </div>

                <p className="text-md font-semibold mt-2">Subordinates:</p>
                {filteredSubordinates.length > 0 ? (
                  <>
                    <div className="overflow-x-auto p-4">
                      <table className="min-w-full w-[500px] table-auto border-none">
                        <thead>
                          <tr>
                            <th className="px-4 py-2 text-left">Name</th>
                            <th className="px-4 py-2 text-left">Rank</th>
                            <th className="px-4 py-2 text-left">Email</th>
                          </tr>
                        </thead>
                        <tbody>
                          {[...filteredSubordinates]
                            .sort((a, b) => (a?.name ?? '').localeCompare(b?.name ?? ''))
                            .map((subordinate) => (
                              <tr key={subordinate.id}>
                                <td className="px-4 whitespace-nowrap truncate">{subordinate.name}</td>
                                <td className="px-4">{subordinate.rank}</td>
                                <td className="px-4">{subordinate.workEmail}</td>
                              </tr>
                            ))}
                        </tbody>
                      </table>
                    </div>
                  </>
                ) : (
                  <p>No subordinates found for this supervisor.</p>
                )}

                <p className="text-md font-semibold mt-4">Incoming Airman:</p>
                {filteredSponsors.length > 0 ? (
                  <>
                    <div className="overflow-x-auto p-4">
                      <table className="min-w-full w-[500px] table-auto border-none">
                        <thead>
                          <tr>
                            <th className="px-4 py-2 text-left">Name</th>
                            <th className="px-4 py-2 text-left">Rank</th>
                            <th className="px-4 py-2 text-left">Email</th>
                          </tr>
                        </thead>
                        <tbody>
                          {[...filteredSponsors]
                            .sort((a, b) => (a?.name ?? '').localeCompare(b?.name ?? ''))
                            .map((sponsor) => (
                              <tr key={sponsor.id}>
                                <td className="px-4 whitespace-nowrap truncate">{sponsor.name}</td>
                                <td className="px-4">{sponsor.rank}</td>
                                <td className="px-4">{sponsor.workEmail}</td>
                              </tr>
                            ))}
                        </tbody>
                      </table>
                    </div>
                  </>
                ) : (
                  <p>No Incoming Airman found for this supervisor.</p>
                )}
              </DialogPanel>
            </div>
          </Dialog>
        )}

      </div>
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default SupervisorReport;

