import { Models } from '@lib/models.server';

export async function completeItem(itemId: string,userId: string,completedById: string,) {
  await Models.checklistItem.markComplete(itemId, userId, completedById);
}

export async function pauseChecklist(itemId: string,userId: string) {
  await Models.checklist.pause(itemId, userId);
}

export async function resumeChecklist(itemId: string,userId: string) {
  await Models.checklist.resume(itemId, userId);
}

export async function archiveChecklist(itemId: string,userId: string) {
  await Models.checklist.archive(itemId, userId);
}

export async function getChecklistItem(
  itemId: string,
  options?: { internalOnly: boolean },
) {
  return Models.checklistItem.getWithComments(itemId, options);
}

export async function uncompleteItem(
  itemId: string,
  userId: string,
  updatedById?: string,
) {
  await Models.checklistItem.markIncomplete(itemId, userId, updatedById);
}


export async function denyTask(
  itemId: string, 
  postedById: string, 
  message: string
): Promise<void> {
  await Models.checklistItem.denyTaskWithMessage(itemId, postedById, message);
}

export async function getTask(taskId: string){
  return Models.checklistItem.getTaskFromId(taskId);
}

export async function getRoles(
  orgId: string,
  userRoles?: Array<string>,
): Promise<Map<string, { name: string; id: string; description: string }>> {
  // Get the roles for the orgId and its sub-organizations
  const roles = await Models.orgRole.getChildOrgRoles(orgId, userRoles);

  // Map to store an easier to use object with all the checklists and roles
  let rolesMap = new Map<
    string,
    { name: string; id: string; description: string }
  >();

  /**
   * For each role find the checklist items that have a required role that matches
   * and add them to the Map
   */
  for (const orgRole of roles) {
    rolesMap.set(orgRole.role.abbreviation, {
      name: orgRole.role.name,
      id: orgRole.uuid,
      description: orgRole.role.description,
    });
  }

  return rolesMap;
}

export async function getItemCountByUserRole(userId: string, orgId: string) {
  return Models.orgRole.getItemCountByRole(userId, orgId);
}

export async function getItemCountForAllRoles(orgId: string) {
  return Models.orgRole.getItemCountForAllRoles(orgId);
}

export function hasCSSRole(userRoles: { abbreviation: string }[]) {
  return userRoles.findIndex((role) => role.abbreviation === 'CSS') > -1;
}

export async function getChecklistItemsByRole(roleId: string, orgId: string, userId: string) {
  return Models.orgRole.getChecklistItems(roleId, orgId, userId);
}
