import { Badge } from '@components/ui/Badge';
import { Button } from '@components/ui/Buttons';
import ErrorReport from '@components/ui/ErrorReport';
import { CalendarDaysIcon, CheckIcon, ClockIcon } from '@heroicons/react/24/solid';
import { Attribute } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData, useNavigate } from '@remix-run/react';
import { requireUserAsObject } from '@services/user.server';
import { getUserChecklist } from './.server/org.queries';
import { RichTextView } from '@components/ui/RichText/Viewer';
import { StartTimer, DatabaseUpdatedIcon, SolidCheckmark } from '@components/ui/Icons';
import { ChecklistItemStatus, ChecklistStatus } from '@prisma/client';
import { ArchiveBoxArrowDownIcon, CheckCircleIcon, LockClosedIcon, PauseIcon } from '@heroicons/react/24/outline';

function StatusBadge({status}: {status: ChecklistStatus}) {
  switch (status) {
    case 'Paused':
      return <Badge className="gap-1 pr-3.5" variant="warning"><PauseIcon className="size-5"/>Paused</Badge>;
    case 'Locked':
      return <Badge className="gap-1" variant="danger"><LockClosedIcon className="size-5"/>Locked</Badge>;
    case 'InProgress':
      return <Badge className="gap-1" variant="primary">In-Progress</Badge>;
    case 'Completed':
      return <Badge className="gap-1" variant="green"><SolidCheckmark className="size-5"/>Completed</Badge>;
    case 'Archived':
      return <Badge className="gap-1" variant="warning"><ArchiveBoxArrowDownIcon className="size-5"/>Archived</Badge>;
  }
}

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUserAsObject(request, {
    permissions: [Attribute.MOVES_READ, Attribute.CHECKLISTS_READ],
  });

  const { orgId, checklistId } = params;
  
  if (!orgId || !checklistId)
    throw new Error('org and checklist ids are required.');

  const { user, ...checklist } = await getUserChecklist(checklistId);

  return json({ user, checklist });
};

const MemberChecklist = () => {
  const { user, checklist } = useLoaderData<typeof loader>();
  const navigate = useNavigate();

  const dueDate = new Date(checklist.createdAt);
  dueDate.setDate(dueDate.getDate() + 30);

  const completedCount = checklist.items.filter(
    (item) => item.isComplete
  ).length;
  const totalCount = checklist.items.length;

  return (
    <>
      <Button onClick={() => navigate(-1)}>Back</Button>

      <div className="grid grid-cols-2 mb-5">
        <div className="flex gap-4 items-center">
          <h1 className="text-2xl my-5">{user.name}'s Checklist</h1>
          <StatusBadge status={checklist.status}></StatusBadge>
        </div>
        <div>
          <h1 className="text-2xl my-5 text-right">
            {completedCount} out of {totalCount} tasks completed
          </h1>
        </div>
        <div className="flex gap-4 col-span-2">
          <div className="inline-flex items-center gap-1 text-nowrap">
            <StartTimer className="size-6" title="Date Started" />
            {new Date(checklist.createdAt).toDateString()}
          </div>
          <div className="inline-flex items-center gap-1.5 text-nowrap">
            <CalendarDaysIcon title="Due Date" className="size-6" />
            {dueDate.toDateString()}
          </div>
          <div className="inline-flex gap-1.5 items-center text-nowrap">
            <DatabaseUpdatedIcon className="size-6" title="Date Last Updated" />
            {new Date(checklist.updatedAt).toDateString()}
          </div>
          <div className="inline-flex gap-1.5 items-center text-nowrap">
            {checklist.status === ChecklistItemStatus.Completed && (
              <SolidCheckmark title="Date Completed" className="size-6 text-green-500"/>
            )}
            {checklist.status === ChecklistItemStatus.Completed && (
              new Date(checklist.dateCompleted ?? dueDate).toDateString()
            )}
          </div>
        </div>
      </div>

      

      <div className="overflow-x-auto">
        <table className="text-left w-full border-neutral-300 border-1 border-collapse">
          <thead>
            <tr className="border-b border-black">
              <th className="px-3 w-[200px]">Item Name</th>
              <th className="px-3">Description</th>
              <th className="text-center px-3">Date Updated</th>
              <th className="text-center px-3">POC</th>
              <th className="text-center px-3">Status</th>
            </tr>
          </thead>
          <tbody>
            {checklist.items.map((item) => (
              <tr key={item.id} className="border-b even:bg-zinc-50">
                <td className="py-2 px-3 whitespace-nowrap overflow-hidden text-ellipsis">
                  {item.templateItem.name}
                </td>
                <td className="px-3 max-w-xs overflow-hidden whitespace-nowrap text-ellipsis">
                  <div className="line-clamp-2">
                    <RichTextView>{item.templateItem.reference?.description ?? item.templateItem.description}</RichTextView>
                  </div>
                </td>
                <td className="text-center px-3">
                  {new Date(item.updatedAt).toDateString()}
                </td>
                <td className="text-center px-3">
                  {item.templateItem.requiredRole.name}
                </td>
                <td className="text-center px-3">
                  {item.isComplete && (
                    <Badge className="gap-1">
                      <CheckIcon className="size-4" />
                  Signed
                    </Badge>
                  )}
                  {!item.isComplete && item.userComplete && (
                    <Badge variant="green">
                  Ready to Complete
                    </Badge>
                  )}
                  {!item.isComplete && !item.userComplete && (
                    <Badge className="gap-1" variant="warning">
                      <ClockIcon className="size-5" />
                  Awaiting Member
                    </Badge>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>

  );
};
export const ErrorBoundary = ErrorReport;
export default MemberChecklist;
