type MetricsBoxProps = {
  header: string;
  metric: string | number;
  colSpan?: number;
}

export function MetricBox({ header, metric, colSpan }: MetricsBoxProps) {
  return (
    <div
      className={`bg-gray-100 border-2 relative border-bg-gray-300 p-2${colSpan ? ` col-span-${colSpan}` : ''}`}>
      <>
        <h3 className="absolute w-[calc(100%_-_2px_-_1rem)]">{header}</h3>
        <p className="flex items-center justify-center h-full font-bold text-3xl">
          {metric}
        </p>
      </>
    </div>
  );
}

export function MetricBoxSkeleton({ colSpan }: { colSpan?: number }) {
  return (
    <div
      className={`bg-gray-100 h-[186px] border-2 border-bg-gray-300 p-2 col-span-${colSpan}`}>
      <div className="animate-pulse h-full grid grid-rows-[2rem_1fr]">
        <div className="h-8 w-full bg-gradient-to-r from-zinc-50 to-zinc-200 rounded-full"></div>
        <div className="m-4 flex-grow flex flex-col items-center justify-center">
          <div className="h-12 w-1/3 bg-gradient-to-r from-zinc-50 to-zinc-200 rounded-full"></div>
        </div>
      </div>
    </div>
  );
}

export function MetricBoxesRowSkeleton() {
  return Array.from({ length: 8 }).map((_, i, array) => (
    <MetricBoxSkeleton
      key={`skeleton-box-row-${i}`} />
  ));
}
