import type { TailwindTextAlignType, TailwindWidthType } from '@lib/types';
import type { PropsWithChildren, ReactNode } from 'react';
import { createContext, forwardRef, useContext } from 'react';

const TableContext = createContext<{
  cells: TableItem[];
}>({ cells: [] });

type TableFnProps = React.TableHTMLAttributes<HTMLTableElement> & {
  cells: TableItem[];
};

export type TableItem = {
  header: string;
  key: string;
  width?: TailwindWidthType;
  textAlign?: TailwindTextAlignType;
  render?: (
    item: string,
    obj: TableRowFnProps['item'],
  ) => ReactNode | ((item: string) => ReactNode);
};

function TableFn(
  props: TableFnProps,
  ref: React.ForwardedRef<HTMLTableElement>
) {
  const classes = `text-left table-auto w-full border-neutral-300 border-1${' ' + props.className}`;
  return (
    <TableContext.Provider value={{ cells: props.cells }}>
      <table {...props} ref={ref} className={classes}></table>
    </TableContext.Provider>
  );
}

function TableHeaderRowFn(
  { children, ...props }: React.ThHTMLAttributes<HTMLTableRowElement>,
  ref: React.ForwardedRef<HTMLTableRowElement>
) {
  const { cells } = useContext(TableContext);
  return (
    <thead>
      <tr {...props} ref={ref} className="border-b border-black">
        {cells.map((cell) => (
          <th
            key={`header-${cell.key}`}
            className={`${cell.width ? ` ${cell.width}` : ''}${cell.textAlign ? ` ${cell.textAlign}` : ''}`}>
            {cell.header}
          </th>
        ))}
        {children}
      </tr>
    </thead>
  );
}

function TableHeaderFn(
  props: React.ThHTMLAttributes<HTMLTableCellElement>,
  ref: React.ForwardedRef<HTMLTableCellElement>
) {
  const { cells } = useContext(TableContext);
  return cells.map((cell) => (
    <th {...props} ref={ref} key={'8'}>
      {cell.header}
    </th>
  ));
}

function TableBodyFn(
  { children }: PropsWithChildren,
  ref: React.ForwardedRef<HTMLTableSectionElement>
) {
  return <tbody ref={ref}>{children}</tbody>;
}

type TableRowFnProps = React.TableHTMLAttributes<HTMLTableRowElement> & {
  item: { id: string; [key: string]: any };
};

function TableRowFn(
  props: TableRowFnProps,
  ref: React.ForwardedRef<HTMLTableRowElement>
) {
  const classes = `table-row border-b even:bg-zinc-50${' ' + props.className}`;
  const { cells } = useContext(TableContext);
  const splitKey = (key: string, item: TableRowFnProps['item']) => {
    const splits = key.split('.');

    let returnVal: typeof item = item;

    splits.forEach((value) => {
      returnVal = returnVal[value];
    });

    return returnVal;
  };

  return (
    <tr {...props} ref={ref} className={classes}>
      {cells.map((cell) => {
        const item = cell.key.includes('.')
          ? splitKey(cell.key, props.item)
          : props.item[cell.key];

        return (
          <td
            key={`${props.item.id}-${cell.key}`}
            className={`p-3${cell.width ? ` ${cell.width}` : ''}`}>
            {cell.render ? cell.render(item, props.item) : item}
          </td>
        );
      })}
      {props.children}
    </tr>
  );
}

function TableCellFn(
  props: React.TdHTMLAttributes<HTMLTableCellElement>,
  ref: React.ForwardedRef<HTMLTableCellElement>
) {
  return <td {...props} ref={ref} className="p-3"></td>;
}

function TableButtonCellFn(
  {
    children,
    className,
    ...props
  }: React.TdHTMLAttributes<HTMLTableCellElement>,
  ref: React.ForwardedRef<HTMLTableCellElement>
) {
  return (
    <td {...props} ref={ref} className="pr-5">
      <div className={className}>{children}</div>
    </td>
  );
}

export let Table = Object.assign(forwardRef(TableFn), {
  HeaderRow: forwardRef(TableHeaderRowFn),
  Header: forwardRef(TableHeaderFn),
  Body: forwardRef(TableBodyFn),
  Row: forwardRef(TableRowFn),
  Cell: forwardRef(TableCellFn),
  ButtonCell: forwardRef(TableButtonCellFn),
});
