import * as Sentry from '@sentry/remix';

/**
 * By default, Remix will handle generating the HTTP Response for you.
 * You are free to delete this file if you'd like to, but if you ever want it revealed again, you can run `npx remix reveal` âœ¨
 * For more information, see https://remix.run/file-conventions/entry.server
 */

import { Models } from '@lib/models.server';
import { getSession } from '@services/session.server';
import { PassThrough } from 'node:stream';

import type { LoaderFunctionArgs, EntryContext } from '@remix-run/node';
import {
  createReadableStreamFromReadable,
} from '@remix-run/node';
import { RemixServer } from '@remix-run/react';
import isbot from 'isbot';
import { renderToPipeableStream } from 'react-dom/server';

function sendErrorToSentry(error: unknown, request: Request) {
  if (error instanceof Error) { // Capture as an error object
    Sentry.captureRemixServerException(error, 'remix.server', request, true);
  } else { // Capture non-Error objects
    Sentry.captureException(error);
  }
}

const ABORT_DELAY = 5_000;

export default function handleRequest(
  request: Request,
  responseStatusCode: number,
  responseHeaders: Headers,
  remixContext: EntryContext
) {
  return isbot(request.headers.get('user-agent'))
    ? handleBotRequest(
      request,
      responseStatusCode,
      responseHeaders,
      remixContext
    )
    : handleBrowserRequest(
      request,
      responseStatusCode,
      responseHeaders,
      remixContext
    );
}

function handleBotRequest(
  request: Request,
  responseStatusCode: number,
  responseHeaders: Headers,
  remixContext: EntryContext
) {
  return new Promise((resolve, reject) => {
    let shellRendered = false;
    const { pipe, abort } = renderToPipeableStream(
      <RemixServer
        context={remixContext}
        url={request.url}
        abortDelay={ABORT_DELAY}/>,
      {
        onAllReady() {
          shellRendered = true;
          const body = new PassThrough();
          const stream = createReadableStreamFromReadable(body);

          responseHeaders.set('Content-Type', 'text/html');

          resolve(
            new Response(stream, {
              headers: responseHeaders,
              status: responseStatusCode,
            })
          );

          pipe(body);
        },
        onShellError(error: unknown) {
          reject(error);
        },
        onError(error: unknown) {
          responseStatusCode = 500;
          // Log streaming rendering errors from inside the shell.  Don't log
          // errors encountered during initial shell rendering since they'll
          // reject and get logged in handleDocumentRequest.
          if (shellRendered) {
            sendErrorToSentry(error, request);
            console.error(error);
          }
        },
      }
    );

    setTimeout(abort, ABORT_DELAY);
  });
}

function handleBrowserRequest(
  request: Request,
  responseStatusCode: number,
  responseHeaders: Headers,
  remixContext: EntryContext
) {
  return new Promise((resolve, reject) => {
    let shellRendered = false;
    const { pipe, abort } = renderToPipeableStream(
      <RemixServer
        context={remixContext}
        url={request.url}
        abortDelay={ABORT_DELAY}/>,
      {
        onShellReady() {
          shellRendered = true;
          const body = new PassThrough();
          const stream = createReadableStreamFromReadable(body);

          responseHeaders.set('Content-Type', 'text/html');
          responseHeaders.set('Document-Policy', 'js-profiling');

          resolve(
            new Response(stream, {
              headers: responseHeaders,
              status: responseStatusCode,
            })
          );

          pipe(body);
        },
        onShellError(error: unknown) {
          reject(error);
        },
        onError(error: unknown) {
          responseStatusCode = 500;
          // Log streaming rendering errors from inside the shell.  Don't log
          // errors encountered during initial shell rendering since they'll
          // reject and get logged in handleDocumentRequest.
          if (shellRendered) {
            sendErrorToSentry(error, request);

            const currentTime = new Date(Date.now());
            console.error(`${currentTime}: The error is:\n${error}`);
          }
        },
      }
    );

    setTimeout(abort, ABORT_DELAY);
  });
}

type ErrorResponse = {
  status: number;
  statusText: string;
  internal: boolean;
  data: string;
  error: Error;
}
const isErrorResponse = (error: unknown): error is ErrorResponse => {
  return !!(error as ErrorResponse).data || (error as ErrorResponse).internal !== undefined || (error as ErrorResponse).statusText !== undefined;
};

export async function handleError(
  error: unknown,
  { request }: LoaderFunctionArgs
): Promise<void> {

  const currentTime = new Date(Date.now());
  console.error(`${currentTime}: Starting error report.`);
  const session = await getSession(request.headers.get('Cookie'));
  const userSession = session.get('user') as unknown as
    | { userId: string }
    | undefined;

  if (userSession) {
    const userData = await Models.userProfile.getSessionObject(
      userSession.userId
    );
    console.error(`${currentTime}: The current user is:\n${userData.name}\t${userData.workEmail}`);
  }

  if (error instanceof Error) {
    console.error(`${currentTime}: The error is:\n${error.message}`);
  } else if (isErrorResponse(error)) {
    console.error(`${currentTime}: The error is:\n${error.data}`);
  }

  sendErrorToSentry(error, request);

  console.error(`${currentTime}: Error report submitted.`);
}
