import { UserCircleIcon, UserIcon } from '@heroicons/react/24/outline';
import useInView from '@lib/hooks/useInView';
import type { ChecklistItemComments } from '@prisma/client';
import {
  useFetcher,
  useLoaderData,
  useMatches,
  useParams,
} from '@remix-run/react';
import type { GetUserChecklistsResponse } from '@repositories/checklist.server';
import { initOpenTelemetry } from '@sentry/remix';
import { useEffect, useRef, useState } from 'react';

type MessageProps = {
  message: MessageContainerProps['comments'][number];
  containerRef: React.MutableRefObject<HTMLElement | null>;
  loggedInUser: string;
  markRead: () => void;
};
export function Message({
  message,
  containerRef,
  loggedInUser,
  markRead,
}: MessageProps) {
  const itemRef = useRef<null | HTMLLIElement>(null);
  const [isVisible, setIsVisible] = useState(false);
  const [viewStartTime, setViewStartTime] = useState<number | null>(null);

  const isInView = useInView(itemRef, containerRef);
  const [prevRead, setPrevRead] = useState([]);

  const [intervalId, setIntervalId] = useState<number | null>(null);
  const isMounted = useRef(true);

  useEffect(() => {
    isMounted.current = true;
    return () => {
      isMounted.current = false;
      if (intervalId) {
        clearInterval(intervalId);
      }
    };
  }, [intervalId]);

  useEffect(() => {
    if (isVisible && viewStartTime) {
      const id = window.setInterval(() => {
        const viewDuration = Date.now() - viewStartTime;
        if (viewDuration >= 2000 && !message.readBy.includes(loggedInUser)) {
          if (isMounted.current) {
            try {
              markRead();
            } catch (error) {
              console.error('Error in markRead:', error);
            }
          }
          clearInterval(id);
        }
      }, 100);

      setIntervalId(id);
    } else {
      if (intervalId) {
        clearInterval(intervalId);
      }
    }
  }, [isVisible, viewStartTime, message.readBy, loggedInUser, markRead]);

  useEffect(() => {
    if (isInView) {
      if (!isVisible) {
        setIsVisible(true);
        setViewStartTime(Date.now());
      }
    } else {
      if (isVisible) {
        setIsVisible(false);
        setViewStartTime(null);
      }
    }
  }, [isInView, isVisible]);

  const messageClass = () => {
    if (message.postedById === loggedInUser) return 'bg-gray-300';
    if (message.isDenied) return 'bg-red-400';
    if (isInView && message.readBy.includes(loggedInUser)) return 'bg-blue-300';
    if (isInView && !message.readBy.includes(loggedInUser))
      return 'bg-blue-500';
  };

  return (
    <li
      className={`mb-1.5 flex ${message.postedById === loggedInUser ? 'justify-end' : 'justify-start'}`}
      ref={itemRef}>
      {message.postedById === loggedInUser ? (
        <UserCircleIcon className="h-7 w-7 min-w-7 min-h-7 mr-2" />
      ) : (
        <UserIcon className="h-7 w-7 min-w-7 min-h-7 mr-2" />
      )}
      <div>
        <div
          className={`flex w-80 gap-2 px-2 py-0.5 rounded-br-md rounded-tr-md rounded-bl-md ${messageClass()}`}>
          <div className="flex flex-col w-full">
            <div className="flex justify-between items-center">
              <p className="mr-2">{message.postedBy?.name}</p>
              <p className="text-sm text-gray-500">
                {new Date(message.datePosted).toLocaleString()}
              </p>
            </div>
            <span className="" style={{ overflowWrap: 'break-word' }}>
              {message.message}
            </span>
          </div>
        </div>
        {/* <p className="text-end pr-2 text-gray-500 text-[10pt]">Seen</p> */}
      </div>
    </li>
  );
}
type MessageContainerProps = {
  loggedInUser: string;
  comments: ChecklistItemComments[];
  isModal?: boolean;
};
export function MessageContainer({
  loggedInUser,
  comments,
  status,
  isModal = false,
}: MessageContainerProps) {
  const containerRef = useRef(null);
  const markReadFetcher = useFetcher();

  //const loaderData = useLoader();

  //console.log("comments:   ", loaderData);

  function handleMarkRead() {
    let messageId: Array<number> = [];

    //console.log(loggedInUser, comments)

    comments.forEach((comment) => {
      if (!comment.readBy.includes(loggedInUser)) {
        //console.log('mark as read', comment.id)
        messageId.push(comment.id);
      }
    });
    // for(const item of messageId){
    // }
    const formData = new FormData();
    formData.append('commentId', encodeURIComponent(JSON.stringify(messageId)));
    formData.append('userId', loggedInUser);
    formData.append('_action', 'markRead');
    markReadFetcher.submit(formData, { method: 'POST' });
  }

  // useEffect(() => {
  //   handleMarkRead()
  // },[])
  return (
    <div
      className={`${isModal ? 'max-h-[75vh] h-52 ' : 'rounded-xl '}min-h-20 overflow-auto bg-slate-200 shadow-inner border-[1px] border-slate-300`}
      ref={containerRef}>
      <ul className="p-2">
        {!comments || comments.length === 0 ? (
          <li className="flex justify-center items-center h-full text-lg text-gray-500">
            No messages
          </li>
        ) : (
          comments.map((message) => (
            <Message
              key={message.id}
              message={message}
              containerRef={containerRef}
              loggedInUser={loggedInUser}
              markRead={handleMarkRead}/>
          ))
        )}
      </ul>
    </div>
  );
}
