import {
  type UserContextValue,
} from '@components/providers/User';
import ErrorReport from '@components/ui/ErrorReport';
import MainNavBar from '@components/ui/MainNavBar';
import { FolderOpenIcon } from '@heroicons/react/20/solid';
import {
  BuildingLibraryIcon,
  ClipboardDocumentCheckIcon,
  InboxStackIcon,
  PresentationChartLineIcon,
  UserIcon,
  UserCircleIcon,
  ArrowLeftStartOnRectangleIcon,
} from '@heroicons/react/24/solid';
import type { NavBarLink } from '@lib/types';
import { Attribute, RolePermissions } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json, redirect } from '@remix-run/node';
import { Outlet, useLoaderData, useLocation } from '@remix-run/react';
import {
  getRoles,
  hasCSSRole,
} from '@routes/_withNav+/_withSideBar+/.server/tasks.queries';
import { requireUserAsObject } from '@services/user.server';
import { useEffect } from 'react';
import { Slide, toast as notify, ToastContainer } from 'react-toastify';
import { getToast } from 'remix-toast';
import { AuthorizationError } from 'remix-auth';
import { isSuperAdmin } from '@lib/utils/utils.server';
import { getRootTree, getRootDescendants, getRootNode } from './_withSideBar+/_manage+/.server/manage.queries';
import { getOrgBeingViewed, getOrgDescendants } from './_withSideBar+/_manage+/.server/org.queries';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  try {
    const {
      currentOrg: org,
      roles,
      permissions,
      ...user
    } = await requireUserAsObject(request);

    let parentNode, orgFlatArray;

    if (isSuperAdmin(roles)) {
      parentNode = await getRootTree();
      orgFlatArray = await getRootDescendants();
    } else {
      if (org) {
        parentNode = await getRootNode(org.uuid);
        orgFlatArray = await getOrgDescendants(org.uuid);
      }
    }

    const UserContextData: UserContextValue = {
      org,
      permissions,
      roles,
      user: {
        name: user.name,
        image: user.image,
        rank: user.rank,
        id: user.id,
        primaryEmail: user.workEmail,
      },
    };

    let orgRoles:
      | Map<string, { id: string; name: string; description: string }>
      | undefined;

    if (hasCSSRole(roles)) {
      orgRoles = await getRoles(org.uuid);
    } else {
      orgRoles = await getRoles(
        org.uuid,
        roles.map((role) => role.abbreviation)
      );
    }

    const { toast, headers } = await getToast(request);
    // TODO: start using useMatches hook where possible to use this data here instead of grabbing the session in EVERY route.
    return json(
      { orgRoles: Array.from(orgRoles), toast, parentNode, orgFlatArray, ...UserContextData },
      {
        headers: {
          ...headers,
          'X-Frame-Options': 'SAMEORIGIN',
        },
      }
    );
  } catch (e) {
    if (e instanceof AuthorizationError) return redirect('/signout');
    else throw e;
  }
};

function filterRoles(
  roles: {
    id: string;
    name: string;
    description: string;
    abbreviation: string;
    owningOrgId: string;
    roleId: string;
  }[]
) {
  const filteredRoles = roles.filter(
    (orgRole) =>
      orgRole.abbreviation !== 'ADMIN' && orgRole.abbreviation !== 'SUPERADMIN'
  );

  const hasRoles = filteredRoles && filteredRoles.length > 0;
  return { filteredRoles, hasRoles };
}

export const profileMenuLinks = [
  /* Menu items go here */
  { name: 'Profile', href: '/profile' },
  { name: 'Sign out', href: '/signout' },
];

const DefaultLayoutWithNavbar = () => {
  const { toast, ...data } = useLoaderData<typeof loader>();

  useEffect(() => {
    if (toast) notify(toast.message, { type: toast.type });
  }, [toast]);

  if (!data) throw new Error('Missing data');

  const { user, org, roles, permissions } = data;

  const { filteredRoles, hasRoles } = filterRoles(roles);

  const orgRoleHref = hasRoles ? `/tasks/${filteredRoles[0]?.roleId}` : '/tasks';

  const links: NavBarLink[] = [
    {
      href: `/checklist`,
      text: 'Your Checklist',
      icon: ClipboardDocumentCheckIcon,
    },
    {
      href: orgRoleHref,
      text: 'Task Signing',
      permissionRequired: RolePermissions.APPROVER,
      icon: InboxStackIcon,
      hasSideNav: true,
      activeText: ['tasks'],
      shouldShow: hasRoles,
    },
    {
      href: `/metrics`,
      text: 'Metrics Dashboard',
      permissionRequired: Attribute.METRICS_READ,
      icon: PresentationChartLineIcon,
    },
    {
      href: `/user`,
      text: 'Administration',
      permissionRequired: [Attribute.ORGS_READ, Attribute.TEMPLATES_READ],
      icon: BuildingLibraryIcon,
      activeText: ['org', 'user', 'template', 'role', 'base', 'onboard'],
      hasSideNav: true,
    },
    {
      href: `/profile`,
      text: 'Profile',
      icon: UserCircleIcon,
    },
    {
      href: '/signout',
      text: 'Logout',
      icon: ArrowLeftStartOnRectangleIcon,
    },
    {
      href: 'https://travisspark.atlassian.net/servicedesk/customer/portal/4',
      text: 'Feedback',
      icon: FolderOpenIcon,
    },
  ];

  const location = useLocation();
  let width = 'grid-cols-[auto_300px_1fr]';

  if (location.pathname.includes('checklist') || location.pathname.includes('metrics') || location.pathname.includes('profile')) {
    width = 'grid-cols-[auto_1fr]';
  }
  return (
    // <UserProvider value={{ user, org, roles, permissions }}>
    <div className={`grid ${width}`}>
      <MainNavBar links={links} userPermissions={permissions} />
      <Outlet />
      <ToastContainer
        transition={Slide}
        position="top-center"
        autoClose={1000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss={false}
        draggable
        pauseOnHover
        theme="light"
        limit={3}/>
    </div>
    // </UserProvider>
  );
};

export const ErrorBoundary = ErrorReport;
export default DefaultLayoutWithNavbar;
