import ErrorReport from '@components/ui/ErrorReport';
import { Form, Input, Label } from '@components/ui';
import { Modal } from '@components/ui/modals';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { getOrgRole } from './_manage+/.server/role.queries';

export { roleEditActionFunction as action } from './_manage+/.server/role.queries';

export const loader = async ({ params, request }: LoaderFunctionArgs) => {
  const { orgRoleId: roleId } = params;

  const orgRoleId = (new URL(request.url)).searchParams.get('orgrole');

  if (!orgRoleId) {
    throw json({ status: 'error', error: 'roleId is required.' });
  }

  const orgRole = await getOrgRole(orgRoleId);
  return json({ orgRole });
};

const EditOrgDefaultPage = () => {
  const { orgRole } = useLoaderData<typeof loader>();

  return (
    <Modal>
      <Form method="POST">
        <Modal.Body>
          <Modal.Title>Edit Contact Info:</Modal.Title>
          <Input.Hidden name="orgRoleId" value={orgRole.uuid} />
          <Form.Row>
            <Label htmlFor="contactPhone">Phone:</Label>
            <Input.Text name="contactPhone" defaultValue={orgRole.contactPhone} />
          </Form.Row>
          <Form.Row>
            <Label htmlFor="contactEmail">Email:</Label>
            <Input.Text name="contactEmail" defaultValue={orgRole.contactEmail} />
          </Form.Row>
        </Modal.Body>
        <Modal.Buttons link="/tasks" value="edit">
          Save
        </Modal.Buttons>
      </Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditOrgDefaultPage;


