export const isInputType = (target: unknown): target is HTMLInputElement =>
  target instanceof HTMLInputElement;

export const isSelectType = (target: unknown): target is HTMLSelectElement =>
  target instanceof HTMLSelectElement;

/**
 * A generic function to determine if a value is in the given enum
 * @param {Record<string, E>} strEnum An enum that has strings as values
 * @returns {(value: (string | string[])) => value is E[] | E}
 */
export const isValueInEnum = <E extends string>(
  strEnum: Record<string, E>
): ((value: string | string[]) => value is E[] | E) => {
  if (!strEnum) throw new Error('no enum');
  const enumValues = Object.values(strEnum) as string[];

  return (value: string | string[]): value is E | E[] => {
    if (!value) throw new Error('in the callback');
    if (Array.isArray(value)) return enumValues.includes(value[0]);
    return enumValues.includes(value);
  };
};

export function isPrismaKnownError(
  err: unknown
): err is Prisma.PrismaClientKnownRequestError {
  return !!(err as Prisma.PrismaClientKnownRequestError).code;
}

