// Verifies obj is of type { roles, permissions, ...user data}
export function isUserSessionData(obj: unknown): obj is UserSessionData {
  return (
    Array.isArray((obj as UserSessionData).roles) &&
    (obj as UserSessionData).roles.every(
      (role: any) => typeof role === 'object' && typeof role.name === 'string'
    ) && // Check if roles array contains objects with a 'name' property of type string
    typeof (obj as UserSessionData).permissions === 'string' &&
    typeof (obj as UserSessionData).email === 'string' &&
    typeof (obj as UserSessionData).id === 'string'
  );
}

// Verifies obj is of type { roles, permissions, user }
export function isUserLayoutData(obj: unknown): obj is SessionData {
  return (
    Array.isArray((obj as SessionData).roles) &&
    (obj as SessionData).roles.every(
      (role: any) => typeof role === 'object' && typeof role.name === 'string'
    ) && // Check if roles array contains objects with a 'name' property of type string
    typeof (obj as SessionData).permissions === 'string' &&
    typeof (obj as SessionData).user.workEmail === 'string' &&
    typeof (obj as SessionData).user.id === 'string'
  );
}

export enum PasswordError {
  None,
  Spaces,
  Length,
  Mismatch,
}

export function isValidPassword(password: string, comparePassword?: string):  PasswordError[] {
  const errors = [];
  if (comparePassword)
    if (comparePassword !== '' &&
        comparePassword !== password) errors.push(PasswordError.Mismatch);

  let hasNoSpaces = !/\s/.test(password);
  if (!hasNoSpaces) errors.push(PasswordError.Spaces);

  const isLongEnough = password.length >= 10;
  if (!isLongEnough) errors.push(PasswordError.Length);

  return errors;
}

// Helper function to get the display name for a rank
export const getRankDisplay = (
  rank: Rank,
  useAbbreviation: boolean = false
): string => {
  return useAbbreviation ? UserRank[rank].abb : UserRank[rank].name;
};
