import type { Models } from '@lib/models.server';
import type { Organization } from '@prisma/client';
import { useMatches } from '@remix-run/react';
import type { OrganizationWithChildren } from '@repositories/organization.server';

export type RootLayoutData = {
  user: {
    name: string;
    image: string | null;
    rank: string;
    id: string;
    primaryEmail: string;
  };
  roles: Array<{
    abbreviation: string;
    description: string;
    id: string;
    name: string;
    owningOrgId: string;
    roleId: string;
  }>;
  permissions: string;
  org: Organization;
  parentNode: OrganizationWithChildren,
  orgFlatArray: Array<Organization>
};

function isValidReturnKey(returnKey: unknown): returnKey is keyof RootLayoutData {
  return ['user', 'roles', 'permissions', 'org', 'parentNode', 'orgFlatArray'].includes(returnKey);
}

function isRootLayoutData(data: unknown): data is RootLayoutData {
  return !!(data as RootLayoutData).user && !!(data as RootLayoutData).roles && !!(data as RootLayoutData).permissions && !!(data as RootLayoutData).org;
}

type DataOrKeyValue<T extends keyof RootLayoutData | string> = T extends keyof RootLayoutData ? RootLayoutData[T] : RootLayoutData

export function useRootLayoutData<K extends keyof RootLayoutData | string>(
  returnKey?: K
): DataOrKeyValue<K> {
  const data = useMatchesData('routes/_withNav+/_layout');
  let errMsg = '';

  if (!data || !isRootLayoutData(data)) {
    // Handle case when data is not available
    errMsg = 'No layout data available.';
    console.error(errMsg);
    throw new Error(errMsg);
  }

  if (!returnKey) {
    return data as DataOrKeyValue<K>;
  }

  if (!isValidReturnKey(returnKey)) {
    errMsg = `Invalid key ${returnKey}.`;
    console.error(errMsg);
    throw new Error(errMsg);
  }

  return (data[returnKey] as DataOrKeyValue<K>);
}

export function useManageLayoutData() {
  const data = useMatchesData('routes/_withNav+/_withSideBar+/_manage+/_layout');

  return (
      data as {
        parentNode: OrganizationWithChildren,
        orgFlatArray: Array<Organization>,
        orgBeingViewed: Organization;
        orgSelected: Organization;
      }
  );
}
export type ChecklistLayoutData = {item: Awaited<ReturnType<typeof Models.checklistItem.getWithComments>>[0]; comments: Awaited<ReturnType<typeof Models.checklistItem.getWithComments>>[1]; loggedInUser: string;}

export function useChecklistLayoutData() {
  const data = useMatchesData('routes/_withNav+/_noSideBar+/checklist.$itemId+/_layout');
  return (
    data as ChecklistLayoutData
  );
}

export function useManageTemplateData() {
  const data = useMatchesData('routes/_withNav+/_withSideBar+/_manage+/template');

  type TemplateType = {
    id: string,
    createdAt: string,
    updatedAt: string,
    isDeleted: boolean,
    createdBy: string,
    updatedBy: string,
    name: string,
    abbreviation: string,
    isWingLevel: boolean,
    parentOrgId: string,
    parentId: string,
    baseId: string,
    templates: Array<{
      id: string,
      name: string,
      description: string,
      isDeleted: boolean,
      organizationId: string,
      type: string,
    }>;
  }

  return (
      data as {
        orgBeingViewed: TemplateType;
      }
  );
}

export default function useMatchesData(id: string) {
  const matches = useMatches();
  const match = matches.find(
    (match) => match.id === id
  );
  if (match?.data)
    return match.data;
}
