import type { GetUserChecklistsResponse } from '@repositories/checklist.server';
import { useState } from 'react';

export function CompleteChecklists( {
  checklists,
}: {
  checklists: GetUserChecklistsResponse;
} ) {
  const [ expandedChecklistId, setExpandedChecklistId ] = useState<string | null>(
    null
  );
  const toggleDropdown = ( checklistId: string ) => {
    setExpandedChecklistId(
      expandedChecklistId === checklistId ? null : checklistId
    );
  };

  const filtered = checklists.filter( ( c ) => c.isComplete );

  if ( filtered.length > 0 ) {
    return (
      <div className="mb-20">
        <span className="text-xl rounded-lg">Completed Checklists</span>
        <div className="overflow-x-auto pb-6 pt-4">
          <div className="grid grid-cols-3 text-left align-middle bg-gray-200">
            <p className="px-4 py-2 text-left font-bold">Checklist Name</p>
            <p className="px-4 py-2 text-left font-bold">Date Assigned</p>
            <p className="px-4 py-2 text-left font-bold">Date Completed</p>
          </div>
          {/* TODO: Instead of loading every complete checklist the member has ever done we could use either another page or eager load it once they click the expand button. */ }
          { filtered.map( ( checklist ) => (
            <div key={ checklist.id } className="bg-gray-100">
              <button
                className="grid grid-cols-3 text-left w-full"
                onClick={ () => toggleDropdown( checklist.id ) }>
                <p className="px-4 py-2">{ checklist.name }</p>
                <p className="px-4 py-2">
                  { new Date( checklist.createdAt ).toLocaleString() }
                </p>
                <p className="px-4 py-2">
                  { new Date( checklist.dateCompleted ).toLocaleString() }
                </p>
              </button>

              { expandedChecklistId === checklist.id && (
                <div className="bg-gray-200 p-4">
                  { checklist.items.map( ( item ) => (
                    <div
                      key={ item.id }
                      className="grid grid-cols-3 py-1 odd:bg-zinc-300 first:rounded-t-lg px-3 last:rounded-b-lg">
                      <p>Task Item: { item.reference.name ?? item.name }</p>
                      <p className="ml-3">
                        Completed On:{ ' ' }
                        { new Date( checklist.dateCompleted ).toLocaleString() }
                      </p>
                      <p className="ml-6">Approved By: { item.role.name }</p>
                    </div>
                  ) ) }
                </div>
              ) }
            </div>
          ) ) }
        </div>
      </div>
    );
  }

  return (
    <div className="flex border-b border-1 border-neutral-200 py-5 gap-5 justify-between mb-10">
      <h2 className="text-xl">No Completed Checklists.</h2>
    </div>
  );
}
