/* eslint-disable import/prefer-default-export */
import type { ChecklistWithItems } from '@lib/constants';
import type {
  OrganizationWithSubAndMembers,
  OrgMemberChecklist,
} from '@lib/types';
import {
  ChecklistStatus,
  Prisma} from '@prisma/client';
import type {
  ChecklistItemStatus,
  Checklist,
  ChecklistItem,
  PrismaClient,
} from '@prisma/client';
import type { ChecklistDto } from './dtos';
import type { IRepository } from './Repository.server';

type Contact = {
  phone: string;
  email: string;
};

type Role = {
  name: string;
  abbreviation: string;
  contact: Contact;
};

type ItemComment = {
  id: number;
  message: string | null;
  postedById: string | null;
  postedBy: { name: string } | null;
  datePosted: string;
  readBy: string[];
  isDenied: boolean;
};

type ChecklistItemResponse = {
  id: string;
  templateItemId: string;
  name: string;
  description: string;
  checklistId: string;
  userComplete: boolean;
  isComplete: boolean;
  isDenied: boolean;
  dateCompleted: string | null;
  completedById: string | null;
  completedBy?: { name: string };
  updatedById: string | null;
  createdAt: string;
  updatedAt: string;
  forDormResidents: boolean;
  forHasDependents: boolean;
  forHasEFMP: boolean;
  role: Role;
  comments: ItemComment[];
  status: ChecklistItemStatus;
  reference: {
    name: string | null;
    description: string | null;
  };
};

export type GetUserChecklistsResponse = Array<{
  id: string;
  isComplete: boolean;
  dateCompleted: string;
  name: string;
  description: string;
  createdAt: string;
  items: ChecklistItemResponse[];
}>;

interface IChecklistRepository
  extends IRepository<Checklist, ChecklistDto, ChecklistDto> {
  getAllInUsers(): void;

  getUsers(): void;

  getWithItems(id: string): Promise<any>;

  getUserChecklists(userId: string): Promise<GetUserChecklistsResponse>;

  getByUserAndTemplate(userId: string, templateId: string): Promise<any>;

  getChildOrgChecklists(orgId: string): Promise<OrgMemberChecklist[]>;

  countAll(): Promise<number>;
}

export class ChecklistRepository implements IChecklistRepository {
  public prisma;

  constructor(prismaClient: PrismaClient) {
    this.prisma = prismaClient;
  }

  public getAllInUsers() {
    throw new Error('Method not implemented.');
  }

  public getUsers() {
    throw new Error('Method not implemented.');
  }

  public async getAll(): Promise<Checklist[]> {
    throw new Error('Method not implemented.');
  }

  public async getChecklistItems(id: string) {
    return this.prisma.checklist.findFirstOrThrow({
      where: { id },
      include: {
        user: true,
        items: {
          orderBy: {
            isComplete: 'asc',
          },
          include: {
            templateItem: {
              include: { requiredRole: true , reference: true},
            },
          },
        },
      },
    });
  }
  public async getChecklistsCount(templateId: string) {
    return this.prisma.checklist.count({
      where: {
        templateId: templateId,
        isComplete: false,
      },
    });
  }

  public async create({
    templateId,
    templateItems,
    assignedById,
    userId,
    name,
  }: ChecklistDto): Promise<string> {
    const userItems = templateItems.map((item) => ({
      updatedAt: new Date(Date.now()),
      templateItemId: item.uuid,
    }));

    return (
      await this.prisma.checklist.create({
        data: {
          userId,
          updatedAt: new Date(Date.now()),
          assignedById,
          name,
          templateId,
          items: {
            createMany: {
              data: userItems,
            },
          },
        },
        select: {
          id: true,
        },
      })
    ).id;
  }

  public async update(id: string, data: ChecklistDto): Promise<void> {
    throw new Error('Method not implemented.');
  }

  // @ts-ignore
  public async delete(id: string, userId: string): Promise<void> {
    const items = await this.prisma.checklistItem.findMany({
      where: {
        checklistId: id,
      },
    });

    const deleteChecklist = this.prisma.checklist.delete({
      where: {
        id,
      },
    });

    const updateUser = this.prisma.userProfile.update({
      where: { id: userId },
      data: {
        moveType: null,
        updatedAt: new Date(Date.now()),
      },
    });

    if (items.length > 0) {
      const deleteItems = this.prisma.checklistItem.deleteMany({
        where: {
          id: {
            in: items.map((item: ChecklistItem) => item.id),
          },
        },
      });

      await this.prisma.$transaction([
        deleteItems,
        deleteChecklist,
        updateUser,
      ]);
      return;
    }

    await this.prisma.$transaction([deleteChecklist, updateUser]);
  }

  public async getWithItems(id: string) {
    return this.prisma.checklist.findFirstOrThrow({
      where: { id },
      include: {
        items: true,
      },
    });
  }

  public async getUserChecklists(userId: string) {
    // Need to finish migration to new many to many org -> roles w/ orgsroles table
    const newQuery = Prisma.sql`
        WITH checklist_items AS (SELECT ci.id                                     AS ci_id,
                                        ci."templateItemId"                       AS ci_template_item_id,
                                        ci."checklistId"                          AS checklist_id,
                                        ci."userComplete"                         AS ci_user_complete,
                                        ci."isComplete"                           AS ci_is_complete,
                                        ci."dateCompleted"                        AS ci_date_completed,
                                        ci."completedById"                        AS ci_completed_by_id,
                                        ci."updatedById"                          AS ci_updated_by_id,
                                        ci."createdAt"                            AS ci_created_at,
                                        ci."updatedAt"                            AS ci_updated_at,
                                        ci."isDenied" AS ci_is_denied,
                                        ci."status" AS ci_status,
                                        ti.name                                   AS ti_name,
                                        tiRef.name                                AS tiRef_name,
                                        tiRef.description                         AS tiRef_desc,
                                        ti.description                            AS ti_description,
                                        ti."forDormResidents"                     AS ti_dorm,
                                        ti."forHasDependents"                     AS ti_depend,
                                        ti."forHasEFMP"                           AS ti_efmp,
                                        r.name                                    AS r_name,
                                        r.abbreviation                            AS r_abbrev,
                                        osrs."contactEmail"                       AS role_email,
                                        osrs."contactPhone"                       AS role_phone,
                                        (SELECT json_agg(
                                                        json_build_object(
                                                                'id', cic.id,
                                                                'postedById', cic."postedById",
                                                                'message', cic.message,
                                                                'datePosted', cast(cic."datePosted" AS varchar) || 'Z',
                                                                'checklistItemId', cic."checklistItemId",
                                                                'internal', cic.internal,
                                                                'postedBy', json_build_object(
                                                                        'name', cic.name
                                                                            ),
                                                                'readBy', cic."readBy"
                                                        )
                                                )
                                         FROM (SELECT cic.id,
                                                      cic."postedById",
                                                      cic.message,
                                                      cic."datePosted",
                                                      cic."checklistItemId",
                                                      cic.internal,
                                                      cic."readBy",
                                                      profile.name
                                               FROM "ChecklistItemComments" cic
                                                        LEFT JOIN "UserProfile" profile ON cic."postedById" = profile.id
                                               WHERE cic.internal = false
                                                 AND cic."checklistItemId" = ci.id
                                               ORDER BY cic."datePosted") AS cic) AS comments
                                 FROM "ChecklistItem" AS ci
                                          JOIN "Checklist" c on c.id = ci."checklistId"
                                          JOIN "Template" t ON t.id = c."templateId"
                                          JOIN "UserProfile" u ON c."userId" = u.id
                                          JOIN "TemplateItem" ti ON ci."templateItemId" = ti.uuid
                                          LEFT JOIN "TemplateItem" tiRef ON tiRef.uuid = ti."referenceId"
                                          JOIN "Role" r ON ti."requiredRoleId" = r.uuid
                                          JOIN "OrganizationsRoles" osrs
                                               ON CASE WHEN r.level = 'UNIT' THEN osrs."organizationId" = t."organizationId" AND osrs."roleId" = r.uuid ELSE osrs."roleId" = r.uuid END
                                 WHERE u.id = ${ userId }
                                 GROUP BY ci.id, ci."templateItemId", ci."checklistId", ci."userComplete",
                                          ci."isComplete", ci."dateCompleted", ci."completedById", ci."updatedById",
                                          ci."createdAt", ci."updatedAt", tiref.name, tiRef_desc, ti.name,
                                          ti.description, ti."forDormResidents", ti."forHasDependents", ti."forHasEFMP",
                                          r.name, r.abbreviation, osrs."contactEmail", osrs."contactPhone")
        SELECT c.id              AS id,
               c."isComplete"    AS "isComplete",
               c."dateCompleted" AS "dateCompleted",
               c."createdAt"     AS "createdAt",
               t."name"          AS name,
               t."description"   AS description,
               json_agg(
                       json_build_object(
                               'id', ci_id,
                               'templateItemId', ci_template_item_id,
                               'name', ti_name,
                               'reference', json_build_object(
                                       'name', tiRef_name,
                                       'description', tiRef_desc
                                            ),
                               'description', ti_description,
                               'isDenied', ci_is_denied,
                               'status', ci_status,
                               'checklistId', checklist_id,
                               'userComplete', ci_user_complete,
                               'isComplete', ci_is_complete,
                               'dateCompleted', ci_date_completed,
                               'completedById', ci_completed_by_id,
                               'updatedById', ci_updated_by_id,
                               'createdAt', ci_created_at,
                               'updatedAt', ci_updated_at,
                               'forDormResidents', ti_dorm,
                               'forHasDependents', ti_depend,
                               'forHasEFMP', ti_efmp,
                               'role',
                               json_build_object(
                                       'name', r_name,
                                       'abbreviation', r_abbrev,
                                       'contact',
                                       json_build_object(
                                               'phone', role_phone,
                                               'email', role_email
                                       )
                               ),
                               'comments', comments
                       )
               )                 AS items
        FROM "Checklist" c
                 JOIN
             checklist_items ci ON c.id = ci.checklist_id
                 JOIN
             "Template" t ON t.id = c."templateId"
        GROUP BY c.id, c."isComplete", c."dateCompleted", c."createdAt", t."name", t."description";
    `;

    return this.prisma.$queryRaw<GetUserChecklistsResponse>(newQuery);
  }

  public async getAllChecklists(): Promise<Checklist[]> {
    return this.prisma.checklist.findMany();
  }

  public async getByUserAndTemplate(userId: string, templateId: string) {
    return this.prisma.checklist.findFirstOrThrow({
      where: {
        userId,
        templateId,
      },
      include: {
        items: true,
      },
    });
  }

  /**
   * Retrieves checklists for a given organization and its sub-organizations.
   *
   * @async
   * @function getChildOrgChecklists
   * @param {string} orgId - The ID of the organization to retrieve checklists for.
   * @returns {Promise<OrganizationWithSubAndMembers['currentMembers'][0]['checklists'][]>} A Promise that resolves to an array of checklists with items and user information.
   * @throws {Error} If there is an error while fetching the checklists.
   *
   * @example
   * const orgId = "yourOrgId";
   * try {
   *   const checklists = await getChildOrgChecklists(orgId);
   *   console.log(checklists);
   * } catch (error) {
   *   console.error(`Error fetching checklists: ${error.message}`);
   * }
   */
  public async getChildOrgChecklists(
    orgId: string
  ): Promise<OrgMemberChecklist[]> {
    const topOrg: OrganizationWithSubAndMembers | null =
      await this.prisma.organization.findFirst({
        where: { uuid: orgId },
        select: {
          subOrgs: {
            select: {
              uuid: true,
            },
          },
          currentMembers: {
            include: {
              checklists: {
                include: {
                  user: {
                    select: {
                      id: true,
                      workEmail: true,
                      name: true,
                      rank: true,
                      currentOrg: {
                        select: {
                          id: true,
                          name: true,
                          abbreviation: true,
                        },
                      },
                    },
                  },
                  items: {
                    include: {
                      comments: {
                        include: {
                          postedBy: true,
                        },
                      },
                      templateItem: {
                        include: {
                          template: true,
                          requiredRole: true,
                        },
                      },
                    },
                  },
                },
              },
            },
          },
        },
      });

    let checklists: OrgMemberChecklist[] = [];

    if (topOrg) {
      for (const user of topOrg.currentMembers) {
        if (user.checklists.length > 0) checklists.push(user.checklists);
      }

      for (const subOrg of topOrg.subOrgs) {
        const result = await this.getChildOrgChecklists(subOrg.uuid);
        checklists.push(...result);
      }
    }

    return checklists;
  }

  public async countAll() {
    return this.prisma.checklist.count({
      where: { isComplete: false },
    });
  }

  public async pause(
    checklistId: string,
    userId: string
  ): Promise<void> {

    await this.prisma.checklist.update({
      where: { id: checklistId },
      data: {
        status: ChecklistStatus.Paused,
        updatedById: userId,
        pausedDate: new Date(Date.now()),
        updatedAt: new Date(Date.now()),
      },
    });
  }

  public async resume(
    checklistId: string,
    userId: string
  ): Promise<void> {

    await this.prisma.checklist.update({
      where: { id: checklistId },
      data: {
        status: ChecklistStatus.InProgress,
        updatedById: userId,
        archivedDate: null,
        isDeleted: false,
        pausedDate: null,
        updatedAt: new Date(Date.now()),
      },
    });
  }

  public async archive(
    checklistId: string,
    userId: string
  ): Promise<void> {

    await this.prisma.checklist.update({
      where: { id: checklistId },
      data: {
        status: ChecklistStatus.Archived,
        updatedById: userId,
        archivedDate: new Date(Date.now()),
        updatedAt: new Date(Date.now()),
      },
    });
  }

}
