import ErrorReport from '@components/ui/ErrorReport';
import { Input, Form, Label } from '@components/ui';
import { Modal } from '@components/ui/modals';
import { RTEProvider } from '@components/ui/RichText/Editor';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData, useParams } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import type { editTemplateItemLoader} from './.server/template.queries';
import { updateTemplateItem } from './.server/template.queries';

export { editTemplateItemLoader as loader } from './.server/template.queries';

export const action = async ({
  request,
  params,
}: ActionFunctionArgs) => {
  const { templateId } = params;
  if (!templateId) throw json({ error: 'No checklist id.' });

  const itemEditSchema = z.object({
    id: z.string().min(1),
    name: z.string().optional(),
    description: z.string(),
    requiredRole: z.string().min(1),
    forHasDependents: z.union([
      z.literal('on').transform(() => true),
      z.literal(undefined).transform(() => false),
    ]),
    forDormResidents: z.union([
      z.literal('on').transform(() => true),
      z.literal(undefined).transform(() => false),
    ]),
    forHasEFMP: z.union([
      z.literal('on').transform(() => true),
      z.literal(undefined).transform(() => false),
    ]),
  });

  try {
    await requireUser(request, { permissions: Attribute.TEMPLATES_READWRITE });
    const {
      id,
      name,
      description,
      requiredRole: requiredRoleId,
      forHasDependents,
      forHasEFMP,
      forDormResidents,
    } = itemEditSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    await updateTemplateItem(id, {
      name,
      description,
      requiredRoleId,
      forHasDependents,
      forHasEFMP,
      forDormResidents,
    });
  } catch (err) {
    console.error(err);
    if (err instanceof AuthorizationError) {
      return jsonWithError(
        { status: 'error', error: 'Authorization Error.' },
        'User is not authorized.'
      );
    }
    if (err instanceof ZodError) {
      err.issues.forEach((issue) =>
        console.error(
          `There was an error validating on item update!\n${issue.path} - ${issue.message}`
        )
      );
      return jsonWithError(
        {
          status: 'error',
          error: {
            message: 'There was an error editing the item.',
            items: err.issues,
          },
        },
        'There was an error.'
      );
    }

    return jsonWithError(
      {
        status: 'error',
        error: 'There was an error editing the item.',
      },
      'There was an error.'
    );
  }

  return redirectWithSuccess(`/template/${templateId}`, 'Item saved!');
};

const EditTemplateItem = () => {
  const { templateId, itemId } = useParams();
  const { item, roles } = useLoaderData<typeof editTemplateItemLoader>();

  return (
    <Modal>
      <RTEProvider>
        <Form method="post">
          <Input.Hidden name="templateId" value={templateId} />
          <Input.Hidden name="id" value={itemId} />
          <Modal.Body>
            <Modal.Title>Create New Item</Modal.Title>
            <Form.Row>
              {item.reference && <p>{item.reference.name}</p>}
              {!item.reference && (<>
                <Label htmlFor="name">Name</Label>
                <Input.Text name="name" defaultValue={item.name} /></>
              )}
            </Form.Row>
            <Form.Row>
              <Label htmlFor="description">Description</Label>
              <Input.RichText content={item.description} limit={1000} name="description"></Input.RichText>
            </Form.Row>
            <Form.Row>
              <Label>Role Required</Label>
              <Input.Select
                name="requiredRole"
                defaultValue={item.requiredRoleId}
                options={roles}
                valueKey="uuid" />
            </Form.Row>
            <Form.Row>
              <Label.Heading>Questions</Label.Heading>
              <Input.CheckboxGroup>
                <Input.Checkbox
                  label="For Members that are Dorm Residents"
                  defaultChecked={item.forDormResidents ?? false}
                  name="forDormResidents"/>
                <Input.Checkbox
                  label="For Members with Dependents"
                  defaultChecked={item.forHasDependents ?? false}
                  name="forHasDependents"/>
                <Input.Checkbox
                  label="For Members with Dependents that qualify for EFMP"
                  defaultChecked={item.forHasEFMP ?? false}
                  name="forHasEFMP"/>
              </Input.CheckboxGroup>
            </Form.Row>
          </Modal.Body>
          <Modal.Buttons link={`/template/${templateId}`} value="edit">
          Save
          </Modal.Buttons>
        </Form>
      </RTEProvider>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditTemplateItem;