import React, { createContext, useContext, useState, useRef } from 'react';

interface BreadcrumbsContextType {
  manualSet: boolean;
  setManualSet: (value: boolean) => void;
  orgId: React.RefObject<string>;
}

const BreadcrumbsContext = createContext<BreadcrumbsContextType | undefined>(
  undefined
);

export function BreadcrumbsProvider({
  children,
  initialOrgId,
}: {
  children: React.ReactNode;
  initialOrgId: string;
}) {
  const [manualSet, setManualSet] = useState(false);
  const orgId = useRef(initialOrgId);

  const value = React.useMemo(
    () => ({
      manualSet,
      setManualSet,
      orgId,
    }),
    [manualSet]
  );

  return (
    <BreadcrumbsContext.Provider value={value}>
      {children}
    </BreadcrumbsContext.Provider>
  );
}

export function useBreadcrumbs() {
  const context = useContext(BreadcrumbsContext);
  if (context === undefined) {
    throw new Error('useBreadcrumbs must be used within a BreadcrumbsProvider');
  }
  return context;
}
