import ChecklistView from '@components/Checklist/View';
import { Badge, type BadgeProps } from '@components/ui/Badge';
import { StartTimer } from '@components/ui/Icons';
import { useRootLayoutData } from '@lib/hooks/useMatchesData';
import type { GetUserChecklistsResponse } from '@repositories/checklist.server';

export function InProgressChecklists( {
  checklists,
}: {
  checklists: GetUserChecklistsResponse;
} ) {
  const user = useRootLayoutData( 'user' );

  const filtered = checklists.filter( ( c ) => !c.isComplete );



  if ( filtered.length > 0 )
    return (
      <div className="mt-3">
        <h1 className="text-xl px-2 py-1 rounded-xl">
          In Progress Checklist
        </h1>
        { filtered.map( ( checklist ) => (
          <ChecklistView
            key={ checklist.id }
            checklist={ checklist }
            loggedInUser={ user.id }/>
        ) ) }
      </div>
    );

  return (
    <div className="flex border-b border-1 border-neutral-200 py-5 gap-5 justify-between mb-10">
      <h3 className="text-xl">No Active Checklists.</h3>
    </div>
  );
}
