import { forwardRef } from 'react';

export const Heading1 = forwardRef<
  HTMLHeadingElement,
  React.HTMLAttributes<HTMLHeadingElement>
>(function Heading1(props, ref) {
  return (
    <h1
      {...props}
      ref={ref}
      className={`${props.className ? `${props.className} ` : ''}text-2xl text-gray-900`}
    />
  );
});

export const Heading2 = forwardRef<
  HTMLHeadingElement,
  React.HTMLAttributes<HTMLHeadingElement>
>(function Heading1(props, ref) {
  return (
    <h2
      {...props}
      ref={ref}
      className={`${props.className ? `${props.className} ` : ''}text-xl text-gray-900`} />
  );
});
