import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/outline';
import { useState, useMemo, useEffect } from 'react';
import { useSearchParams } from '@remix-run/react';

export function usePagination(items: any[], initialPerPage: number) {
  const [currentItems, setCurrentItems] = useState<any[]>([]);
  const [currentParams, setSearchParams] = useSearchParams();
  const pageParam = currentParams.get('page');
  let page = 1;
  if (pageParam) {
    page = Number.parseInt(pageParam);
  }
  const [currentPage, setCurrentPage] = useState<number>(page);
  const itemParam = currentParams.get('items');
  let itemsToShow = initialPerPage;
  if (itemParam) {
    itemsToShow = Number.parseInt(itemParam);
  }
  const [itemsPerPage, setItemsPerPage] = useState<number>(itemsToShow);

  function setPageParam(page: number) {
    setSearchParams((prev) => {
      prev.set('page', String(page));
      return prev;
    });
  }

  function setItemParam(toShow: number) {
    setSearchParams((prev) => {
      prev.set('items', String(toShow));
      return prev;
    });
  }

  useEffect(() => {
    if (pageParam) setCurrentPage(Number.parseInt(pageParam));
    else setCurrentPage(1);
  }, [pageParam]);

  useEffect(() => {
    if (itemParam) {
      setItemsPerPage(Number.parseInt(itemParam));
      setPageParam(1);
    } else setItemsPerPage(initialPerPage);
  }, [itemParam, initialPerPage]);

  const totalItemsLength = items.length;

  // Memoize derived values to avoid unnecessary recalculations
  const totalPages = useMemo(
    () => Math.ceil(totalItemsLength / itemsPerPage),
    [totalItemsLength, itemsPerPage]
  );
  const indexOfLastItem = useMemo(
    () => currentPage * itemsPerPage,
    [currentPage, itemsPerPage]
  );
  const indexOfFirstItem = useMemo(
    () => indexOfLastItem - itemsPerPage,
    [indexOfLastItem, itemsPerPage]
  );

  useEffect( () => {
    if (items && items.length > 0) {
      setCurrentItems(items.slice(indexOfFirstItem, indexOfLastItem));
    } else {
      setCurrentItems([]);
    }
  }, [indexOfFirstItem, indexOfLastItem, items] );

  return {
    currentItems,
    currentPage,
    totalPages,
    itemsPerPage,
    setItemsPerPage: setItemParam,
    itemPos: {
      length: totalItemsLength,
      first: indexOfFirstItem + 1,
      last:
        totalItemsLength > itemsPerPage
          ? indexOfFirstItem + itemsPerPage
          : totalItemsLength,
    },
    setCurrentPage: setPageParam,
    nextPage: () => setPageParam(Math.min(currentPage + 1, totalPages)),
    prevPage: () => setPageParam(Math.max(currentPage - 1, 1)),
  };
}

interface PaginationProps {
  currentPage: number;
  totalPages: number;
  setCurrentPage: (page: number) => void;
  setItemsPerPage: (page: number) => void;
  itemsPerPage: number;
  nextPage: () => void;
  prevPage: () => void;
  itemPos: {
    first: number;
    last: number;
    length: number;
  };
}

export function Pagination({
  currentPage,
  totalPages,
  setCurrentPage,
  nextPage,
  prevPage,
  itemPos,
  setItemsPerPage,
  itemsPerPage,
}: PaginationProps) {
  const pages = useMemo(() => {
    let pageNumbers: Array<string | number> = [];

    if (totalPages < 10) {
      pageNumbers = Array.from({ length: totalPages }, (_, i) => i + 1);
    } else {
      if (currentPage < 6) {
        pageNumbers = Array.from<unknown, string | number>(
          { length: 7 },
          (_, i) => i + 1
        ).concat(['...'], [totalPages]);
      } else if (currentPage >= 6 && currentPage <= totalPages - 5) {
        pageNumbers = [1, '...'].concat(
          Array.from({ length: 5 }, (_, i) => currentPage - 2 + i),
          ['...'],
          [totalPages]
        );
      } else {
        pageNumbers = [1, '...'].concat(
          Array.from({ length: 6 }, (_, i) => totalPages - 6 + i),
          totalPages
        );
      }
    }

    return pageNumbers;
  }, [currentPage, totalPages]);

  function handleChange(e: React.ChangeEvent<HTMLSelectElement>) {
    setCurrentPage(+e.target.value);
    // navigate(`?page=${e.target.value}`);
  }

  function handleClick(page: number) {
    setCurrentPage(page);
  }

  const arrowButtonStyle =
    'flex w-14 items-center justify-center border border-gray-300 bg-white disabled:bg-zinc-300 px-2 py-2 font-medium text-gray-500 hover:bg-gray-50';

  function handlePerPageChange(e: React.ChangeEvent<HTMLSelectElement>) {
    setItemsPerPage(+e.target.value);
  }

  return (
    <div className="flex flex-col justify-center items-center gap-2 mt-5">
      <div className="flex items-center gap-16 text-sm">
        <div>
          Showing <span className="font-bold">{itemPos.first}</span> to{' '}
          <span className="font-bold">{itemPos.last}</span> of{' '}
          <span className="font-bold">{itemPos.length}</span> items
        </div>
        <div>
          Go to page:
          <select
            onChange={handleChange}
            value={currentPage}
            disabled={totalPages === 1}
            className="ml-3 rounded-md border-gray-300 text-xs shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50">
            {Array.from({ length: totalPages }, (_, i) => i + 1).map(
              (page, index) => (
                <option key={`goto-${page}-${index}`} value={page}>
                  {page}
                </option>
              )
            )}
          </select>
        </div>
        <div>
          Items per page:
          <select
            value={itemsPerPage}
            onChange={handlePerPageChange}
            className="ml-3 rounded-md border-gray-300 text-xs shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50 disabled:cursor-not-allowed disabled:bg-gray-50">
            <option value={10}>10</option>
            <option value={20}>20</option>
            <option value={50}>50</option>
            <option value={100}>100</option>
          </select>
        </div>
      </div>
      <nav aria-label="Pagination">
        <ul className="inline-flex items-center -space-x-px rounded-md text-sm shadow-sm">
          <li>
            <button
              onClick={prevPage}
              disabled={currentPage === 1}
              className={arrowButtonStyle + ' rounded-l-md'}>
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="w-5 h-5" />
            </button>
          </li>
          {pages.map((page, index) => (
            <li key={index}>
              {typeof page === 'number' ? (
                <button
                  onClick={() => {
                    handleClick(page);
                  }}
                  disabled={currentPage === page}
                  className={`flex w-14 justify-center items-center border border-gray-300 px-4 py-2 font-medium disabled:border-x-zinc-400 ${
                    currentPage === page
                      ? 'bg-zinc-200 text-gray-700'
                      : 'bg-white text-gray-500 hover:bg-gray-50'
                  }`}
                  aria-current={currentPage === page ? 'page' : undefined}>
                  {page}
                </button>
              ) : (
                <span className="flex justify-center w-14 items-center border border-gray-300 px-4 py-2 font-medium bg-white text-gray-500">
                  ...
                </span>
              )}
            </li>
          ))}
          <li>
            <button
              onClick={nextPage}
              disabled={currentPage === totalPages}
              className={
                arrowButtonStyle + ' rounded-r-md disabled:border-l-zinc-400'
              }>
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="w-5 h-5" />
            </button>
          </li>
        </ul>
      </nav>
    </div>
  );
}
