import type {
  AttributeKeys,
  CategoryPermission,
  RolePermissionsEnum,
} from '@lib/types';
import { isValueInEnum } from '@lib/utils';

export enum AttributeCategory {
  ADMIN = 'admin',
  SUPERADMIN = 'superadmin',
  CHECKLISTS = 'checklists',
  MEMBERS = 'members',
  METRICS = 'metrics',
  MOVES = 'moves',
  ORGS = 'orgs',
  ORGROLES = 'orgroles',
  ROLES = 'roles',
  TASKS = 'tasks',
  TEMPLATES = 'templates',
  UNITS = 'units',
  BASES = 'bases',
}

export enum Operation {
  READ = 'READ',
  WRITE = 'WRITE',
  DELETE = 'DELETE',
}

export function createPermission(
  category: AttributeCategory,
  ...attributes: Operation[]
): CategoryPermission {
  return {
    category,
    attributes: attributes.length === 1 ? attributes[0] : attributes,
  };
}

export function generateAttributes<T extends object>(categoryEnum: T): Record<keyof AttributeKeys, CategoryPermission> {
  const categories = Object.keys(categoryEnum);
  let categoryPermissions: Record<string, CategoryPermission> = {};
  for (const category of categories) {
    categoryPermissions[`${ category }_READ`] = createPermission(
      category.toLowerCase() as AttributeCategory,
      Operation.READ
    );
    categoryPermissions[`${ category }_READWRITE`] = createPermission(
      category.toLowerCase() as AttributeCategory,
      Operation.READ,
      Operation.WRITE
    );
    categoryPermissions[`${ category }_READWRITEDELETE`] = createPermission(
      category.toLowerCase() as AttributeCategory,
      Operation.READ,
      Operation.WRITE,
      Operation.DELETE
    );
  }

  return categoryPermissions;
}

export const Attribute: AttributeKeys = {...generateAttributes(AttributeCategory)} as const;

const cssPermissions = [
  Attribute.TASKS_READWRITEDELETE,
  Attribute.CHECKLISTS_READWRITEDELETE,
  Attribute.METRICS_READ,
  Attribute.TEMPLATES_READWRITEDELETE,
  Attribute.ORGS_READWRITEDELETE,
  Attribute.MOVES_READWRITEDELETE,
  Attribute.MEMBERS_READWRITEDELETE,
  Attribute.ORGROLES_READWRITE,
];

const adminPermissions = [...cssPermissions, Attribute.ROLES_READWRITEDELETE, Attribute.ADMIN_READWRITEDELETE];

const superAdminPermissions = [...adminPermissions, Attribute.SUPERADMIN_READWRITEDELETE];

export const RolePermissions: RolePermissionsEnum = {
  APPROVER: [Attribute.TASKS_READWRITEDELETE, Attribute.METRICS_READ],
  COMMANDER: [Attribute.MEMBERS_READ, Attribute.METRICS_READ],
  CSS: cssPermissions,
  ADMIN: adminPermissions,
  SUPERADMIN: superAdminPermissions,
} as const;

/**
 * Compares two maps, checking if the userMap contains all the required values specified in the reqdMap for each category.
 *
 * @param userMap - The user map to be compared.
 * @param reqdMap - The map containing required values for each category.
 * @returns {boolean} Returns true if userMap contains all the required values for each category, otherwise false.
 */
export function compareMaps(
  userMap: Map<string, Operation[] | Operation>,
  reqdMap: Map<string, Operation[] | Operation>
): boolean {
  for (const [category, requiredValues] of reqdMap) {
    if (!userMap.has(category)) {
      return false;
    }

    const userValues = userMap.get(category);
    if (Array.isArray(requiredValues)) {
      for (const requiredValue of requiredValues) {
        if (!userValues?.includes(requiredValue)) {
          return false;
        }
      }
    } else {
      if(!userValues?.includes(requiredValues))
        return false;
    }
  }

  return true;
}

/**
 * Verifies the attributes in the provided userMap to ensure they are valid role attributes.
 * Throws a TypeError if any attribute is not READ, WRITE, or DELETE.
 *
 * @param {Map<AttributeCategory, Operation[]>} userMap - The map containing role categories and their associated attributes.
 * @throws {TypeError} Throws a TypeError if any attribute is not READ, WRITE, or DELETE.
 */
export function verifyAttributes(userMap: Map<string, Operation[] | Operation>) {
  const isOperation = isValueInEnum(Operation);
  for (const [key, attributes] of userMap) {
    if (key && attributes) {
      if (Array.isArray(attributes)) {
        for (const attribute of attributes) {
          if (!attribute || !isOperation(attribute)) {
            // Check if the user category has the requested attribute
            throw new TypeError(
              `Role attribute ${attribute} should be READ, WRITE, or DELETE`
            );
          }
        }
      }
      else {
        if (!attributes || !isOperation(attributes)) {
          // Check if the user category has the requested attribute
          throw new TypeError(
            `Role attribute ${attributes} should be READ, WRITE, or DELETE`
          );
        }
      }
    } else {
      throw new TypeError(
        `Role attribute ${attributes} should be READ, WRITE, or DELETE`
      );
    }
  }
}

/**
 * Verifies that the provided operations are valid Operation types.
 * Throws a TypeError if any operation is invalid.
 *
 * @param operations - The operation(s) to verify.
 * @throws {TypeError} Throws a TypeError if any operation is not a valid Operation type.
 */
export function verifyOperations(operations: Operation | Operation[]) {
  const isOperation = isValueInEnum(Operation);
  if (Array.isArray(operations)) {
    for (const op of operations) {
      if (!op || !isOperation(op)) throw new TypeError(`${op} is not an Operation type.`);
    }
  } else if (!operations || !isOperation(operations)) throw new TypeError(`${operations} is not an Operation type.`);
}

/**
 * Converts an object or array of objects representing category permissions into a Map.
 *
 * @param permObject - The object or array of objects representing category permissions.
 * @returns The map containing category permissions.
 * @throws {TypeError} Throws a TypeError if any category or operation is invalid.
 */
export function objectToMap(permObject: CategoryPermission | CategoryPermission[]) {
  const isRoleCategory = isValueInEnum(AttributeCategory);

  const requiredPermissions = new Map<AttributeCategory, Operation[]>();

  function setMap(
    category: AttributeCategory,
    operations: Operation | Operation[]
  ) {
    if (!category || !isRoleCategory(category)) throw new TypeError(`${category} is not a valid category.`);

    verifyOperations(operations);

    if (Array.isArray(operations))
      requiredPermissions.set(category, operations);
    else requiredPermissions.set(category, [operations]);
  }

  if (Array.isArray(permObject)) {
    for (const requiredPerm of permObject) {
      setMap(requiredPerm.category, requiredPerm.attributes);
    }
  } else {
    setMap(permObject.category, permObject.attributes);
  }

  return requiredPermissions;
}
