/* eslint-disable import/prefer-default-export */
import { Models } from '@lib/models.server';
import type { MoveType, PrismaClient, Template } from '@prisma/client';
import type { TemplateDto } from './dtos';
import { Repository, type IRepository } from './Repository.server';
import type { ChecklistType } from '@lib/constants';

interface ITemplateRepository
  extends IRepository<Template, TemplateDto, TemplateDto> {
  getAllByOrgId(organizationId: string): Promise<any[]>;

  getWithOwningOrg(id: string): Promise<any>;

  getWithTemplateItems(id: string): Promise<any>;

  getInheritedItems(firstTemplateId: string): Promise<{
    templateItems: FlatArray<any[], 1>[];
    type: any;
    templateId: any;
  }>;
}

export class TemplateRepository implements ITemplateRepository {
  public prisma;

  constructor(prismaClient: PrismaClient) {
    this.prisma = prismaClient;
  }

  public getAll(): Promise<Template[]> {
    throw new Error('Method not implemented.');
  }

  public get(id: string): Promise<Template> {
    throw new Error('Method not implemented.');
  }

  public async getAllByOrgId(organizationId: string) {
    // return this org and lower
    const firstOrg = await this.prisma.organization.findFirstOrThrow({
      where: { uuid: organizationId },
      include: {
        templates: true,
        subOrgs: true,
      },
    });

    const templates = [...firstOrg.templates];

    for (const subOrg of firstOrg.subOrgs) {
      const result = await this.getAllByOrgId(subOrg.uuid);
      templates.push(...result);
    }

    return templates;
  }

  public async getWithOwningOrg(id: string) {
    return this.prisma.template.findFirstOrThrow({
      where: { id },
      include: {
        owningOrganization: true,
      },
    });
  }

  public async getWithTemplateItems(id: string) {
    return this.prisma.template.findFirstOrThrow({
      where: { id },
      select: {
        id: true,
        items: {
          include: {
            reference: true,
          },
        },
        type: true,
        organizationId: true,
        name: true,
        description: true,
      },
    });
  }

  public async create({
    owningOrganizationId,
    type,
    name,
    description,
  }: TemplateDto): Promise<string> {


    const newTemplate = await this.prisma.template.create({
      data: {
        name,
        description,
        type,
        owningOrganization: { connect: { uuid: owningOrganizationId } },
      },
      select: {
        id: true,
      },
    });

    await this.updateFamilyTreeTemplates(owningOrganizationId, type, newTemplate.id);
    
    return newTemplate.id;
  }

  public async updateFamilyTreeTemplates(
    uuid: string,
    type: ChecklistType,
    newTemplateId: string
  )

  {
    const [ancestors, descendants] = await Models.org.findFamilyTree(uuid, false, type);

    const ancestorTemplateIds = [];
    const descendantTemplateIds = [];

    for (const ancestor of ancestors) {
      if (ancestor.templates?.length) {
        const ancestorTemplate = ancestor.templates[0];
        if (!ancestorTemplate.childTemplates.includes(newTemplateId) && ancestorTemplate.id !== newTemplateId)
          ancestorTemplateIds.push(ancestorTemplate.id);
      }
    }
    for (const descendant of descendants) {
      if (descendant.templates?.length) {
        const descendantTemplate = descendant.templates[0];
        if (!descendantTemplate.inheritedFrom.includes(newTemplateId) && descendantTemplate.id !== newTemplateId)
          descendantTemplateIds.push(descendantTemplate.id);
      }
    }
    const data: { inheritedFrom?: string[]; childTemplates?: string[]} = {};
    if (ancestorTemplateIds.length > 0)
      data.inheritedFrom = ancestorTemplateIds;
    if (descendantTemplateIds.length > 0)
      data.childTemplates = descendantTemplateIds;

    await this.prisma.template.update({
      where: { id: newTemplateId },
      data,
    });
    
    if (ancestorTemplateIds.length > 0)
      await this.prisma.$transaction(ancestorTemplateIds.map((ancestorTemplateId) =>
        this.prisma.template.update({
          where: { id: ancestorTemplateId },
          data: {
            childTemplates: {
              push: newTemplateId,
            },
          },
        })
      ));

    if (descendantTemplateIds.length > 0)
      await this.prisma.$transaction(descendantTemplateIds.map((descendantTemplateId) =>
        this.prisma.template.update({
          where: { id: descendantTemplateId },
          data: {
            inheritedFrom: {
              push: newTemplateId,
            },
          },
        })
      ));
  }

  public async update(
    id: string,
    { owningOrganizationId, type, description, name }: TemplateDto
  ): Promise<void> {
    await this.prisma.template.update({
      where: { id },
      data: {
        name,
        description,
        type,
        // organizationId: owningOrganizationId,
      },
    });
  }

  public async delete(id: string) {
    const template = await this.prisma.template.findUniqueOrThrow({
      where: { id },
      include: { items: true },
    });
    if (template && template.items.length === 0)
      return this.prisma.template.update({
        where: { id },
        data: { isDeleted: true },
        select: { id: true },
      });
    else return -1;
  }

  public async getInheritedItems(firstTemplateId: string) {
    // get owning org from first template
    const firstTemplate = await this.prisma.template.findFirstOrThrow({
      where: { id: firstTemplateId },
      include: {
        owningOrganization: true,
      },
    });

    let orgId: string | null = firstTemplate.owningOrganization.uuid;
    const templateItems = [];
    // loop up the org tree grabbing each template that matches the type
    while (orgId && orgId !== 'DAF') {
      const parentOrg = await this.prisma.organization.findFirstOrThrow({
        where: { uuid: orgId },
        include: {
          templates: {
            where: {
              type: firstTemplate.type,
              isDeleted: false,
            },
            include: {
              items: {
                include: {
                  reference: true,
                },
                where: {
                  isDeleted: false,
                },
              },
            },
          },
        },
      });

      for (const temp of parentOrg.templates) {
        templateItems.push(temp.items);
      }

      if (parentOrg.templates.some((t: { type: string }) => t.type === 'SQ'))
        break;

      orgId = parentOrg.parentOrgId;
    }

    return {
      type: firstTemplate.type,
      templateId: firstTemplate.id,
      templateItems: templateItems.flat(),
    };
    // add the items that match the user profile each time
  }

  async findByOrgAndType( orgId: string, moveType: MoveType ) {
    return this.prisma.template.findFirstOrThrow( {
      where: { owningOrganization: { uuid: orgId }, type: moveType },
    } );
  }
}

