import { Breadcrumbs } from '@components/OrgBreadcrumbs';
import ErrorReport from '@components/ui/ErrorReport';
import { Input, InputRow, TextInput, TextLabel } from '@components/ui/Input';
import { Modal } from '@components/ui/modals';
import { useManageLayoutData } from '@lib/hooks/useMatchesData';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs, LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useFetcher, useLoaderData } from '@remix-run/react';
import { requireUser, requireUserAsObject } from '@services/user.server';
import React, { useState } from 'react';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import { getAllOrgs, getOrg, moveOrgs, updateOrg } from './.server/org.queries';
import { Models } from '@lib/models.server';
import useIsAdmin from '@lib/hooks/useIsAdmin';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  const { roles: userRoles } = await requireUserAsObject(request, { permissions: Attribute.ORGS_READWRITE });
  const { orgId } = params;
  if (!orgId) throw json({ status: 'error', error: 'orgId is required.' });
  const org = await getOrg(orgId);
  const parent = await getAllOrgs();
  const ancestors = await Models.org.findAncestors(orgId);
  const ancestorsIds = ancestors.map(org => org.uuid);
  
  return json({ org, parent, ancestors: ancestorsIds, userRoles});
};

// TODO: Add try catch
export async function action({ request }: ActionFunctionArgs) {
  const schema = z.object({
    orgName: z.string().min(3).max(80),
    newParent: z.string().optional(),
    moveOrg: z.preprocess((value) => value === 'on', z.boolean()),
    isWingLevel: z.preprocess((value) => value === 'on', z.boolean()),
    orgId: z.string().min(3),
    abbreviation: z.string(),
    currentParent: z.string().optional(),
  });
  try {
    const { id: userId } = await requireUserAsObject(request, {
      permissions: Attribute.ORGS_READWRITE,
    });

    const { orgId, orgName, abbreviation, newParent, moveOrg, isWingLevel, currentParent } = schema.parse(
      Object.fromEntries((await request.formData()).entries())
    );
    
    if(currentParent && newParent === currentParent){
      return jsonWithError(
        { status: 'error', error: 'Validation Error.' },
        `Requested to move organization, but didn't change location.`
      );
    }

    if (moveOrg) {
      if (!newParent) return jsonWithError({ status: 'error', error: 'Parent org cannot be blank.'},
        'Parent org must be selected.'
      );

      if (newParent === 'DAF') return jsonWithError(
        { status: 'error', error: 'Parent Org cannot be Department of the Airforce.' },
        'Parent Org cannot be Department of the Airforce.'
      );

      await moveOrgs(orgId, newParent);
    }
    
    await updateOrg(orgId, {
      userId,
      orgName,
      abbreviation,
      isWingLevel,
    });

    return redirectWithSuccess('/org', 'Organization Saved!');
  } catch (e) {
    console.error(e);

    if (e instanceof Error) {
      if (e instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (e instanceof ZodError) {
        return jsonWithError(
          { status: 'error', message: 'validation error', errors: e.errors },
          'There was an error saving the org.'
        );
      }
      return jsonWithError(
        { status: 'error', message: e.message },
        'There was an error saving the org.'
      );
    }
  }
}

const isNameValid = (val?: string | number | readonly string[] | undefined): boolean => {
  if (typeof val === 'string') {
    return val.trim().length > 2;
  }
  return false;
};

const EditOrgDefaultPage = () => {
  const { org, ancestors, userRoles} = useLoaderData<typeof loader>();
  const [moveOrgCheck, setMoveOrgCheck] = useState(false);
  const [ isWingLevel, setIsWingLevel ] = useState(org.isWingLevel);
  const { parentNode } = useManageLayoutData();
  const [newParent, setNewParent] = useState('');
  const [ orgNameState, setOrgNameState] = useState(org.name); 
  const fetcher = useFetcher();

  const {isAdmin} = useIsAdmin(userRoles);

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setOrgNameState(e.target.value);
  };
  
  return (
    <Modal>
      <fetcher.Form onSubmit={(e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        for (let [key, val ] of data.entries()) {
          data.set(key, val.toString().trim());
        }
        console.log(data);
        console.log(e.currentTarget);
        fetcher.submit(data, { method: 'POST'});
      }}>
        <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
          <Modal.Title>Edit Organization:</Modal.Title>
          {(!org.isBase && parentNode.uuid !== org.uuid )&& (
            <>
              <Input.Checkbox
                label="Relocate Org?"
                name="moveOrg"
                checked={moveOrgCheck}
                onChange={() => setMoveOrgCheck(!moveOrgCheck)}></Input.Checkbox>
              {moveOrgCheck && (
                <>
                  <InputRow className="pb-0">
                    <Breadcrumbs tree={parentNode} setNewParent={setNewParent} ancestors={ancestors} orgId={org.uuid}/>
                    <input type="hidden" value={newParent} name="newParent" />
                  </InputRow>
                  <InputRow>
                    <input
                      type="hidden"
                      value={org.parentOrgId ?? ''}
                      name="currentParent"/>
                  </InputRow>
                </>
              )}
              {isAdmin && <Input.Checkbox label="Is Wing-level Org?" name="isWingLevel" checked={isWingLevel} onChange={() => setIsWingLevel(!isWingLevel)}></Input.Checkbox>}
            </>
          )}
          <InputRow>
            <input type="hidden" name="orgId" value={org.uuid}></input>
            <Input.TextWithValidation id="orgName" name="orgName" label="New Name:" validationFn={isNameValid} value={orgNameState} onChange={handleNameChange} validationMsg="Name requires at least 3 characters"/>
          </InputRow>
          <InputRow>
            <TextLabel htmlFor="abbreviation">Abbreviation:</TextLabel>
            <TextInput name="abbreviation" defaultValue={org.abbreviation} />
          </InputRow>
        </div>
        <Modal.Buttons link="/org" value="edit">
          Save
        </Modal.Buttons>
      </fetcher.Form>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default EditOrgDefaultPage;
