import ErrorReport from '@components/ui/ErrorReport';
import {
  Input,
  Form,
  Label,
} from '@components/ui';
import { Modal } from '@components/ui/modals';
import { RTEProvider } from '@components/ui/RichText/Editor';
import { Attribute } from '@lib/utils';
import type { ActionFunctionArgs } from '@remix-run/node';
import { useLoaderData, useParams } from '@remix-run/react';
import { requireUser } from '@services/user.server';
import { AuthorizationError } from 'remix-auth';
import { jsonWithError, redirectWithSuccess } from 'remix-toast';
import z, { ZodError } from 'zod';
import type { createTemplateItemLoader } from './.server/template.queries';
import { createTemplateItem } from './.server/template.queries';

export { createTemplateItemLoader as loader } from './.server/template.queries';

export const action = async ({ request, params }: ActionFunctionArgs) => {
  if (!params.templateId) throw new Error('Template ID is required.');

  const itemCreateSchema = z.object({
    templateId: z.literal(params.templateId),
    name: z.string().min(1),
    description: z.string().optional(),
    requiredRole: z.string().min(1),
    forHasDependents: z.union([
      z.literal('on').transform(() => true),
      z.literal(undefined).transform(() => false),
    ]),
    forDormResidents: z.union([
      z.literal('on').transform(() => true),
      z.literal(undefined).transform(() => false),
    ]),
    forHasEFMP: z.union([
      z.literal('on').transform(() => true),
      z.literal(undefined).transform(() => false),
    ]),
  });

  try {
    await requireUser(request, {
      options: {
        redirectOnFailure: false,
      },
      permissions: Attribute.TEMPLATES_READWRITE,
    });

    const {
      templateId,
      name,
      description,
      requiredRole: requiredRoleId,
      forHasDependents,
      forHasEFMP,
      forDormResidents,
    } = itemCreateSchema.parse(
      Object.fromEntries((await request.formData()).entries())
    );

    await createTemplateItem({
      name,
      description,
      requiredRoleId,
      templateId,
      forDormResidents,
      forHasEFMP,
      forHasDependents,
    });
  } catch (err) {
    if (err instanceof Error) {
      if (err instanceof AuthorizationError) {
        return jsonWithError(
          { status: 'error', error: 'Authorization Error.' },
          'User is not authorized.'
        );
      }
      if (err instanceof ZodError) {
        err.issues.forEach((issue) =>
          console.error(
            `There was an error validating on item creation!\n${issue.path} - ${issue.message}`
          )
        );
        return jsonWithError(
          {
            status: 'error',
            error: 'There was an error adding the item.',
            items: err.issues,
          },
          'There was an error adding the item.'
        );
      }
      return jsonWithError(
        { status: 'error', error: err.message },
        'There was an error adding the item.'
      );
    }
  }

  return redirectWithSuccess(`/template/${params.templateId}`, 'Item Added!');
};

const CreateItem = () => {
  const { templateId } = useParams();
  const { roles } = useLoaderData<typeof createTemplateItemLoader>();

  return (
    <Modal>
      <RTEProvider>
        <Form method="post">
          <Input.Hidden name="templateId" value={templateId}/>
          <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4 mb-5">
            <Modal.Title>Create New Item</Modal.Title>
            <Form.Row>
              <Label htmlFor="name">Name</Label>
              <Input.Text name="name" />
            </Form.Row>
            <Form.Row>
              <Label htmlFor="description">Description</Label>
              <Input.RichText name="description" limit={2500} />
            </Form.Row>
            <Form.Row>
              <Label>Role Required</Label>
              <Input.Select options={roles} valueKey="uuid" name="requiredRole"></Input.Select>
            </Form.Row>
            <Form.Row>
              <Label.Heading>Questions</Label.Heading>
              <Input.CheckboxGroup>
                <Input.Checkbox
                  label="For Members that are Dorm Residents"
                  name="forDormResidents"/>
                <Input.Checkbox
                  label="For Members with Dependents"
                  name="forHasDependents"/>
                <Input.Checkbox
                  label="For Members with Dependents that qualify for EFMP"
                  name="forHasEFMP"/>
              </Input.CheckboxGroup>
            </Form.Row>
          </div>
          <Modal.Buttons link={`/template/${templateId}`} value="create">
          Create Item
          </Modal.Buttons>
        </Form>
      </RTEProvider>
    </Modal>
  );
};
export const ErrorBoundary = ErrorReport;
export default CreateItem;