const userSelect = { id: true, name: true, rank: true };

async function filterAlreadyAssigned(
  currentUsers: OrgRoleAssignedUsers[],
  allUsers: OrgRoleAssignedUsers[],
) {
  // Create a Set of 'id' values in array2
  const idsInArray2 = new Set(currentUsers.map((item) => item.id));

  return allUsers.filter((item) => !idsInArray2.has(item.id));
}

export async function findUsersToAssign(
  org: Organization,
  orgRolePromise: ReturnType<typeof getOrgRoleWithUsers>,//{ level: RoleLevel; abbreviation: string },
  // currentUsers: OrgRoleAssignedUsers[],
  excludeUserId?: string,
) {
  /**
   * IF role === 'ADMIN' (Wing Admin) use the first wing level org in the tree
   * IF role === 'SUPERADMIN' grab every user ever
   * IF role is a base agency grab users for the org role's owning org base
   * Otherwise get the users from the org role's owning org
   */
  let users: Array<OrgRoleAssignedUsers> = [];

  const { users: currentUsers, role } = await orgRolePromise

  if (role.abbreviation === 'SUPERADMIN') {
    console.log('Finding every user')
    users = await Models.userProfile.findMany(userSelect);
  } else if (role.abbreviation === 'ADMIN') {
    console.log('Finding wing users')
    // get users from the first wing level org
    users = await Models.org.findWingUsers(org.uuid, userSelect)
  } else if (role.level === 'BASE') {
    console.log('Finding base users')
    // get users from given orgId base
    users = await Models.org.findBaseUsers(org.baseId, userSelect, org.uuid);
  } else {
    console.log('Finding org users')
    // get users from the given orgId
    // users = await Models.userProfile.getAllByOrg(org.uuid, { excludeUserId })
    users = await Models.userProfile.findMany(userSelect, {
      AND: {
        currentOrg: { uuid: org.uuid },
        id: { not: excludeUserId },
        isDeleted: false,
      },
    });
  }

  users = await filterAlreadyAssigned(currentUsers, users);

  // await new Promise(r => setTimeout(r, 2000))

  return users
    .sort(rankComparator);
}

export const rankComparator = (a: { rank: Rank; name: string }, b: { rank: Rank; name: string }): number => {
  const orderDiff = UserRank[a.rank].order - UserRank[b.rank].order;

  // If ranks are different, return rank comparison
  if (orderDiff !== 0) {
    return orderDiff;
  }

  // If ranks are the same, sort by name
  return a.name.localeCompare(b.name);
};

