import ContainerLayout from '@components/ui/Container';
import ErrorReport from '@components/ui/ErrorReport';
import SideBarNav from '@components/SideBar';
import type { SideBarNavLink } from '@lib/types';
import { Attribute } from '@lib/utils';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import {
  getItemCountForAllRoles,
  getItemCountByUserRole,
  hasCSSRole,
  getChecklistItemsByRole,
} from './.server/tasks.queries';
import { requireUserAsObject } from '@services/user.server';

export const loader = async ({ params, request }: LoaderFunctionArgs) => {
  const { id, roles: userOrgRoles, currentOrg } = await requireUserAsObject(
    request,
    { permissions: Attribute.TASKS_READWRITE }
  );

  const { orgRoleId: roleId } = params;
  if (!roleId) throw new Error('roleId is required');

  // const items =

  const orgRole = userOrgRoles.find(orgRole => orgRole.roleId === roleId);

  // if (!orgRole) throw new Error(`could not find orgrole with role id: ${roleId}`);

  // let items;
  //
  // if (hasCSSRole(userOrgRoles)) {
  //   items = getChecklistItemsByRole( roleId, cssOrgId );
  // } else {
  //   if (!orgRole) throw new Error(`could not find orgrole with role id: ${roleId}`);
  //   items = getChecklistItemsByRole( roleId, orgRole?.owningOrgId);
  // }

  let roles;

  if (hasCSSRole(userOrgRoles)) {
    const cssOrgId = userOrgRoles.find( orgRole => orgRole.abbreviation === 'CSS' )?.owningOrgId;
    if ( !cssOrgId ) throw new Error( 'CSS owning org id not found.' );
    roles = await getItemCountForAllRoles( cssOrgId );
  }
  else
  {
    if (!orgRole) throw new Error(`could not find orgrole with role id: ${roleId}`);
    roles = await getItemCountByUserRole( id, orgRole?.owningOrgId );
  }
  return json({
    roleId: params.roleId,
    roles,
  }, {
    headers: {
      'X-Frame-Options': 'SAMEORIGIN',
      'Content-Security-Policy': 'frame-ancestors: none',
    },
  });
};


type RoleLinkProps = {
  info: any & { name: string };
  notCompleteCount: number;
  overdueCount: number;
}

function RoleLink({ info, notCompleteCount, overdueCount }: RoleLinkProps) {

  let text = '';
  if (overdueCount > 0) {
    if (overdueCount > 99) {
      text = '99+';
    } else {
      text = overdueCount.toString();
    }
  } else {
    if (notCompleteCount > 99) {
      text = '99+';
    } else {
      text = notCompleteCount.toString();
    }
  }

  return (
    <div className="flex w-full justify-between">
      <span className="text-ellipsis overflow-hidden whitespace-nowrap max-w-[calc(100%-3rem)]">
        {info.name}
      </span>
      {notCompleteCount > 0 && (
        <span
          className={`${overdueCount > 0 ? 'bg-red-500' : 'bg-primary-500'} px-2.5 rounded-full text-center text-white`}
          title="Unsigned Tasks">
          { text }
        </span>
      )}
    </div>
  );
}

const TasksTabLayout = () => {
  const { roles } = useLoaderData<typeof loader>();

  let links: SideBarNavLink['links'] = [];

  for (const orgRole of roles) {
    links.push({
      text: (
        <RoleLink
          info={orgRole}
          notCompleteCount={orgRole.notCompleteCount ?? 0}
          overdueCount={orgRole.overdueCount}/>
      ),
      href: `/tasks/${orgRole.id}`,
      urlPattern: orgRole.id,
      extraClasses: orgRole.overdueCount && orgRole.overdueCount > 0 ? 'font-bold' : undefined,
    });
  }

  const sideBarLinks = [
    {
      header: 'View Items By Role',
      links: [...links],
    },
  ];

  return (
    <>
      <SideBarNav navLinks={sideBarLinks}></SideBarNav>
      <ContainerLayout>
        <Outlet />
      </ContainerLayout>
    </>
  );
};

export const ErrorBoundary = ErrorReport;
export default TasksTabLayout;
