/**
 * This was planned to be a place for CSS to manage user checklists. But that will all be done from the checklist pages
 */
import ErrorReport from '@components/ui/ErrorReport';
import { TrashIcon } from '@heroicons/react/24/solid';
import { userProfileSelect } from '@lib/constants';
import { Attribute } from '@lib/utils';
import { json, type LoaderFunctionArgs } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import db from '@services/db.server';
import { requireUser } from '@services/user.server';

export const loader = async ({ request, params }: LoaderFunctionArgs) => {
  await requireUser(request, {permissions:[
    Attribute.MEMBERS_READWRITE,
    Attribute.CHECKLISTS_READWRITE,
  ]});
  const manageChecklist = await db.checklist.findFirst({
    where: {
      id: params.checklistId,
      userId: params.userId,
    },
    include: {
      items: {
        include: {
          templateItem: {
            include: {
              requiredRole: {
                include: {
                  role: true,
                },
              },
            },
          },
        },
      },
      user: {
        select: {
          ...userProfileSelect(),
        },
      },
    },
  });

  return json({ manageChecklist });
};

const ManageUserChecklist = () => {
  const { manageChecklist } = useLoaderData<typeof loader>();

  return (
    <>
      <h1>Manage User Checklist</h1>
      <h2>{manageChecklist?.user.name}</h2>
      {manageChecklist && <p>{manageChecklist.name}</p>}
      <div>
        {manageChecklist &&
          manageChecklist.items.map((item) => (
            <p key={item.id}>
              {item.templateItem.name} -{' '}
              {item.isComplete ? 'Complete' : 'Not Complete'} -{' '}
              {item.templateItem.requiredRole.role.name}{' '}
              <TrashIcon className="h-6 w-6 text-red-700"></TrashIcon>
            </p>
          ))}
      </div>
    </>
  );
};
export const ErrorBoundary = ErrorReport;
export default ManageUserChecklist;
