  private async getMultiOrgAverageChecklistTime(
    orgId = this.orgId
  ): Promise<{ days: number; hours: number; minutes: number }> {
    const { totalMillis, taskCount } = await this.getRecursiveOrgsAverage(
      orgId,
      this.getSingleOrgAverageChecklistTime.bind(this)
    );

    if (taskCount === 0) {
      return { days: 0, hours: 0, minutes: 0 };
    }

    const averageMillis = totalMillis / taskCount;

    return this.calculateAverage(averageMillis);
  }
  private async getSingleOrgAverageChecklistTime(
    orgId = this.orgId
  ): Promise<{ totalMillis: number; taskCount: number }> {
    const completedChecklists = await this.prisma.checklist.findMany({
      where: {
        isComplete: true,
        dateCompleted: {
          not: null,
        },
        user: {
          currentOrgId: orgId,
        },
      },
      select: {
        createdAt: true,
        dateCompleted: true,
      },
    });

    if (completedChecklists.length === 0) {
      return { totalMillis: 0, taskCount: 0 };
    }

    const totalCompletionTime = completedChecklists.reduce(
      (total, checklist) => {
        if (checklist.dateCompleted) {
          const completionTime =
            new Date(checklist.dateCompleted).getTime() -
            new Date(checklist.createdAt).getTime();
          return total + completionTime;
        }
        return total;
      },
      0
    );

    return {
      totalMillis: totalCompletionTime,
      taskCount: completedChecklists.length,
    };
  }

  public async getAverageOverdueChecklist(): Promise<{
    days: number;
    hours: number;
    minutes: number;
  }> {
    return this.includeChildren
      ? this.getMultiOrgAverageOverdueChecklistTime(this.orgId)
      : this.getSingleOrgAverageOverdueChecklistTime(this.orgId).then(
        (result) => {
          const averageMillis = result.totalMillis / result.taskCount;

          const averageDays = Math.floor(
            averageMillis / (1000 * 60 * 60 * 24)
          ); // 1 day = 86400000 ms
          const averageHours = Math.floor(
            (averageMillis % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
          ); // 1 hour = 3600000 ms
          const averageMinutes = Math.floor(
            (averageMillis % (1000 * 60 * 60)) / (1000 * 60)
          ); // 1 minute = 60000 ms

          return {
            days: !isNaN(averageDays) ? averageDays : 0,
            hours: !isNaN(averageHours) ? averageHours : 0,
            minutes: !isNaN(averageMinutes) ? averageMinutes : 0,
          };
        }
      );
  }
  private async getMultiOrgAverageOverdueChecklistTime(
    orgId: string
  ): Promise<{ days: number; hours: number; minutes: number }> {
    const { totalMillis, taskCount } = await this.getRecursiveOrgsAverage(
      orgId,
      this.getSingleOrgAverageOverdueChecklistTime.bind(this)
    );

    if (taskCount === 0) {
      return { days: 0, hours: 0, minutes: 0 };
    }

    const averageMillis = totalMillis / taskCount;

    const averageDays = Math.floor(averageMillis / (1000 * 60 * 60 * 24));
    const averageHours = Math.floor(
      (averageMillis % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
    );
    const averageMinutes = Math.floor(
      (averageMillis % (1000 * 60 * 60)) / (1000 * 60)
    );

    return {
      days: !isNaN(averageDays) ? averageDays : 0,
      hours: !isNaN(averageHours) ? averageHours : 0,
      minutes: !isNaN(averageMinutes) ? averageMinutes : 0,
    };
  }
  private async getSingleOrgAverageOverdueChecklistTime(
    orgId: string
  ): Promise<{ totalMillis: number; taskCount: number }> {
    const milisecondConversion = 30 * 24 * 60 * 60 * 1000;

    const overdueChecklists = await this.prisma.checklist.findMany({
      where: {
        isComplete: true,
        dateCompleted: {
          not: null,
        },
        user: {
          currentOrgId: orgId,
        },
      },
      select: {
        createdAt: true,
        dateCompleted: true,
      },
    });

    const overdueDurations = overdueChecklists
      .map((checklist) => {
        if (checklist.dateCompleted) {
          const completionTime =
            new Date(checklist.dateCompleted).getTime() -
            new Date(checklist.createdAt).getTime();
          const overdueTime = completionTime - milisecondConversion;
          return overdueTime > 0 ? overdueTime : null;
        }
        return null;
      })
      .filter((overdueTime) => overdueTime !== null);

    if (overdueDurations.length === 0) {
      return { totalMillis: 0, taskCount: 0 };
    }

    const totalOverdueTime = overdueDurations.reduce(
      (total, overdueTime) => total + (overdueTime || 0),
      0
    );

    return {
      totalMillis: totalOverdueTime,
      taskCount: overdueDurations.length,
    };
  }

  public async getMostCompletedItems(): Promise<any[]> {
    const allTasks = this.includeChildren
      ? await this.getMultiOrgMostCompletedItems(this.orgId)
      : await this.getSingleOrgMostCompletedItems(this.orgId);
    return this.getMostCompletedItemsProcess(allTasks);
  }
  private async getMultiOrgMostCompletedItems(orgId: string): Promise<any[]> {
    const tasks = await this.getSingleOrgMostCompletedItems(orgId);

    const childOrgs = await this.prisma.organization.findMany({
      where: { parentOrgId: orgId },
      select: { uuid: true },
    });

    for (const childOrg of childOrgs) {
      const childTasks = await this.getMultiOrgMostCompletedItems(
        childOrg.uuid
      );
      tasks.push(...childTasks);
    }

    return tasks;
  }
  private async getSingleOrgMostCompletedItems(orgId: string): Promise<any[]> {
    return this.prisma.checklistItem.findMany({
      where: {
        isComplete: true,
        dateCompleted: {
          not: null,
        },
        checklist: {
          user: {
            currentOrgId: orgId,
          },
        },
      },
      select: {
        templateItemId: true,
        createdAt: true,
        dateCompleted: true,
      },
    });
  }
  private async getMostCompletedItemsProcess(allTasks: any[]): Promise<any[]> {
    const taskMap = new Map<
      string,
      { totalCompletionTime: number; count: number }
    >();

    allTasks.forEach((task) => {
      const completionTimeMs =
        new Date(task.dateCompleted!).getTime() -
        new Date(task.createdAt).getTime();

      if (taskMap.has(task.templateItemId)) {
        const existing = taskMap.get(task.templateItemId)!;
        existing.totalCompletionTime += completionTimeMs;
        existing.count += 1;
      } else {
        taskMap.set(task.templateItemId, {
          totalCompletionTime: completionTimeMs,
          count: 1,
        });
      }
    });

    const tasksWithAverageTime = Array.from(taskMap.entries()).map(
      ([templateItemId, { totalCompletionTime, count }]) => {
        const averageCompletionTimeMs = totalCompletionTime / count;

        const days = Math.floor(
          averageCompletionTimeMs / (1000 * 60 * 60 * 24)
        );
        const hours = Math.floor(
          (averageCompletionTimeMs % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        const minutes = Math.floor(
          (averageCompletionTimeMs % (1000 * 60 * 60)) / (1000 * 60)
        );
        const seconds = Math.floor(
          (averageCompletionTimeMs % (1000 * 60)) / 1000
        );
        const milliseconds = Math.floor(averageCompletionTimeMs % 1000);

        return {
          templateItemId,
          averageCompletionTime: {
            days,
            hours,
            minutes,
            seconds,
            milliseconds,
            totalMilliseconds: averageCompletionTimeMs,
          },
        };
      }
    );

    const templateIds = tasksWithAverageTime.map((task) => task.templateItemId);
    const templates = await this.prisma.templateItem.findMany({
      where: {
        uuid: {
          in: templateIds,
        },
      },
      select: {
        uuid: true,
        name: true,
      },
    });

    const templateMap = new Map(
      templates.map((template) => [template.uuid, template.name])
    );

    const sortedTasks = tasksWithAverageTime
      .sort(
        (a, b) =>
          a.averageCompletionTime.totalMilliseconds -
          b.averageCompletionTime.totalMilliseconds
      )
      .slice(0, 6);

    const itemsWithNames = sortedTasks.map((task) => ({
      templateId: task.templateItemId,
      templateName: templateMap.get(task.templateItemId) || 'Unknown',
      averageCompletionTime: task.averageCompletionTime,
    }));

    return itemsWithNames;
  }

